@use "Settings/colors" as c;
@use "Settings/typography" as t;

$color: c.$primary !default;
$color--hover: c.$primary-variant--chambray !default;
$background-color: transparent !default;
$transition_time: .2s !default;
$transition_timing-function: ease-in-out !default;
$text-decoration: none !default;
$letter-spacing: t.$letter-spacing !default;
$weight: 300 !default;
$paragraph__link_color: $color !default;
$paragraph__link_color--hover: $color--hover !default;
$paragraph__link_text-decoration: underline !default;

a {
	color: $color;
	transition: $transition_time $transition_timing-function color;
	text-decoration: $text-decoration;
	letter-spacing: $letter-spacing;
	font-weight: $weight;
	background-color: $background-color;
	padding: 0;
	margin: 0;
	border: 0;
	text-transform: none;

	&:visited {
		color: $color;
	}

	&:hover,
	&:focus,
	&:active {
		color: $color--hover;
	}

	&:focus {
		outline: thin dotted;
	}

	&:hover,
	&:active {
		outline: 0;
	}

	&[href="#"] {
		cursor: pointer;
	}
}

p a {
	color: $paragraph__link_color;
	text-decoration: $paragraph__link_text-decoration;

	&:hover,
	&:focus {
		color: $paragraph__link_color--hover;
	}
}
