@use "Settings/grid"as g;

.cover-hero-pattern {
	padding: 2rem 1.5rem;
	min-height: unset;

	@media (min-width: g.$breakpoint-m) {
		padding: 10rem 3rem;
	}

	@media (min-width: g.$breakpoint-xl) {
		padding: 15rem 3rem;
	}

	&.alignfull {
		width: unset;
	}

	.wp-block-cover__inner-container > *:first-child {
		margin-top: 0;
	}
}
