@use "Settings/colors" as c;
@use "Settings/grid" as g;

$grouped-product-list__details__color: c.$base-400 !default;

.woocommerce-grouped-product-list.group_table {
	width: 100%;
}

/* stylelint-disable */
.woocommerce div.product form.cart.grouped_form {
	margin-bottom: 0 !important;
}

.woocommerce.single-product .woocommerce-grouped-product-list-item.product {
	border: 0;
	border-bottom: 1px solid $grouped-product-list__details__color;
	display: flex;
	flex-direction: column;
	padding-top: 0;
	padding: 10px 0 !important;
	position: relative;

	&:last-child {
		border-bottom: 0;
	}
}

.group-products-section-title {
	border-bottom: 1px solid #000;
	width: 100%;
	margin-bottom: 0;
	padding-bottom: 10px;

	@media (max-width: g.$breakpoint-m) {
		padding: 0 25px 10px;
	}
}

td.woocommerce-grouped-product-list-item__label {
	order: 1;
	padding: 0 !important;

	label a {
		color: #000;
		font-weight: 500;

		&:hover,
		&:focus {
			color: c.$primary-500;
			text-decoration: underline;
		}
	}
}

td.woocommerce-grouped-product-list-item__price {
	order: 2;

	span,
	bdi {
		font-weight: 100 !important;
	}
}

.woocommerce div.product form.cart .group_table {
	td.woocommerce-grouped-product-list-item__label,
	td.woocommerce-grouped-product-list-item__price {
		width: 100%;
		padding: 0 120px 0 0 !important;
	}

	td.woocommerce-grouped-product-list-item__quantity {
		width: 100px;
	}
}
/* stylelint-enable */
.woocommerce .woocommerce-grouped-product-list-item__quantity {
	order: 3;
	position: absolute;
	right: 0;
	top: 10px;

	@media (max-width: g.$breakpoint-s) {
		.quantity-modifier {
			padding: 0;
			width: 30px;
		}
	}

	.quantity-wrapper {
		max-width: 100px;
		width: 100%;
		border: 1px solid $grouped-product-list__details__color;
		height: 40px;
		display: flex;
	}

	a.button.product_type_variable.add_to_cart_button {
		padding: 5px 10px;
		font-size: .82rem;
		line-height: 1.2;
	}

	.quantity {

		@media (max-width: g.$breakpoint-s) {
			border: 0;
		}

		.qty {

			@media (max-width: g.$breakpoint-s) {
				width: 40px;
			}
		}
	}
}

.woocommerce.single-product .product-type-grouped {
	.first-payment-date {
		padding: 0 30px;
		margin-bottom: 0;
	}
}
