/*
 * === General variables for section spacing and the grid ===
 * -- Breakpoint values
 * -- Breakpoints for media queries * -- Grid settings
 */

// Breakpoint values, without 'px' suffix
$grid-breakpoint-xs: 375 !default;
$grid-breakpoint-s: 576 !default;
$grid-breakpoint-m: 768 !default;
$grid-breakpoint-l: 992 !default;
$grid-breakpoint-xl: 1240 !default;

// Breakpoints with 'px' suffix - used for media queries
$breakpoint-xs: $grid-breakpoint-xs * 1px !default;
$breakpoint-s: $grid_breakpoint-s * 1px !default;
$breakpoint-m: $grid_breakpoint-m * 1px !default;
$breakpoint-l: $grid_breakpoint-l * 1px !default;
$breakpoint-xl: $grid_breakpoint-xl * 1px !default;

// Wordpress Breakpoints
$breakpoint-wp-s: 600px !default;
$breakpoint-wp-m: 783px !default;

// Above iPad breakpoint
$breakpoint-ipad-m: 1025px !default;

// Gridsettings
$vertical-gutter: 85px !default;
$gutters: 30px !default;
$gutters-smallscreen-horizontal: 15px !default;
$gutters-smallscreen-vertical: 50px !default;
$gutters-smallscreen-breakpoint: $breakpoint-m !default;
$container_size_narrow: var(--wp--custom--content-size) !default;
$container_size: var(--wp--custom--wide-size) !default; // a single column will have 85px width.

$container_fluid_size: var(--wp--custom--fluid-size) !default; // 100% for REAL fluid layout.

$nr_of_columns: 12 !default;
$map-grid-breakpoints: ("-xxs": 0,
"-xs": $grid-breakpoint-xs,
"-s": $grid-breakpoint-s,
"-m": $grid-breakpoint-m,
"-l": $grid-breakpoint-l,
"-xl": $grid-breakpoint-xl) !default;
