// make wp-block-columns behave like it used to at this breakpoint
// but only if we have an even number of .wp.block-column children
@media (min-width: 600px) and (max-width: 781px) {
	.wp-block-columns:not(.is-not-stacked-on-mobile):has(> .wp-block-column:last-child:nth-of-type(even)) {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}
}
