@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

@use "Elements/body"as b;
@use "Elements/headings"as *;
@use "Elements/links" as l;
@use "Components/headings"as *;

$meta_color: c.$base-900 !default;
$meta_border-color: c.$base-300 !default;
$price_color: c.$base-900 !default;
$gallery_border-color: c.$base-100 !default;
$gallery_border-color--active: c.$primary !default;
$gallery_width: 765px !default;

// if Qala Theme Settings Header Layout Logo Centered has been used, change this height to reflect the new header height - (155px)
$summary_inner_wrapper_sticky_top: 70px !default;
$product_wrap_horizontal_padding_large: g.$gutters !default;
$product_wrap_inner_elements_horizontal_padding_mobile: g.$gutters-smallscreen-horizontal !default;
$product_wrap_inner_elements_horizontal_padding_medium: 0 !default;
$product_title_margin-bottom: .75rem !default;
$product_title_margin-bottom--large: 1.15rem !default;

// show or hide last breadcrumb item, change to inline-block to show
$product_breadcrumb_last_display: none !default;
$product_breadcrumb_color: t.$color__content !default;
$product_breadcrumb_font-weight: t.$base__font-weight !default;
$product_breadcrumb_font-weight--current: b.$strong_font-weight !default;
$product_breadcrumb_link-color: l.$color !default;
$product_breadcrumb_link-color--hover: l.$color--hover !default;

// =====================
// ====== SUMMARY ======
// =====================
$summary_max-width: 450px !default;
$summary_font-family: t.$font__content !default;

// spacing
$summary_padding-top: 25px !default;
$summary_padding-right: 0 !default;
$summary_padding-bottom: 0 !default;
$summary_padding-left: 0 !default;
$summary_padding: $summary_padding-top $summary_padding-right $summary_padding-bottom $summary_padding-left !default;

// spacing - Desktop
$summary_padding-top--medium: g.$gutters !default;
$summary_padding-right--medium: g.$gutters !default;
$summary_padding-bottom--medium: g.$gutters !default;
$summary_padding-left--medium: g.$gutters !default;
$summary_padding--medium: $summary_padding-top--medium $summary_padding-right--medium $summary_padding-bottom--medium $summary_padding-left--medium !default;

// spacing - XL
$summary_padding-top--xl: g.$gutters * 2 !default;
$summary_padding-right--xl: 7px !default;
$summary_padding-bottom--xl: g.$gutters !default;
$summary_padding-left--xl: 83px !default;
$summary_padding--xl: $summary_padding-top--xl $summary_padding-right--xl $summary_padding-bottom--xl $summary_padding-left--xl !default;

.woocommerce.single-product,
.qala-quickview {
	.woocommerce-container {
		width: 100%;
		padding: 0;

		&:before,
		&:after {
			display: table;
			content: " ";
		}
	}

	.product {
		width: 100%;

		@media (min-width: g.$breakpoint-l) {
			padding-top: g.$gutters;
		}
	}

	.woocommerce-breadcrumb {
		@extend .meta;

		display: none;

		@media (min-width: g.$breakpoint-l) {
			display: block;
			text-transform: none;
			letter-spacing: t.$letter-spacing--large;
			padding-right: $product_wrap_horizontal_padding_large;
			padding-left: $product_wrap_horizontal_padding_large;
			margin: 0;
			color: $product_breadcrumb_color;
			font-weight: $product_breadcrumb_font-weight;
		}

		a {

			@media (min-width: g.$breakpoint-l) {
				color: $product_breadcrumb_link-color;

				&:hover,
				&:focus {
					color: $product_breadcrumb_link-color--hover;
				}
			}
		}
	}

	.woocommerce-breadcrumb__part:last-child,
	.woocommerce-breadcrumb__delimiter:nth-last-of-type(2) {
		display: $product_breadcrumb_last_display;
		font-weight: $product_breadcrumb_font-weight--current;
	}

	.product-wrap {
		width: 100%;

		@media (min-width: g.$breakpoint-l) {
			display: grid;
			grid-template-columns: 1fr 1fr;
			align-items: flex-start;
			padding-right: $product_wrap_horizontal_padding_large;
			padding-left: $product_wrap_horizontal_padding_large;
			margin-top: g.$gutters-smallscreen-horizontal;
		}

		@media (min-width: g.$breakpoint-xl) {
			grid-template-columns: $gallery_width 1fr;
		}
	}

	/* stylelint-disable selector-max-id, declaration-no-important  */
	#content div.product div.summary,
	div.product div.summary {
		flex-grow: 5;
		float: none;
		height: 100%;
		width: 100%;
		margin-bottom: 0;
		position: relative;

		@media (min-width: g.$breakpoint-l) {
			grid-row: span 2;
			max-width: 100%;
		}

		@media (min-width: g.$breakpoint-xl) {
			width: auto;
		}

		@media (max-width: g.$breakpoint-xl - 1) {
			position: static !important;
		}
	}

	/* stylelint-enable */
	.summary-inner-wrapper {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		justify-content: space-between;
		background: transparent;
		margin-top: 0;
		width: 100%;
		padding-top: 25px;
		padding-bottom: 0;

		@media (min-width: g.$breakpoint-m) {
			padding: $summary_padding--medium;
		}

		@media (min-width: g.$breakpoint-l) {
			position: sticky;
			padding-right: 0;
			padding-top: 0;
			top: #{"calc(#{$summary_inner_wrapper_sticky_top} + #{$product_title_margin-bottom--large})"}; // scss interpolation to use CSS calc not SASS calc
		}

		@media (min-width: g.$breakpoint-xl) {
			max-width: $summary_max-width;
		}

		> .qala-star-rating {

			@media (max-width: g.$breakpoint-m - 1px) {
				margin-left: $product_wrap_inner_elements_horizontal_padding_mobile;
				margin-right: $product_wrap_inner_elements_horizontal_padding_mobile;
			}
		}

		span.availability_date {
			padding: 0 $product_wrap_inner_elements_horizontal_padding_mobile;
			margin-bottom: 20px;

			@media (min-width: g.$breakpoint-m) {
				padding: unset;
			}
		}
	}

	.product .product_title {
		@extend .h3;

		font-weight: 400;
		margin-top: 0;
		margin-bottom: $product_title_margin-bottom;
		padding-left: $product_wrap_inner_elements_horizontal_padding_mobile;
		padding-right: $product_wrap_inner_elements_horizontal_padding_mobile;
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			line-height: 1.33;
			margin-top: 0;
			margin-bottom: $product_title_margin-bottom--large;
			padding-left: 0;
			padding-right: 0;
			@include tool.font-size(24);
		}
	}

	div.product p.price {
		line-height: 1;
		width: 100%;
		margin-top: 0;
		margin-bottom: 1.25rem;
		padding-left: $product_wrap_inner_elements_horizontal_padding_mobile;
		padding-right: $product_wrap_inner_elements_horizontal_padding_mobile;
		color: $price_color;
		font-family: $summary_font-family;
		@include tool.font-size(24);

		@media (min-width: g.$breakpoint-m) {
			font-weight: 500;
			margin-top: 0;
			margin-bottom: 1.45rem;
			padding-left: 0;
			padding-right: 0;
		}

		.amount {
			color: $price_color;
			font-weight: 500;
			font-family: t.$font__content;
			@include tool.font-size(18);

			@media (min-width: g.$breakpoint-s) {
				@include tool.font-size(16);
			}
		}
	}

	div.product p.price ins {
		text-decoration: none;
		margin-right: .5rem;

		.amount {
			color: c.$primary;
		}
	}

	div.product p.price del {
		opacity: 1;
		font-weight: 300;
		@include tool.font-size(14);

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size(15);
		}

		.amount {
			color: $price_color;
			font-weight: 300;
			@include tool.font-size(14);

			@media (min-width: g.$breakpoint-s) {
				@include tool.font-size(15);
			}
		}
	}

	.woocommerce-product-details__short-description {
		width: 100%;
		padding-left: $product_wrap_inner_elements_horizontal_padding_mobile;
		padding-right: $product_wrap_inner_elements_horizontal_padding_mobile;

		@media (min-width: g.$breakpoint-m) {
			padding-left: 0;
			padding-right: 0;
		}

		p {
			margin-top: 0;
			margin-bottom: 2rem;
			font-weight: 300;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.54;
			letter-spacing: t.$letter-spacing;
			@include tool.font-size(13);

			@media (min-width: g.$breakpoint-m) {
				margin-top: inherit;
				line-height: 1.73;
				letter-spacing: t.$letter-spacing;
			}
		}
	}
}

/**
 * 1. for some reason the loading of this button with position: absolute
 * this is delayed in plugins/qala-wishlist/dist/app.css, which causes a big
 * layout shift on the slider on first load. Adding it here early to fix that */

.product-gallery-wrap {
	position: relative;
	width: 100%;
	margin-bottom: 0;
	border-bottom: 1px solid $gallery_border-color;
	overflow-x: hidden;
	max-width: 100%;

	@media (min-width: g.$breakpoint-m) {
		border: 0;
		padding: 0;
	}

	.qala-wishlist-button {
		z-index: 2;
		position: absolute; /* [1] */
		top: 15px;
		right: 15px;
	}

	.qala-wishlist-button__no-image-slider {
		position: static;
	}
}

.ac-module__wrapper--pdp {
	padding: 2rem 0 1rem;

	@media (min-width: g.$breakpoint-m) {
		padding: 4rem 0 2rem;
		margin-left: calc(50% - 50vw);
		margin-right: calc(50% - 50vw);
	}
}

.container--productlistings {
	overflow: hidden;

	@media (min-width: g.$breakpoint-l) {
		padding-right: $product_wrap_horizontal_padding_large;
		padding-left: $product_wrap_horizontal_padding_large;
	}
}

.single-product .site-main {
	overflow: visible;
}

.product-icons {
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	width: 100%;

	&__column {
		flex: 1;
		text-align: center;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-direction: column;
		width: 100%;

		h6 {
			color: c.$black;
			font-family: t.$font__content;
			margin: tool.calculate-rem(6) 0 tool.calculate-rem(4);
			font-weight: 500;
			@include tool.font-size(12);
		}

		span {
			color: c.$black;
			line-height: 1;
			@include tool.font-size(12);
		}
	}

	&__icon {
		min-height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;

		svg,
		img {
			display: inline-block;
			min-width: 30px;
			width: 100%;
			height: auto;
		}
	}
}

.product-model-info {
	margin-bottom: 1em;

	span {
		@include tool.font-size(14);
	}
}

.woocommerce div.product form.cart .variations th.label {
	line-height: 1.4;

	label {
		font-weight: 600;
		@include tool.font-size(14);
	}
}
