@use "Settings/colors"as c;
@use "Settings/grid"as g;
@use "Settings/typography" as t;
@use "Settings/general" as k;

@use "Tools"as tool;

$border-radius: 2px !default;
$error_background-color: c.$error-lighter !default;
$error_text-color: c.$error-text-color !default;
$error_border-color: c.$error-darker !default;
$notice_color: c.$success-notice-background !default;
$notice_text-color: c.$success-notice-text-color !default;
$notice_border-color: c.$success-notice-border !default;
$info_color: c.$info-notice-background !default;
$info_text-color: c.$info-notice-text-color !default;
$info_border-color: c.$info-notice-border !default;

.woocommerce {
	.woocommerce-error,
	.woocommerce-info,
	.woocommerce-message {
		border-radius: $border-radius;
		margin: 0;
		border: 1px solid;
		border-left-width: 4px;
		font-weight: 300;
		font-style: normal;
		line-height: 1.53;
		letter-spacing: t.$letter-spacing;
		margin-bottom: 1rem;
		margin-top: 1rem;
		@include tool.font-size(13);

		@media (min-width: g.$breakpoint-m) {
			@include tool.font-size(17);
		}
	}

	.woocommerce-notices-wrapper a.wc-forward {
		display: none;
	}

	// woocommerce message when action is done
	.woocommerce-message {
		background-color: $notice_color;
		color: $notice_text-color;
		border-color: $notice_border-color;

		&:before {
			color: $notice_text-color;
		}

		.button {
			background-color: $notice_text-color;
		}
	}

	// woocommerce info such as coupon code in checkout or empty cart visiting the cart-url
	.woocommerce-info {
		background-color: $info_color;
		color: $info_text-color;
		border-color: $info_border-color;
		padding: 1rem 4rem;

		&:before {
			color: $info_text-color;
			content: "";
			background-image: k.$info-icon;
			width: 24px;
			height: 24px;
			background-size: contain;
		}

		.button {
			background-color: $info_text-color;
		}
	}

	.woocommerce-error {
		background-color: $error_background-color;
		color: $error_text-color;
		border-color: $error_border-color;
		padding: 1rem 3rem;

		@media (min-width: g.$breakpoint-m) {
			padding: 1rem 4rem;
		}

		&:before {
			color: $error_text-color;
			padding-right: 20px;
			top: 50%;
			transform: translateY(-50%);
		}

		li:before {
			display: none;
		}

		.button {
			background-color: $error_text-color;
		}
	}
}
