@use "Settings/grid"as g;
@use "Settings/colors"as c;

@use "Tools"as tool;

$color: c.$primary !default;
$color--current: $color !default;
$font-size: 13 !default;
$font-weight: 300 !default;
$font-weight--current: $font-weight !default;
$background-color: c.$base-100 !default;
$padding: 12px 0 !default;
$padding--desktop: 15px 0 !default;

.ac-breadcrumbs-wrapper {
	background-color: $background-color;
	padding: $padding;
	color: $color;
	@include tool.font-size($font-size);

	@media (min-width: g.$breakpoint-s) {
		padding: $padding--desktop;
	}

	.container {
		display: flex;
		justify-content: space-between;
	}

	.ac-breadcrumbs {
		margin: 0;
		display: none;
		font-weight: $font-weight;
		flex: 1;
		@include tool.font-size($font-size);

		@media (min-width: g.$breakpoint-s) {
			display: block;
		}

		a {
			text-decoration: none;
		}

		.breadcrumb_last {
			color: $color--current;
			font-weight: $font-weight--current;
		}
	}

	.next-post-link,
	.previous-post-link {
		display: none;

		@media (min-width: g.$breakpoint-s) {
			display: block;
		}
	}

	.previous-page-link {
		display: block;
		@include tool.font-size(11);

		@media (min-width: g.$breakpoint-s) {
			display: none;
		}
	}

	.next-post-link {
		margin-left: 20px;
		order: 1;
	}
}

.qala-breadcrumbs {
	.crumbs-separator:last-of-type {
		display: none;
	}
}
