@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

$subheading_font-size: 18 !default;
$subheading_font-size--desktop: 20 !default;
$subheading_font-family: t.$font__heading !default;
$subheading_font-weight: 500 !default;
$subheading_font-style: italic !default;
$subheading_font-stretch: normal !default;
$subheading_line-height: 1.44 !default;
$subheading_line-height--desktop: 1.4 !default;
$subheading_letter-spacing: t.$letter-spacing !default;
$section-heading_font-size: 11 !default;
$section-heading_font-size--desktop: 13 !default;
$section-heading_font-family: t.$font__heading !default;
$section-heading_text-transform: uppercase !default;
$section-heading_font-weight: bold !default;
$section-heading_font-style: normal !default;
$section-heading_font-stretch: normal !default;
$section-heading_line-height: 1.46 !default;
$section-heading_letter-spacing: t.$letter-spacing--large !default;
$meta_font-size: 11 !default;
$meta_font-size--desktop: 13 !default;
$meta_font-family: t.$font__heading !default;
$meta_font-weight: normal !default;
$meta_font-style: normal !default;
$meta_font-stretch: normal !default;
$meta_letter-spacing: t.$letter-spacing !default;
$meta_line-height: 1.45 !default;
$meta_text-transform: uppercase !default;
$meta_line-height--desktop: 1.46 !default;

.subheading {
	font-family: $subheading_font-family;
	font-weight: $subheading_font-weight;
	font-style: $subheading_font-style;
	font-stretch: $subheading_font-stretch;
	line-height: $subheading_line-height;
	letter-spacing: $subheading_letter-spacing;
	@include tool.font-size($subheading_font-size);

	@media (min-width: g.$breakpoint-s) {
		line-height: $subheading_line-height--desktop;
		@include tool.font-size($subheading_font-size--desktop);
	}
}

.section-heading {
	font-family: $section-heading_font-family;
	text-transform: $section-heading_text-transform;
	font-weight: $section-heading_font-weight;
	font-style: $section-heading_font-style;
	font-stretch: $section-heading_font-stretch;
	line-height: $section-heading_line-height;
	letter-spacing: $section-heading_letter-spacing;
	@include tool.font-size($section-heading_font-size);

	@media (min-width: g.$breakpoint-s) {
		@include tool.font-size($section-heading_font-size--desktop);
	}
}

.meta {
	font-family: $meta_font-family;
	font-weight: $meta_font-weight;
	font-style: $meta_font-style;
	font-stretch: $meta_font-stretch;
	letter-spacing: $meta_letter-spacing;
	line-height: $meta_line-height;
	text-transform: $meta_text-transform;
	@include tool.font-size($meta_font-size);

	@media (min-width: g.$breakpoint-s) {
		line-height: $meta_line-height--desktop;
		@include tool.font-size($meta_font-size--desktop);
	}
}
