////
/// Styling for the Cookie bar and modal.
/// Component specific animations should instead live in the respective component.
///
/// @group Cookies
/// @link https://sv.wordpress.org/plugins/cookie-law-info/
////

@use "Objects/animations";
@use "Objects/grid";
@use "Elements/headings";
@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/buttons" as buttons;
@use "Tools" as tool;
@use "Settings/z-index" as z;

/// Time for the animation
/// @type seconds
$animation__time: .5s !default;

/// Name of the animation to use.
/// @type String
$animation__name: slide-right !default;

/// Default color for text etc.
/// @type Color
$color: c.$white !default;

/// Active/toggled color state
/// @type Color
$color--active: c.$primary !default;

/// Border color between the accordion items
/// @type Color
$accordion__border-color: c.$base-500 !default;

/// Button border color
/// @type Color
$button__border-color: c.$white !default;

/// Button top,left,right margins
/// @type String
$button__margins: 16px !default;

/// Background color for the cookie bar
/// @type Color
$bar__background-color: c.$base-900 !default;

/// Font size for the cookie bar text
/// @type int
$bar__font-size: 12 !default;

/// Font weight for the cookie bar text
/// @type int
$bar__font-weight: 300 !default;

/// Font size for the accordion sections headings
/// @type int
$sections__font-size: 18 !default;

/// font weight for the accordion sections headings
/// @type Color
$sections__font-weight: 500 !default;

/// Width of the tick icon svg
/// @type String
$tick-icon__width: 20px !default;

/// Height of the tick icon svg
/// @type String
$tick-icon__height: 20px !default;

/// Encoded URL of the tick icon svg, use https://yoksel.github.io/url-encoder/ to easily create an encoded string.
/// @type String
$tick-icon__url: "data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.0347 5.83382C14.833 5.83973 14.6415 5.92429 14.5008 6.06962L8.3198 12.2826L5.50382 9.45207C5.43226 9.37716 5.34656 9.31735 5.25172 9.27615C5.15688 9.23495 5.05482 9.21319 4.95151 9.21213C4.8482 9.21107 4.74572 9.23075 4.65006 9.27C4.55441 9.30926 4.46751 9.3673 4.39446 9.44074C4.3214 9.51417 4.26366 9.60152 4.22461 9.69766C4.18556 9.79381 4.16598 9.89682 4.16703 10.0007C4.16808 10.1045 4.18974 10.2071 4.23072 10.3024C4.27171 10.3978 4.33121 10.4839 4.40574 10.5558L7.77076 13.9383C7.9164 14.0846 8.11388 14.1668 8.3198 14.1668C8.52572 14.1668 8.72321 14.0846 8.86884 13.9383L15.5989 7.17339C15.7109 7.06371 15.7874 6.9226 15.8183 6.76851C15.8492 6.61443 15.8332 6.45455 15.7722 6.30979C15.7112 6.16504 15.6082 6.04216 15.4766 5.95723C15.345 5.87231 15.191 5.82929 15.0347 5.83382Z' fill='%235483bf'/%3E%3C/svg%3E%0A" !default; // stylelint-disable-line max-line-length

/* stylelint-disable selector-max-id, declaration-no-important */
#cookie-law-info-bar {
	background-color: $bar__background-color !important;
	color: $color !important;
	padding-left: 0 !important;
	padding-right: 0 !important;

	.cli-style-v2 {
		line-height: normal;
	}

	.cli-bar-container {
		@extend .container;
	}

	.cli-bar-message {
		width: 100%;
		flex-grow: 1;
		font-weight: $bar__font-weight;
		margin-top: .625em;
		margin-bottom: .625em;
		@include tool.font-size( $bar__font-size );
	}

	.cli-bar-btn_container {
		display: flex;
		flex-direction: column;
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			flex-direction: row;
			width: auto;
		}
	}

	.cli-bar-btn_container a {
		@include tool.btn;

		margin-left: 0;
		margin-right: 0;
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			width: auto;
		}
	}

	.cookie_action_close_header {
		color: $color !important;
		background-color: buttons.$background-color !important;
		line-height: normal;
		order: 1;
		margin-bottom: $button__margins;

		@media (min-width: g.$breakpoint-m) {
			margin: 0 $button__margins !important;
		}
	}

	.cli_settings_button {
		@include tool.btn-type(3);

		margin-left: 0 !important;
		margin-right: 0 !important;
		order: 2;
		color: $color !important;
		border-color: $button__border-color !important;
	}
}

.cli-modal {
	& .cli-tab-footer .wt-cli-privacy-accept-btn {
		@include tool.btn;
	}

	.cli-switch input:checked + .cli-slider {
		background-color: $color--active;
	}

	.cli-tab-section {
		margin: 0;
		border-bottom: 1px solid $accordion__border-color;
	}

	.cli-tab-container .cli-row {
		max-height: 85vh;
	}

	.cli-tab-header {
		background: none;
		border-radius: 0;
		padding-top: 24px;
		padding-bottom: 24px;
	}

	.cli-tab-header a.cli-nav-link {
		font-weight: $sections__font-weight;
		@include tool.font-size( $sections__font-size );
	}
}

.cli-modal.cli-blowup .cli-modal-dialog {
	animation: $animation__name $animation__time forwards;
	margin: 0;
	min-height: 100vh;

	@media (min-width: g.$breakpoint-m) {
		float: right;
	}

	.cli-modal-content {
		min-height: 100vh;
		border-radius: 0;
	}

	.cli-modal-content h4 {
		@extend .h3;
	}

	span.cli-necessary-caption {
		color: $color--active;
		display: flex;
		align-items: center;

		&:before {
			content: "";
			display: block;
			width: $tick-icon__width;
			height: $tick-icon__height;
			background-image: url($tick-icon__url);
		}
	}
}

.cli-modal.cli-blowup.cli-out .cli-modal-dialog {
	animation: $animation__name .2s reverse;
}

.cli-modal-backdrop {
	z-index: z.$modal-overlay;
}

/* stylelint-enable */
