@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

$background-color: c.$white !default;
$header__title_font-size: 26 !default;
$header__title_line-height: 1.28 !default;
$header__title_letter-spacing: t.$letter-spacing--large !default;
$header__title_font-size--desktop: 36 !default;

.woocommerce-order-received {
	.site-content {
		background-color: $background-color;
	}

	h1 {
		line-height: $header__title_line-height;
		letter-spacing: $header__title_letter-spacing;
		@include tool.font-size($header__title_font-size);

		@media (min-width: g.$breakpoint-m) {
			margin: 0;
			@include tool.font-size($header__title_font-size--desktop);
		}
	}

	.entry-header {
		padding-top: 1rem;
		padding-bottom: 1rem;

		@media (min-width: g.$breakpoint-m) {
			padding-top: 4rem;
			padding-bottom: 3rem;
		}

		.entry-title {
			margin: 0;
		}
	}

	.entry-content {
		margin: 0;
	}

	.woocommerce-order > p {
		margin: 0;
		padding-bottom: 1.5rem;

		@media (min-width: g.$breakpoint-m) {
			padding-bottom: 2.8rem;
		}
	}

	.order-note {
		max-width: 460px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		padding-top: .4rem;

		span:first-of-type {
			text-transform: uppercase;
			margin-right: .5rem;
		}

		span {
			text-transform: initial;
			font-family: t.$font__heading;
			font-weight: 700;
			line-height: 1.45;
			letter-spacing: t.$letter-spacing--large;
			color: c.$base-500;
			@include tool.font-size(11);

			@media (min-width: g.$breakpoint-m) {
				line-height: 1.46;
				letter-spacing: t.$letter-spacing--large;
				@include tool.font-size(13);
			}
		}
	}

	.woocommerce-order-details__title,
	.woocommerce-table {
		display: none;
	}

	.woocommerce-customer-details {
		display: flex;

		@media (min-width: g.$breakpoint-m) {
			display: none;
		}
	}
}

/* Order summary at glance, top section */
.woocommerce-order-received ul.woocommerce-thankyou-order-details {
	li {
		border-right: 0;
		font-family: t.$font__heading;
		font-weight: 700;
		line-height: 1.46;
		letter-spacing: t.$letter-spacing--large;
		margin-right: 0;
		padding: 0 0 .7rem;
		display: flex;
		justify-content: space-between;
		@include tool.font-size(13);

		@media (min-width: g.$breakpoint-m) {
			display: block;
			padding: 0;
		}

		strong {
			padding-top: 0;
			font-family: t.$font__heading;
			font-weight: 300;
			line-height: 1.46;
			letter-spacing: t.$letter-spacing;
			color: c.$base-900;
			@include tool.font-size(13);

			@media (min-width: g.$breakpoint-m) {
				padding-top: 1rem;
				font-family: t.$font__content;
				line-height: 1.73;
				letter-spacing: t.$letter-spacing;
				@include tool.font-size(15);
			}
		}

		&:before {
			display: none;
		}
	}
}

.woocommerce-thankyou-order-details {
	display: flex;
	justify-content: space-between;
	margin: 0;
	flex-flow: column wrap;

	@media (min-width: g.$breakpoint-m) {
		flex-flow: row nowrap;
	}
}

/* Removes pseudo-elements that takes up space in the DOM */
.woocommerce ul.order_details.woocommerce-thankyou-order-details:before,
.woocommerce ul.order_details.woocommerce-thankyou-order-details:after {
	display: none;
}

.woocommerce-order-received .woocommerce-order ul.order_details {
	margin: 0;
	padding-bottom: 1rem;

	@media (min-width: g.$breakpoint-m) {
		padding-bottom: 6rem;
	}
}

/* Styling for the custom order-table */
.custom-order-table {
	border-top: solid 2px c.$base-100;
	padding-bottom: 1rem;

	@media (min-width: g.$breakpoint-m) {
		border-top: 0;
		padding-bottom: 6rem;
	}
}

.table-headings {
	display: none;
	justify-content: space-between;
	padding-bottom: 1.5rem;
	border-bottom: solid 2px c.$base-100;

	@media (min-width: g.$breakpoint-m) {
		display: flex;
	}
}

.table-headings__product,
.table-headings__price,
.table-headings__quantity,
.table-headings__total {
	font-family: t.$font__heading;
	font-weight: 700;
	line-height: 1.46;
	letter-spacing: t.$letter-spacing--large;
	text-transform: uppercase;

	@media (min-width: g.$breakpoint-m) {
		@include tool.font-size(13);
	}
}

.product-cell {
	display: flex;
	align-items: center;

	.image {
		border: solid 2px c.$base-100;
		max-width: 5.5rem;
		max-height: 5.5rem;
		display: flex;
		margin-right: 1.5rem;
		width: max-content;

		@media (min-width: g.$breakpoint-m) {
			margin-right: 3.2rem;
		}
	}

	img {
		width: 100%;
		height: auto;
	}

	a {
		display: none;

		@media (min-width: g.$breakpoint-m) {
			display: block;
		}
	}
}

/* Styling for each row in the order-details table */
.table-content__row {
	padding: .6rem 0;
	border-bottom: 0;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;

	@media (min-width: g.$breakpoint-m) {
		border-bottom: solid 2px c.$base-100;
		align-items: center;
		padding: 2rem 0;
	}

	&:first-of-type {
		padding: 2rem 0;
	}
}

div.row_product_name {
	display: block;

	@media (min-width: g.$breakpoint-m) {
		display: none;
	}
}

.table-content__row-rest,
.table-heading-rest {
	display: flex;
	max-width: 340px;
	width: 100%;
	justify-content: space-between;
	flex-direction: column;

	@media (min-width: g.$breakpoint-m) {
		max-width: 340px;
		flex-direction: row;
	}

	@media (min-width: g.$breakpoint-l) {
		max-width: 440px;
	}
}

.row_quantity {
	display: flex;

	&:before {
		content: attr(data-name);
		display: block;
		margin-right: 4px;

		@media (min-width: g.$breakpoint-m) {
			display: none;
		}
	}

	span {
		max-width: 330px;
		width: 100%;
		flex: 1;
		text-align: left;

		@media (min-width: g.$breakpoint-m) {
			text-align: center;
		}
	}
}

div.row_total {
	display: none;

	@media (min-width: g.$breakpoint-m) {
		display: flex;
		justify-content: flex-end;
	}
}

div.row_price {
	justify-content: flex-start;
	font-weight: 700;

	@media (min-width: g.$breakpoint-m) {
		font-weight: 500;
	}
}

.row_total,
.row_price,
.row_quantity,
.row_product_name {
	width: 100%;
	word-break: break-word;
	display: flex;
	align-items: center;
	line-height: 1.46;
	letter-spacing: t.$letter-spacing--large;
	font-weight: 300;
	font-family: t.$font__heading;
	@include tool.font-size(13);

	@media (min-width: g.$breakpoint-m) {
		width: 100px;
		line-height: 1.53;
		letter-spacing: t.$letter-spacing;
		font-weight: 500;
		font-family: t.$font__content;
		@include tool.font-size(17);
	}
}

.row_quantity { // stylelint-disable-line
	justify-content: center;
}

/* Summary table (totals,shipping-cost...) */
.custom-order-summary {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	padding-top: 1.5rem;

	@media (min-width: g.$breakpoint-m) {
		padding-top: 2.8rem;
	}
}

.summary-row {
	max-width: 460px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding-top: .4rem;

	.summary-label {
		text-transform: uppercase;
	}

	span {
		text-transform: initial;
		font-family: t.$font__heading;
		font-weight: 700;
		line-height: 1.45;
		letter-spacing: t.$letter-spacing--large;
		color: c.$base-500;
		@include tool.font-size(11);

		@media (min-width: g.$breakpoint-m) {
			line-height: 1.46;
			letter-spacing: t.$letter-spacing--large;
			@include tool.font-size(13);
		}
	}

	&:last-of-type {
		padding-top: .65rem;

		span {
			color: c.$base-900;
			font-family: t.$font__content;
			font-weight: 700;
			line-height: 1.53;
			letter-spacing: t.$letter-spacing;
			text-transform: initial;
			@include tool.font-size(15);

			@media (min-width: g.$breakpoint-m) {
				line-height: 1.6;
				letter-spacing: t.$letter-spacing;
				@include tool.font-size(17);
			}
		}
	}
}

/* Styling of customer details, address etc. */
.woocommerce-order-received .custom-customer-details {
	padding-bottom: 0;

	@media (min-width: g.$breakpoint-m) {
		padding-bottom: 6rem;
	}
}

/* Styling for the customer details with address */
.woocommerce-order-received .woocommerce-customer-details { // stylelint-disable-line no-duplicate-selectors
	.woocommerce-columns.woocommerce-columns--2 {
		display: flex;
		flex-direction: column;

		@media (min-width: g.$breakpoint-m) {
			flex-direction: row;
		}

		> div:first-of-type {
			margin-right: 0;

			@media (min-width: g.$breakpoint-m) {
				margin-right: 1rem;
			}
		}

		> div:nth-of-type(2) {
			padding-top: 2.3rem;

			@media (min-width: g.$breakpoint-m) {
				padding-top: 0;
			}
		}

		.woocommerce-column__title {
			margin: 0;
			font-family: t.$font__heading;
			font-weight: 700;
			line-height: 1.56;
			letter-spacing: t.$letter-spacing;
			color: c.$base-900;
			text-transform: uppercase;
			padding-bottom: 1.5rem;
			@include tool.font-size(17);

			@media (min-width: g.$breakpoint-m) {
				line-height: 1.46;
				letter-spacing: t.$letter-spacing--large;
				@include tool.font-size(13);
			}
		}

		.woocommerce-column {
			display: flex;
			flex-direction: column;
		}
	}

	address {
		border: 0;
		border-bottom-width: 0;
		border-right-width: 0;
		border-radius: 0;
		height: 100%;
		font-family: t.$font__content;
		line-height: 1.6;
		letter-spacing: t.$letter-spacing;
		color: c.$base-500;
		font-weight: 300;
		padding: 0;
		@include tool.font-size(15);

		@media (min-width: g.$breakpoint-m) {
			border: solid 1px c.$base-300;
			border-bottom-width: 1px;
			border-right-width: 1px;
			padding: .6rem 1.1rem;
		}
	}
}

/* Shows other placement of customer details on desktop */
.woocommerce-order-received .custom-customer-details .woocommerce-customer-details {
	display: none;

	@media (min-width: g.$breakpoint-m) {
		display: block;
	}
}
