@use "Settings/typography" as t;
@use "Settings/grid" as g;

@use "Tools" as tool;

$weight: 700 !default;
$h1_font-size: 38 !default;
$h1_font-size--desktop: 48 !default;
$h1_font-weight: $weight !default;
$h1_color: t.$color__heading !default;
$h1_lh: 1.21 !default;
$h1_lh--desktop: 1.25 !default;
$h1_ls: t.$letter-spacing !default;
$h1_ls--desktop: t.$letter-spacing !default;
$h1_font-style: normal !default;
$h1_font-stretch: normal !default;
$h2_font-size: 26 !default;
$h2_font-size--desktop: 36 !default;
$h2_font-weight: $weight !default;
$h2_color: t.$color__heading !default;
$h2_lh: 1.38 !default;
$h2_lh--desktop: 1.28 !default;
$h2_ls: t.$letter-spacing !default;
$h2_ls--desktop: t.$letter-spacing !default;
$h2_font-style: normal !default;
$h2_font-stretch: normal !default;
$h3_font-size: 18 !default;
$h3_font-size--desktop: 24 !default;
$h3_font-weight: $weight !default;
$h3_color: t.$color__heading !default;
$h3_lh: 1.56 !default;
$h3_lh--desktop: 1.33 !default;
$h3_ls: t.$letter-spacing !default;
$h3_ls--desktop: t.$letter-spacing !default;
$h3_font-style: normal !default;
$h3_font-stretch: normal !default;
$h4_font-size: 16 !default;
$h4_font-size--desktop: $h4_font-size !default;
$h4_font-weight: $weight !default;
$h4_color: t.$color__heading !default;
$h4_lh: initial !default;
$h4_lh--desktop: initial !default;
$h4_ls: initial !default;
$h4_ls--desktop: initial !default;
$h4_font-style: normal !default;
$h4_font-stretch: normal !default;
$h5_font-size: 13 !default;
$h5_font-size--desktop: $h5_font-size !default;
$h5_font-weight: $weight !default;
$h5_color: t.$color__heading !default;
$h5_lh: initial !default;
$h5_lh--desktop: initial !default;
$h5_ls: initial !default;
$h5_ls--desktop: initial !default;
$h5_font-style: normal !default;
$h5_font-stretch: normal !default;
$h6_font-size: 10 !default;
$h6_font-size--desktop: $h6_font-size !default;
$h6_font-weight: $weight !default;
$h6_color: t.$color__heading !default;
$h6_lh: initial !default;
$h6_lh--desktop: initial !default;
$h6_ls: initial !default;
$h6_ls--desktop: initial !default;
$h6_font-style: normal !default;
$h6_font-stretch: normal !default;

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3 {
	font-family: t.$font__heading;
}

h1,
.h1 {
	color: $h1_color;
	line-height: $h1_lh;
	letter-spacing: $h1_ls;
	font-weight: $h1_font-weight;
	font-style: $h1_font-style;
	font-stretch: $h1_font-stretch;

	@include tool.font-size($h1_font-size);

	@media (min-width: g.$breakpoint-s) {
		line-height: $h1_lh--desktop;
		letter-spacing: $h1_ls--desktop;

		@include tool.font-size($h1_font-size--desktop);
	}
}

