@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography" as t;

@use "Tools"as tool;

.woocommerce-cart,
.woocommerce-checkout {
	.woocommerce-form-coupon-toggle {
		order: 2;
		width: 100%;
		flex-basis: 100%;
		margin-top: 10px;
		padding: 0 0 g.$gutters;

		@media (min-width: g.$breakpoint-m) {
			margin-top: 0;
			order: 0;
			padding: 0 0 g.$gutters * .5;
		}
	}

	.woocommerce-form-coupon-toggle .woocommerce-info {
		padding-left: 0;
		background-color: transparent;
		line-height: 1.54;
		letter-spacing: t.$letter-spacing;
		border-color: transparent;
		@include tool.font-size(13);

		@media (min-width: g.$breakpoint-m) {
			margin-top: g.$gutters;
			line-height: 1.73;
			letter-spacing: t.$letter-spacing;
			@include tool.font-size(15);
		}

		a {
			color: c.$base-900;
			text-decoration: underline;
		}
	}

	.woocommerce-form-coupon-toggle .woocommerce-info:before {
		content: none;
	}

	.woocommerce form.checkout_coupon {
		border: 0;
		width: 100%;
		margin-top: 0;
		margin-bottom: g.$gutters;
		display: flex;
		order: 3;
		flex-basis: 100%;
		padding: 0 0 g.$gutters * .5;

		@media (min-width: g.$breakpoint-m) {
			order: 0;
			flex-basis: 58.3333%;
			max-width: 58.3333%;
			padding: 0 0 g.$gutters * .5;
		}

		p:first-of-type {
			display: none;
		}

		.form-row {
			padding: 0;
			margin: 0;
		}

		.form-row-first {
			flex-grow: 1;
			margin-right: g.$gutters * .5;
		}

		.form-row-last {
			display: flex;
		}

		.form-row-last .button {
			flex-grow: 1;
			height: 100%;
		}
	}

	.grid .woocommerce-error,
	.grid .woocommerce-message {
		order: 3;
		margin: 0 25px g.$gutters * .5;
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			margin: 0 g.$gutters * .5 g.$gutters * .5;
			width: 58%;
		}
	}
}
