@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography" as t;

@use "Tools"as tool;

@use "Elements/headings"as *;

$form__text_color: c.$primary-variant--chambray !default;
$form__title_color: $form__text_color !default;
$form__paragraph_font-size: 16 !default;
$form__paragraph_color: $form__text_color !default;
$form__input_max-width: 320 !default;
$form__input__label_color: $form__text_color !default;
$form__input__label_font-weight: 500 !default;
$form__input__label_letter-spacing: t.$letter-spacing !default;
$form__input__label_line-height: 1.67 !default;
$form__input__label_padding-bottom: 5px !default;
$form__input__label_font-size: 12 !default;
$form__button_type: "4" !default;
$form__lost-password_color: $form__text_color !default;
$form__lost-password_letter-spacing: t.$letter-spacing !default;
$form__lost-password_line-height: 1.63 !default;
$form__lost-password_font-size: 16 !default;
$form__lost-password__h1_color: $form__text_color !default;
$form__lost-password__label_color: $form__text_color !default;

.woocommerce-account {
	&.logged-in .site-content {
		background-color: c.$white;
	}

	.site-content {
		@include tool.standard-vertical-padding;
	}

	.entry-content {
		margin-bottom: 0;
		margin-top: 0;
	}

	.entry-header {
		display: none;
	}

	&.logged-in .entry-header {
		display: block;
	}

	form.login,
	form.register {
		border: 0;
		margin: 0;
		padding: 0;
	}
}

.woocommerce-account .woocommerce .u-columns.col2-set,
.woocommerce-account .woocommerce .woocommerce-form-login {
	.col-1,
	.col-2 {
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			width: 48%;
		}
	}

	.col-1 {
		border-right: 0;
		border-bottom: 1px dashed c.$primary;

		@media (min-width: g.$breakpoint-m) {
			border-bottom: 0;
			border-right: 1px dashed c.$primary;
		}
	}

	.col-2 {
		padding-top: g.$gutters * .5;

		@media (min-width: g.$breakpoint-m) {
			padding-top: 0;
		}
	}

	.woocommerce-form-row {
		max-width: 100%;
		padding: 0;
		margin-bottom: g.$gutters * .5;

		@media (min-width: g.$breakpoint-s) {
			max-width: tool.calculate-rem($form__input_max-width);
		}
	}

	h2 {
		@extend h3;

		color: $form__title_color;
	}

	p {
		color: $form__paragraph_color;
		@include tool.font-size($form__paragraph_font-size);
	}

	a {
		color: $form__lost-password_color;
		font-stretch: normal;
		font-style: normal;
		font-weight: normal;
		letter-spacing: $form__lost-password_letter-spacing;
		line-height: $form__lost-password_line-height;
		@include tool.font-size($form__lost-password_font-size);
	}

	.woocommerce-LostPassword a {
		font-weight: 300;
	}

	.login-paragraph {
		margin: 0 0 1rem;
	}

	label {
		color: $form__input__label_color;
		font-weight: $form__input__label_font-weight;
		letter-spacing: $form__input__label_letter-spacing;
		line-height: $form__input__label_line-height;
		padding-bottom: $form__input__label_padding-bottom;
		@include tool.font-size($form__input__label_font-size);
	}

	.woocommerce-button,
	.woocommerce-Button {
		@include tool.btn;
		@include tool.btn-type($form__button_type);

		float: none;
		margin: 0;
		display: block;
	}

	.woocommerce-form-login__rememberme {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: g.$gutters * .5;

		input {
			border-radius: 2px;
			border: 0;
			background-color: c.$white;
		}

		span {
			padding-left: 5px;
			font-weight: 300;
			@include tool.font-size(14);
		}
	}
}

.woocommerce-account.woocommerce-lost-password .site-main .container,
.woocommerce-account.woocommerce-rest-password .site-main .container {
	max-width: calc(g.$container_size * .5);

	h1 {
		color: $form__lost-password__h1_color;
	}

	.woocommerce-form-row {
		width: 100%;
		max-width: 100%;
		padding: 0;
		margin-bottom: g.$gutters * .5;

		@media (min-width: g.$breakpoint-s) {
			max-width: tool.calculate-rem($form__input_max-width);
		}
	}

	label {
		color: $form__lost-password__label_color;
		font-weight: 500;
		letter-spacing: t.$letter-spacing;
		line-height: 1.67;
		padding-bottom: 5px;
		@include tool.font-size(12);
	}

	.woocommerce-Button {
		@include tool.btn;
		@include tool.btn-type($form__button_type);

		float: none;
		margin: 0;
		display: block;
	}
}
