@use "Settings/colors" as c;
@use "Settings/grid" as g;
@use "Settings/z-index" as z;

@use "Tools"as tool;

$background-color: c.$primary !default;
$color: c.$white !default;
$max-height: 68px !default;
$max-height--expanded: 600px !default;
$content_max-width: 720px !default;
$content_padding: 12px #{g.$gutters * .5} !default;
$content_font-weight: 600 !default;
$content_line-height: 1.75 !default;
$button_font-weight: 300 !default;

.info-bar {
	position: relative;
	z-index: z.$above-overlay;
	left: 0;
	top: 0;
	background-color: $background-color;
	max-height: $max-height;
	width: 100%;
	height: auto;
	overflow: hidden;
	transition: max-height 250ms cubic-bezier(0, 1, 0, 1);

	a,
	p {
		color: $color;
	}

	p {
		margin: 0;
		@include tool.font-size(13);

		@media (min-width: g.$breakpoint-l) {
			@include tool.font-size(15);
		}
	}

	.info-bar-content {
		max-width: $content_max-width;
		font-weight: $content_font-weight;
		line-height: $content_line-height;
		padding: $content_padding;
		text-align: center;
		margin: 0 auto;

		&--show-more,
		&--show-less {
			cursor: pointer;
			font-weight: $button_font-weight;

			&:hover,
			&:focus,
			&:active {
				text-decoration: underline;
			}
		}

		&--dots,
		&--show-more {
			display: inline;
		}

		&--additional-text,
		&--show-less {
			display: none;
		}
	}

	&.expanded {
		max-height: $max-height--expanded;

		.info-bar-content--dots,
		.info-bar-content--show-more {
			display: none;
		}

		.info-bar-content--additional-text {
			display: inline;
		}

		.info-bar-content--show-less {
			display: block;
		}
	}
}
