@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Tools" as tool;

$padding: g.$gutters 0 0 0 !default;

.entry-header {
	max-width: g.$container_size_narrow;
	margin-left: auto;
	margin-right: auto;

	@media (min-width: g.$breakpoint-m) {
		.page & {
			padding: $padding;
		}
	}

	.woocommerce-page & {
		max-width: g.$container_size;
	}

	.entry-title {
		word-wrap: break-word;
	}

	> * {
		@include tool.standard-horizontal-padding;
	}
}

.post-password-required {
	.entry-header {
		display: none;
	}
}

.password-box {
	background-color: c.$base-100;
	padding: tool.calculate-rem(116) 0;
	margin-left: calc(50% - 50vw);
	margin-right: calc(50% - 50vw);

	.post-password-box {
		width: auto;
		max-width: tool.calculate-rem(414);
		margin: 0 tool.calculate-rem(36);
		padding: tool.calculate-rem(38) tool.calculate-rem(22) tool.calculate-rem(42);
		background-color: c.$white;
		border-radius: tool.calculate-rem(10);

		@media (min-width: g.$breakpoint-m) {
			margin: auto;
			padding: tool.calculate-rem(40) tool.calculate-rem(36) tool.calculate-rem(32);
		}

		.lock-icon {
			width: tool.calculate-rem(91);
			height: tool.calculate-rem(91);
			border-radius: 50%;
			background-color: c.$primary-100;
			margin: auto;
			display: flex;
		}

		.lock-icon svg {
			height: tool.calculate-rem(39);
			width: auto;
			margin: auto;
		}

		.post-password-heading {
			font-size: tool.calculate-rem(20);
			font-weight: 700;
			margin: tool.calculate-rem(16) 0 tool.calculate-rem(10);

			@media (min-width: g.$breakpoint-m) {
				font-size: tool.calculate-rem(24);
			}
		}

		.post-password-message {
			font-size: tool.calculate-rem(14);
			font-weight: 400;
			margin-bottom: tool.calculate-rem(24);
			padding: 0 tool.calculate-rem(24);
		}

		.post-password-heading,
		.post-password-message,
		.post-password-form {
			text-align: center;
		}

		.post-password-form {
			position: relative;
		}

		.post-password-form__label {
			font-size: tool.calculate-rem(14);
			font-weight: 600;
			margin-bottom: tool.calculate-rem(8);
			width: 100%;
			display: flex;
			justify-content: left;

			@media (min-width: g.$breakpoint-m) {
				font-size: tool.calculate-rem(16);
			}
		}

		.post-password-form__input {
			border: 1.5px solid c.$base-1000;
			border-radius: 2px;
			background-image: url("../../../qala-theme/assets/images/icons/user/user.svg");
			background-size: tool.calculate-rem(22);
			background-position: tool.calculate-rem(12) tool.calculate-rem(10);
			background-repeat: no-repeat;
			padding: tool.calculate-rem(10.5) tool.calculate-rem(44);
		}

		.post-password-form__input.wrong-password {
			border: 1.5px solid c.$error-darker;
		}

		div.wrong-password {
			color: c.$error-darker;
			text-align: left;
			margin-top: tool.calculate-rem(8);
		}

		.post-password-form__reset {
			display: inline-block;
			background-image: url("../../../qala-theme/assets/images/icons/action/close-circle.svg");
			background-size: tool.calculate-rem(18);
			background-position: tool.calculate-rem(10) tool.calculate-rem(13);
			background-repeat: no-repeat;
			border: 0;
			border-radius: 0;
			position: absolute;
			top: tool.calculate-rem(34);
			right: tool.calculate-rem(2);
			height: tool.calculate-rem(45);
			width: tool.calculate-rem(40);
			background-color: transparent;
		}

		.post-password-form__reset.d-none {
			display: none;
		}

		.post-password-form__submit {
			margin-top: tool.calculate-rem(30);
		}
	}
}
