@use "Settings/grid"as g;
@use "Settings/colors"as c;

@use "Tools"as tool;

@use "Elements/headings" as *;
@use "Elements/body" as *;

$heading_color: c.$primary !default;
$border-color: c.$base-100 !default;
$margin-top: 20px !default;

.guides {
	counter-reset: guide-number;
	margin-top: $margin-top * -1;
	justify-content: center;

	@include tool.standard-vertical-padding;
}

.guide {
	counter-increment: guide-number;
	margin-top: $margin-top;
}

.guide-inner {
	border: solid 2px $border-color;
	padding: g.$gutters g.$gutters * .5;

	@media (min-width: g.$breakpoint-m) {
		border: 0;
		padding: 0 g.$gutters * .5;
	}
}

.guide-number {
	@extend h3;

	display: block;

	&:before {
		content: "0"counter(guide-number);
	}
}

.guide-number,
.guide-icon {
	margin-top: 0;
	margin-bottom: 5px;
	text-align: center;

	@media (min-width: g.$breakpoint-m) {
		margin-bottom: 6px;
	}
}

.guide-icon {

	@media (min-width: g.$breakpoint-m) {
		margin-bottom: 15px;
	}
}

.guide-icon img {
	max-width: 32px;

	@media (min-width: g.$breakpoint-m) {
		max-width: 35px;
	}
}

.guide-heading {
	@extend p;

	color: $heading_color;
	text-align: center;
	font-weight: 700;
	margin-top: 0;
	margin-bottom: 4px;

	@media (min-width: g.$breakpoint-m) {
		margin-bottom: 10px;
	}
}

.guide-paragraph {
	text-align: center;
	margin-bottom: 0;
	margin-top: 0;
}
