@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

$border-thickness: 2px !default;
$border-color: c.$base-300 !default;
$border: $border-thickness solid c.$base-300 !default;

.woocommerce .quantity {
	display: flex;
	position: relative;
	border: $border;

	@media (min-width: g.$breakpoint-s) {
		border: 0;
	}

	.qty {
		appearance: none;
		border: 0;
		font-weight: 500;
		line-height: 1.69;
		letter-spacing: t.$letter-spacing;
		width: 1.7em;
		@include tool.font-size( 16 );

		@media (min-width: g.$breakpoint-s) {
			width: 3em;
		}

		// Remove increment buttons in chrome.
		&::-webkit-inner-spin-button,
		&::-webkit-outer-spin-button {
			appearance: none;
			margin: 0;
		}
	}

	input[type="button"] {
		background-color: transparent;
		border: 0;
		line-height: 1.69;
		letter-spacing: t.$letter-spacing;
		color: c.$base-500;
		padding: .5em;
		position: relative;
		z-index: 2;
		font-weight: 400;
		@include tool.font-size( 15 );

		@media (min-width: g.$breakpoint-s) {
			padding: .5em 1em;
			@include tool.font-size( 16 );
		}

		&:active,
		&:active:focus,
		&:focus,
		&:hover,
		&:hover:focus:active {
			color: c.$primary;
			background-color: transparent;
		}
	}
}
