/**
 * Our implementation of sliders is based on the swiper library.
 * Component specific slider designs should live in the respective component
 * The default styles for this is loaded up with the swiper library through src/javascript/components/sliders.js
 * Documentation:
 * @link https://swiperjs.com/
 * @group Vendors
 *
 */

@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Elements/links" as l;

@import "~swiper/swiper";
@import "~swiper/modules/navigation/navigation";
@import "~swiper/modules/manipulation/manipulation";
@import "~swiper/modules/pagination/pagination";
@import "~swiper/modules/thumbs/thumbs";

$swiper-pagination-bullet-color: c.$black !default;
$swiper-pagination-bullet-color-active: l.$color !default;
$swiper-pagination-bullet-size: 8px !default;
$swiper-pagination-bullet-inactive-opacity: .2 !default;
$swiper-pagination-bullet-opacity: 1 !default;

.swiper-pagination-bullet {
	background: $swiper-pagination-bullet-color;
	width: $swiper-pagination-bullet-size;
	height: $swiper-pagination-bullet-size;
	opacity: $swiper-pagination-bullet-inactive-opacity;

	&-active {
		background: $swiper-pagination-bullet-color-active;
		opacity: $swiper-pagination-bullet-opacity;
	}
}

.slider-container {
	margin-top: 26px;
	overflow: hidden;
	position: relative;

	@media (min-width: g.$breakpoint-s) {
		margin-top: 65px;
		margin-bottom: 65px;
	}

	.btn {
		display: flex;
		width: max-content;
		margin: 46px auto 20px;

		@media (min-width: g.$breakpoint-m) {
			margin: 0 auto;
		}
	}

	.swiper-buttons {
		position: absolute;
		z-index: 2;
		bottom: 88px; // make header buttons on bottom at small breakpoints

		@media (min-width: g.$breakpoint-m) {
			position: static; // then reset
		}
	}

	&.archive-popular-products {
		.swiper-buttons {
			bottom: 0; // no .btn here so different bottom: value
		}
	}

	.swiper-initialized {
		.product {
			flex-basis: calc((100% - 2rem ) / 2) !important; // stylelint-disable-line declaration-no-important
			width: calc((100% - 2rem ) / 2) !important; // stylelint-disable-line declaration-no-important

			@media (min-width: g.$breakpoint-m) {
				flex-basis: calc((100% - 6rem ) / 4) !important; // stylelint-disable-line declaration-no-important
				width: calc((100% - 6rem ) / 4) !important; // stylelint-disable-line declaration-no-important
			}
		}
	}
}

.slider-header {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px 0;
	width: 100%;

	@media (min-width: g.$breakpoint-m) {
		justify-content: space-between;
		flex-direction: row;
	}

	&__title {
		margin: 0;
	}
}

.swiper-buttons {
	align-items: center;
	display: grid;
	gap: 10px;
	grid-template-columns: auto auto;
	min-height: 50px;
	top: unset;
	margin: 0;

	@media (min-width: g.$breakpoint-m) {
		margin-left: auto;
	}
}

.slider-arrow {
	width: 50px;
	height: 50px;
	background: #ebf4fe;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 4px;
	transition: ease-in-out .2s all;
	cursor: pointer;

	svg {
		fill: #0070f0;
	}

	&--prev {
		transform: scale(-1, -1);
	}

	&:hover,
	&:focus {
		background: #0070f0;

		svg {
			fill: #ebf4fe;
		}
	}
}

.swiper-initialized {
	position: relative;
	overflow: hidden;

	.swiper-wrapper {
		display: flex !important; // stylelint-disable-line declaration-no-important
		flex-wrap: nowrap;
	}
}

// show 2 slides on mobile

/* stylelint-disable max-nesting-depth */
.popular-products-slider,
.products-slider {
	&.swiper-initialized {
		.swiper-slide {
			transition: opacity .2s ease-in-out;
			opacity: 0;
			pointer-events: none;

			&.swiper-slide-visible:not(.swiper-slide-prev),
			&.swiper-slide-active {
				opacity: 1;
				pointer-events: all;
			}
		}
	}

	.product:nth-child(n+3) {
		display: none;
	}
}

// then 4 at breakpoint-m.
// this has to be with a duplicate selector, otherwise the media query is
// saved first and the display: none trumps the media query

/* stylelint-disable no-duplicate-selectors */
.popular-products-slider,
.products-slider {

	@media (min-width: g.$breakpoint-m) {
		.product {
			&:nth-child(n+3) {
				display: block;
			}

			&:nth-child(n+5) {
				display: none;
			}
		}
	}
}
