@use "Settings/grid"as g;
@use "Tools" as tool;

@use "Components/pages/single-page" as page;
@use "Components/pages/single-page" as post;

.entry-content {
	> * {
		max-width: g.$container_size_narrow;
		margin-right: auto;
		margin-left: auto;
		@include tool.standard-horizontal-padding;

		&.woocommerce,
		&.alignwide {
			max-width: g.$container_size;
		}

		&.alignfull {
			padding-left: unset;
			padding-right: unset;
			max-width: unset;
		}

		// make nested group block without .alignfull have default horizontal padding
		&.alignfull.wp-block-group > .wp-block-group:not(.alignfull) {
			@include tool.standard-horizontal-padding;
		}
	}
}

// override WPs questionable "is-light" with .has-black-background-color logic
// https://github.com/WordPress/gutenberg/issues/37031#issuecomment-1269928088
.wp-block-cover.is-light:has(> .has-black-background-color),
.wp-block-cover.is-light .has-black-background-color ~ .wp-block-cover__inner-container,
.wp-block-cover.is-light:has(> .has-background-dim),
.wp-block-cover.is-light .has-background-dim ~ .wp-block-cover__inner-container {
	color: #fff;
}

// for whatever reason this is not a setting you can chagne in theme.json
// I do not know what WP are smoking with the gallery settings, I don't
// think they know either:
// https://github.com/WordPress/WordPress/blob/master/wp-includes/blocks/gallery.php#L87
.wp-block-gallery {
	--wp--style--gallery-gap-default: 1em;
}
