@use "Settings/grid" as g;

$font-size: inherit !default;
$font-weight: 300 !default;
$ul_font-size: $font-size !default;
$ul_font-weight: $font-weight !default;
$ul_list-style: initial !default;
$ol_font-size: $font-size !default;
$ol_font-weight: $font-weight !default;
$ol_list-style: decimal !default;

ul {
	font-size: $ul_font-size;
	font-weight: $ul_font-weight;
	list-style: $ul_list-style;
}

ol {
	font-size: $ol_font-size;
	font-weight: $ol_font-weight;
	list-style: $ol_list-style;
}

ul,
ol {
	.entry-content > & {
		padding-left: calc(g.$gutters-smallscreen-horizontal + 15px);
		padding-right: calc(g.$gutters-smallscreen-horizontal + 15px);

		@media (min-width: g.$gutters-smallscreen-breakpoint) {
			padding-left: calc(g.$gutters + 15px);
			padding-right: calc(g.$gutters + 15px);
		}
	}
}
