@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;

@use "Tools" as tool;

@use "Elements/headings" as *;
@use "Components/headings" as *;

// ==================
// ====== FORM ======
// ==================
$billing-fields_field-spacing: calculaterem(15) !default;
$billing-fields_border-color: c.$base-100 !default;
$billing-fields_border-thickness: 1px !default;
$billing-fields_border: 1px solid $billing-fields_border-color !default;

// Single fields
$billing-fields__field__label_text-transform: uppercase !default;
$billing-fields__field__label_line-height: 1.6 !default;
$billing-fields__field__label_letter-spacing: t.$letter-spacing--large !default;
$billing-fields__field__label_font-weight: 700 !default;
$billing-fields__field__label_font-family: t.$font__heading !default;
$billing-fields__field__label_font-size: 10 !default;
$billing-fields__field__label_color: c.$base-500 !default;
$billing-fields__field__label_margin-bottom: 5px !default;
$billing-fields__field__label_line-height--desktop: 1.45 !default;
$billing-fields__field__label_letter-spacing--desktop: 2.2px !default;
$billing-fields__field__label_font-size--desktop: 11 !default;

// =============================
// ====== PAYMENT METHODS ======
// =============================
$payment-methods__payment-method_padding: 20px !default;
$payment-methods__payment-method_padding--desktop: 35px !default;
$payment-methods__payment-method_background-color: transparent !default;
$payment-methods__payment-method_background-color--active: c.$primary !default;
$payment-methods__payment-method_margin-bottom: 9px !default;
$payment-methods__payment-method_border-thickness: 1px !default;
$payment-methods__payment-method_border-color: c.$base-300 !default;
$payment-methods__payment-method_border: $payment-methods__payment-method_border-thickness
	solid $payment-methods__payment-method_border-color !default;
$payment-methods__payment-method_box-shadow: 0 2px 4px 0 rgb(0 0 0 / 3%) !default;

// Label
$payment-methods__payment-method__label_font-weight: 700 !default;
$payment-methods__payment-method__label_line-height: 1.73 !default;
$payment-methods__payment-method__label_letter-spacing: t.$letter-spacing !default;
$payment-methods__payment-method__label_color: c.$base-900 !default;
$payment-methods__payment-method__label_color--active: c.$white !default;
$payment-methods__payment-method__label_font-size: 15 !default;
$payment-methods__payment-method__label_font-size--desktop: 17 !default;

// Description
$payment-methods__payment-method__description_color: $payment-methods__payment-method__label_color--active !default;

// Checkbox
$payment-methods__payment-method__checkbox_background-color: $payment-methods__payment-method__description_color !default;
$payment-methods__payment-method__checkbox_border-color: c.$base-300 !default;
$payment-methods__payment-method__checkbox_fill-color: c.$primary-variant--chambray !default;

// ============================
// ====== BOTTOM CONTENT ======
// ============================
$privacy-policy_font-size: 13 !default;
$privacy-policy_font-size--desktop: 15 !default;

.woocommerce-checkout,
.woocommerce-cart {
	.customer-details-wrapper {
		order: 2;

		@media (min-width: g.$breakpoint-m) {
			order: 0;
		}

		.col2-set {
			padding-top: g.$gutters;
			border-top: $billing-fields_border;

			@media (min-width: g.$breakpoint-m) {
				padding-top: 0;
				border-top: 0;
			}
		}
	}

	.woocommerce table.shop_table {
		border: 0;
		margin: 0;
		border-collapse: collapse;
		border-radius: 0;
		width: 100%;
	}

	.woocommerce form .form-row:after,
	.woocommerce form .form-row:before,
	.woocommerce-page form .form-row:after,
	.woocommerce-page form .form-row:before {
		display: none;
	}

	.woocommerce .col2-set .col-1,
	.woocommerce-page .col2-set .col-1,
	.woocommerce .col2-set .col-2,
	.woocommerce-page .col2-set .col-2 {
		width: 100%;
		float: none;
	}

	.woocommerce-billing-fields,
	.woocommerce-shipping-fields,
	.woocommerce-additional-fields {
		.form-row {
			margin-bottom: $billing-fields_field-spacing;
		}

		.form-row-first,
		.woocommerce-page form .form-row-first,
		.form-row-last,
		.woocommerce-page form .form-row-last {
			width: 100%;

			@media (min-width: g.$breakpoint-m) {
				width: 47%;
			}
		}

		label {
			text-transform: $billing-fields__field__label_text-transform;
			line-height: $billing-fields__field__label_line-height;
			letter-spacing: $billing-fields__field__label_letter-spacing;
			font-weight: $billing-fields__field__label_font-weight;
			font-family: $billing-fields__field__label_font-family;
			color: $billing-fields__field__label_color;
			margin-bottom: $billing-fields__field__label_margin-bottom;
			@include tool.font-size($billing-fields__field__label_font-size);

			@media (min-width: g.$breakpoint-m) {
				line-height: $billing-fields__field__label_line-height--desktop;
				letter-spacing: $billing-fields__field__label_letter-spacing--desktop;
				@include tool.font-size(
					$billing-fields__field__label_font-size--desktop
				);
			}
		}
	}

	/* stylelint-disable selector-max-id, selector-id-pattern */
	#add_payment_method #payment,
	.woocommerce-cart #payment,
	.woocommerce-checkout #payment {
		border-bottom: 0;
		background-color: transparent;

		ul.payment_methods {
			padding: 0;
			border-bottom: 0;
		}

		ul.payment_methods li {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			padding: $payment-methods__payment-method_padding;
			background-color: $payment-methods__payment-method_background-color;
			margin-bottom: $payment-methods__payment-method_margin-bottom;
			border: $payment-methods__payment-method_border;
			box-shadow: $payment-methods__payment-method_box-shadow;

			@media (min-width: g.$breakpoint-m) {
				padding: $payment-methods__payment-method_padding--desktop;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		ul.payment_methods li.active-payment-method {
			background-color: $payment-methods__payment-method_background-color--active;
		}

		ul.payment_methods li.active-payment-method label {
			color: $payment-methods__payment-method__label_color--active;
		}

		ul.payment_methods label {
			flex: 1 0 auto;
			display: flex;
			align-items: center;
			font-weight: $payment-methods__payment-method__label_font-weight;
			line-height: $payment-methods__payment-method__label_line-height;
			letter-spacing: $payment-methods__payment-method__label_letter-spacing;
			color: $payment-methods__payment-method__label_color;
			@include tool.font-size(
				$payment-methods__payment-method__label_font-size
			);

			@media (min-width: g.$breakpoint-m) {
				@include tool.font-size(
					$payment-methods__payment-method__label_font-size--desktop
				);
			}
		}

		div.payment_box {
			background-color: transparent;
			color: $payment-methods__payment-method__description_color;
			margin: 0;
			padding: 0 0 0 29px;
		}

		div.payment_box:before {
			border: 0;
		}

		div.form-row,
		.woocommerce-cart #payment div.form-row,
		.woocommerce-checkout #payment div.form-row {
			padding: 53px 0 0;
		}
	}

	#add_payment_method #payment ul.payment_methods li input,
	.woocommerce-cart #payment ul.payment_methods li input,
	.woocommerce-checkout #payment ul.payment_methods li input {
		margin-top: -5px;

		&:after {
			width: 16px;
			height: 16px;
			border-radius: 15px;
			top: -1px;
			left: -1px;
			position: relative;
			background-color: $payment-methods__payment-method__checkbox_background-color;
			content: "";
			display: inline-block;
			visibility: visible;
			border: solid 1px $payment-methods__payment-method__checkbox_border-color;
			border: solid 1px $payment-methods__payment-method__checkbox_fill-color;
		}

		&:checked:after {
			background-color: $payment-methods__payment-method__checkbox_fill-color;
			border: 4px solid white;
		}
	}

	/* stylelint-enable */
	.payment_method_billmate_cardpay img {
		display: none;
	}

	.final-checkout-total {
		@extend .h3;

		margin-bottom: 24px;
	}

	.woocommerce-privacy-policy-text p,
	.woocommerce-terms-and-conditions-checkbox-text {
		@include tool.font-size($privacy-policy_font-size);

		@media (min-width: g.$breakpoint-m) {
			@include tool.font-size($privacy-policy_font-size--desktop);
		}

		a {
			text-decoration: underline;
		}
	}

	form .form-row .woocommerce-terms-and-conditions-wrapper {
		margin-bottom: g.$gutters;
	}

	form .form-row .woocommerce-terms-and-conditions-wrapper label.checkbox {
		display: flex;
		align-items: flex-start;
		line-height: normal;
	}

	form .form-row .woocommerce-terms-and-conditions-wrapper .input-checkbox {
		margin-top: 0;
	}

	/* stylelint-disable selector-max-id, selector-id-pattern */
	.woocommerce-invalid #terms {
		outline: none;
	}

	.woocommerce #payment #place_order,
	.woocommerce-page #payment #place_order {
		width: auto;
		float: none;
	}

	/* stylelint-enable */
}

// Adyen credit card image style
.payment_method_woosa_adyen_credit_card > label > img {
	object-fit: none;
	object-position: left;
	height: 24px;
}
