@use "Settings/colors" as c;
@use "Settings/grid" as g;
@use "Settings/typography" as t;

@use "Tools" as tool;

@use "Components/headings" as h;

$color: c.$white !default;
$background-color: c.$primary-300 !default;
$gap: .4rem !default;
$padding: .3em 1.1em !default;
$margin: 0 0 1.25rem 0 !default;
$border-radius: 3px !default;
$font-size: 10 !default;
$font-size--desktop: 10 !default;
$font-weight: 400 !default;
$font-style: normal !default;
$font-stretch: normal !default;
$letter-spacing: t.$letter-spacing !default;
$line-height: 1.5 !default;
$line-height--desktop: 1.5 !default;
$text-transform: uppercase !default;

.product-tags {
	display: flex;
	flex-wrap: wrap;
	padding-left: 25px;
	padding-right: 25px;
	gap: $gap;
	margin: $margin;

	@media (min-width: g.$breakpoint-m) {
		padding-left: 0;
		padding-right: 0;
	}

	a {
		color: $color;
		background-color: $background-color;
		padding: $padding;
		border-radius: $border-radius;
		white-space: nowrap;
		font-weight: $font-weight;
		font-style: $font-style;
		font-stretch: $font-stretch;
		letter-spacing: $letter-spacing;
		line-height: $line-height;
		text-transform: $text-transform;

		@include tool.font-size($font-size);

		@media (min-width: g.$breakpoint-s) {
			line-height: $line-height--desktop;

			@include tool.font-size($font-size--desktop);
		}
	}
}

/* Don't if I should create a new file for Product Categories List - START */
.wc-block-product-categories {
	margin-bottom: tool.calculate-rem(16);
}

.wc-block-product-categories-list-item-count {
	&:before {
		content: " (";
	}

	&:after {
		content: ")";
	}
}
