@use "sass:math";

@use "Settings/buttons"as b;
@use "Settings/colors"as c;
@use "Settings/grid"as g;
@use "Settings/general"as ge;
@use "Settings/z-index"as z;

$height: 55px !default;
$height--desktop: 134px !default;
$height--sticky: 70px !default;
$background-color: c.$white !default;
$z-index: z.$header !default;
$branding_max-height: $height * .5 !default;
$branding_max-height--desktop: math.div($height--desktop, 1.5) !default;
$branding_height: 40px !default;
$branding_height--desktop: $branding_max-height--desktop * .6 !default;
$branding_color: c.$base-900 !default;
$branding_padding: 0 30px 0 0 !default;
$mobile-menu__close-button_color: c.$white !default;
$mobile-menu__close-button_width: 22px !default;
$search_input_background_colour: #f2f2f2 !default;
$search_input_background_colour--hover: #e5e5ea !default;
$search_input_placeholder_colour: #9b9b9b !default;
$search_input_focus_colour: c.$alerts-success-48 !default;
$search_input_border_colour: transparent !default;
$search_input_border_colour--hover: transparent !default;
$search_input_border_colour--filled: c.$base-500 !default;
$search_input_icon_background_colour: transparent !default;
$search_input_icon_background_colour--active: c.$primary-500 !default;
$search_input_icon_background_colour--hover: c.$primary-600 !default;
$search_input_icon_colour: c.$black !default;
$search_input_icon_colour--hover: c.$white !default;

.site-branding {
	vertical-align: middle;
	line-height: $height;

	@media (min-width: g.$breakpoint-l) {
		line-height: 1.4;
	}

	a {
		display: inline-block;
		max-width: 100%;
		max-height: 100%;
	}

	img {
		height: $branding_height;
		max-height: 100%;
		width: auto;

		@media (min-width: g.$breakpoint-xl) {
			height: $branding_height--desktop;
		}
	}
}

.site-header {
	position: relative;
	z-index: $z-index;
	box-shadow: ge.$box-shadow__bottom;
	width: 100%;
	background-color: $background-color;
}

.site-header.js-is-stuck {
	top: 0;
}

.admin-bar .site-header.js-is-stuck {
	top: 46px;

	@media (min-width: 781px) {
		top: 32px;
	}
}

.site-title {
	margin: 0;
}

.site-content [class*="navigation"] {
	margin: 0 0 1.5em;
	overflow: hidden;
}

[class*="navigation"] .nav-previous {
	float: left;
	width: 50%;
}

[class*="navigation"] .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

.toggle-sub-menu {
	display: none;
}

.main-navigation {
	display: flex;
	align-items: center;
	justify-content: space-between;

	.site-header-right-column {
		.site-title a {
			color: $branding_color;

			&:hover,
			&:focus {
				text-decoration: none;
			}
		}
	}
}

.mobile-nav-toggle {
	margin-right: 0;
	padding: 5px 0 0;
	background-color: transparent;
	background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
	border: 0;

	@media (min-width: g.$breakpoint-l) {
		display: none;
	}

	&:focus {
		outline: 0;
	}
}

.close-mobile-nav {
	display: none;
}

.mobile-open .close-mobile-nav {
	display: block;
	position: absolute;
	left: 85vw;
	top: 0;
	width: 15vw;
	height: 100%;
	text-align: center;

	@media (min-width: g.$breakpoint-l) {
		display: none;
	}

	svg {
		fill: $mobile-menu__close-button_color;
		width: $mobile-menu__close-button_width;

		g {
			stroke: $mobile-menu__close-button_color;
		}
	}
}

.site-header__user-button {
	a {
		display: flex;
	}
}

.site-header__desktop-column__button {
	display: none !important; /* stylelint-disable-line */

	@media (min-width: g.$breakpoint-l) {
		display: flex !important; /* stylelint-disable-line */
	}
}

/* stylelint-disable max-nesting-depth */
.site-header--layout-logo-centered {
	.main-navigation {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}

	.site-branding {
		grid-area: 1 / 1 / 1 / 2;
		margin: 12px 0;
	}

	.site-header-right-column {
		grid-area: 1 / 2 / 1 / 3;
	}

	.site-search-form {
		grid-area: 2 / 1 / 2 / 3;
		margin-bottom: 20px;
		transition: margin .3s;

		&.site-search-form--hide-mob {
			display: none;
		}

		.form-group {
			display: flex;
			position: relative;
		}

		.form-control {
			border-radius: 32px;
			height: 54px;
			background-color: $search_input_background_colour;
			border-color: $search_input_border_colour;
			padding: .75rem 4rem .75rem 1.5rem;

			&::placeholder {
				color: $search_input_placeholder_colour;
			}

			&:focus-visible {
				outline-color: $search_input_focus_colour;
			}

			&:focus {
				outline-color: $search_input_border_colour--hover;
				background-color: $search_input_background_colour--hover;
			}

			&:hover,
			&:focus {
				border-color: transparent;
				background-color: $search_input_background_colour--hover;
			}

			&:not(:placeholder-shown) {
				border-color: $search_input_border_colour--filled;

				& ~ .btn {
					background-color: $search_input_icon_background_colour--active;

					path {
						fill: $search_input_icon_colour--hover;
					}
				}
			}
		}

		.btn {
			position: absolute;
			right: 0;
			top: 0;
			height: 54px;
			width: 54px;
			border-radius: 100%;
			background-color: $search_input_icon_background_colour;
			border-color: transparent;
			padding: 0;

			&:hover,
			&:focus {
				background-color: $search_input_icon_background_colour--hover !important; // stylelint-disable-line declaration-no-important

				path {
					fill: $search_input_icon_colour--hover;
				}
			}

			svg {
				width: 21px;
				height: 21px;

				path {
					fill: $search_input_icon_colour;
				}
			}
		}
	}

	.main-navigation--hamburger-left {
		grid-template-columns: 1fr 1fr 1fr;
		position: static;
		transition: grid-template-columns .3s;

		.mobile-toggle {
			grid-area: 1 / 1 / 1 / 2;

			.mobile-menu-open & {
				margin: 0;
				top: 28px;
				right: 30px;
			}
		}

		.site-branding {
			grid-area: 1 / 2 / 1 / 2;
		}

		.site-header-right-column {
			grid-area: 1 / 3 / 1 / 3;
		}

		.site-search-form {
			grid-area: 2 / 1 / 2 / 4;

			@media (min-width: g.$breakpoint-l) {
				grid-area: 1 / 1 / 2 / 2;
			}
		}
	}

	&.js-is-sticky,
	&.js-is-stuck {
		.main-navigation--hamburger-left {
			grid-template-columns: 1fr 2.5fr 1fr;

			@media (min-width: g.$breakpoint-l) {
				grid-template-columns: 1fr 1fr 1fr;
			}

			.site-search-form {
				grid-area: 1 / 2 / 1 / 2;
				margin-top: 20px;

				@media (min-width: g.$breakpoint-l) {
					grid-area: 1 / 1 / 2 / 2;
					margin-top: 0;
				}
			}

			.site-branding {
				visibility: hidden;

				@media (min-width: g.$breakpoint-l) {
					visibility: visible;
				}
			}
		}
	}

	@media (min-width: g.$breakpoint-l) { /* stylelint-disable-line order/order */
		.main-navigation {
			grid-template-columns: 1fr 1fr 1fr;
		}

		.main-navigation__right-column-header-buttons {
			display: grid;
			grid-template-columns: 1fr 1fr;
		}

		.site-search-form {
			grid-area: 1 / 1 / 2 / 2;
			margin: 0;

			form,
			.qesf-header-search {
				margin-right: 48px;
				max-width: 77%;
			}

			&.site-search-form--hide-mob {
				display: block;
			}
		}

		.site-branding {
			grid-area: 1 / 2 / 2 / 3;
			margin: 18px 0;
			display: flex;
			justify-content: center;
		}

		.site-header-right-column {
			grid-area: 1 / 3 / 2 / 4;
		}

		.site-header-buttons.desktop {
			grid-area: 1 / 4 / 2 / 4;
		}

		.ac-nav-collapse {
			grid-area: 2 / 1 / 2 / 4;

			.nav-menu > .menu-item {
				// need this to stop jump on scroll when js-is-sticky--change active
				height: 100% !important; // stylelint-disable-line declaration-no-important

				> a {
					padding: 16px 0 26px !important; // stylelint-disable-line declaration-no-important
				}
			}

			.nav-menu .btn {
				margin-top: -10px;
			}
		}
	}
}

.main-navigation--hamburger-left {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	position: relative;

	@media (min-width: g.$breakpoint-l) {
		display: flex;
	}

	> .mobile-toggle {
		display: flex;

		button {
			margin: 0;
			padding: 0;

			&:hover,
			&:focus {
				border: 0;
				background-color: transparent;
			}
		}

		svg {
			height: 24px;
			width: auto;
		}

		.mobile-menu-open & {
			z-index: 11;
			top: 0;
			position: absolute;
			bottom: 0;
			margin: auto;
			height: 24px;
			right: 0;

			path {
				fill: white;
			}
		}
	}

	.site-branding {
		display: flex;
		justify-content: center;
	}

	.site-header-right-column {

		@media (min-width: g.$breakpoint-s) {
			padding-left: g.$gutters-smallscreen-horizontal;
		}
	}
}

/* stylelint-enable */
.site-header-buttons {
	display: grid;
	gap: 15px;
	margin: unset;
	padding: 1rem 2rem;
	list-style: none;

	@media (min-width: g.$breakpoint-l) {
		display: none;
	}

	&.desktop {
		display: none;
		padding: unset;
		margin-left: 20px;

		@media (min-width: g.$breakpoint-l) {
			display: flex;
			align-items: center;
		}
	}

	.btn {
		text-align: center;

		a {
			color: inherit;
			font-weight: b.$font-weight;
		}
	}
}
