@use "Settings/colors" as c;
@use "Settings/typography" as t;

@use "Tools"as tool;

@use "Components/pages/woocommerce/product"as p;

.quantity-wrapper {
	display: flex;
	border: 2px solid p.$meta_border-color;
	width: max-content;
	margin: 0 tool.calculate-rem(16) tool.calculate-rem(16) 0;

	&.disabled {
		display: none;
	}
}

.quantity-wrapper .quantity-modifier {
	background: none;
	border: 0;
	padding: 0 10px;
	color: c.$primary-500;
	font-weight: 500;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.73;
	letter-spacing: t.$letter-spacing;
	@include tool.font-size(15);

	&:focus,
	&:active,
	&:hover {
		outline: 0;
		border: 0;
	}
}

.quantity-wrapper .plus {
	padding-left: 5px;
}

.quantity-wrapper .minus {
	padding-right: 5px;
}

div.quantity {
	display: flex;
	margin: 0;

	input[type="number"] {
		border: 0;
		padding: 0;
		width: 40px;
		border-radius: 0;
		appearance: none;

		&::-webkit-inner-spin-button,
		&::-webkit-outer-spin-button {
			appearance: none;
			margin: 0;
		}
	}
}
