@use "Settings/grid" as g;
@use "Settings/colors" as c;

.archive-popular-products {
	overflow: hidden;
	padding-top: 40px;
	margin-bottom: 40px;

	@media (min-width: g.$breakpoint-s) {
		padding-top: 50px;
	}

	@media (min-width: g.$breakpoint-m) {
		margin-bottom: 0;
	}

	.popular-products-slider {
		padding: 15px 0;
	}

	h3 {
		margin-top: 0;

		@media (min-width: g.$breakpoint-l) {
			text-align: left;
		}
	}
}

.popular-products-slider {
	display: flex;
	width: 100%;
	position: relative;

	.qala-wishlist-button {
		z-index: 2;
	}

	/* stylelint-disable declaration-no-important  */
	ul.products[class*="columns-"] {
		display: flex !important; // display block override from {.qesf-loaded.woocommerce .archive-popular-products ul.products}

		&:before {
			display: none !important; // override to fix Woo CSS causing CLS (*huge* cumulative layout shift)
		}
	}

	body &.swiper-initialized ul.products {
		flex-wrap: nowrap;

		.product {
			display: block;
		}
	}
}
