@use "Settings/grid"as g;
@use "Settings/typography" as t;
@use "Settings/colors"as c;
@use "Elements/links"as l;

@use "Tools"as tool;

.post-type-archive-product main.site-main {
	@include tool.standard-vertical-padding;
}

.woocommerce .site-main .woocommerce-result-count {
	margin: 1em 0;
	@include tool.font-size(12);
}

// todo: Currently no clear way of customizing the grid layout. Maybe this can be refactored in to a newer technology(Grid or Flex), provided that this customization is of use to us?
.woocommerce ul.products,
.woocommerce-page ul.products {
	display: flex;
	flex-wrap: wrap;
	margin-right: -1rem;
	margin-left: -1rem;

	li.product {
		margin-right: 1rem;
		margin-left: 1rem;
		clear: none;
	}

	&.columns-1 li.product {
		width: 100%;
	}

	&.columns-2 li.product {
		flex-basis: calc((100% - 4rem ) / 2);
		width: calc((100% - 4rem ) / 2);
	}

	&.columns-3 li.product {
		flex-basis: calc((100% - 4rem ) / 2);
		width: calc((100% - 4rem ) / 2);

		@media (min-width: g.$breakpoint-m) {
			flex-basis: calc((100% - 6rem ) / 3);
			width: calc((100% - 6rem ) / 3);
		}
	}

	&.columns-4 li.product {
		flex-basis: calc((100% - 4rem ) / 2);
		width: calc((100% - 4rem ) / 2);

		@media (min-width: g.$breakpoint-m) {
			flex-basis: calc((100% - 8rem ) / 4);
			width: calc((100% - 8rem ) / 4);
		}
	}

	&.columns-5 li.product {
		flex-basis: calc((100% - 4rem ) / 2);
		width: calc((100% - 4rem ) / 2);

		@media (min-width: g.$breakpoint-m) {
			flex-basis: calc((100% - 10rem ) / 5);
			width: calc((100% - 10rem ) / 5);

			&:nth-child(6n) {
				clear: both;
			}
		}
	}

	&.columns-6 li.product {
		flex-basis: calc((100% - 4rem ) / 2);
		width: calc((100% - 4rem ) / 2);

		@media (min-width: g.$breakpoint-m) {
			flex-basis: calc((100% - 12rem ) / 6);
			width: calc((100% - 12rem ) / 6);

			&:nth-child(7n) {
				clear: both;
			}
		}
	}

	li.product mark {
		background-color: unset;
	}
}

.woocommerce nav.woocommerce-pagination ul {
	border: 0;

	li {
		border-right: 0;

		span,
		a {
			width: 46px;
			height: 46px;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			margin: 0 tool.calculate-rem(4);
			transition: all .2s ease-in-out;
			border: 2px solid transparent;
			color: t.$color__content;

			/* stylelint-disable max-nesting-depth */
			&.current {
				background-color: transparent;
				color: inherit;
				border-color: l.$color;
			}
		}

		a {
			&:hover,
			&:focus {
				background-color: l.$color;
				color: c.$white;
				border-color: l.$color;
			}
		}

		.next,
		.prev {
			font-size: 1.25rem;

			path {
				fill: t.$color__content;
			}

			&:hover,
			&:focus {
				path {
					fill: c.$white;
				}
			}
		}

		.next {
			svg {
				transform: translateX(1.5px);
			}
		}

		.prev {
			svg {
				transform: translateX(-1.5px);
			}
		}

		/* stylelint-enable */
	}
}

.qesf-pagination-block {
	margin: 0;

	button {
		width: 46px;
		height: 46px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		margin: 0 tool.calculate-rem(4);
		transition: all .2s ease-in-out;
		border: 2px solid transparent;
		font-family: inherit;
		color: t.$color__content;

		&:hover,
		&:focus {
			background-color: l.$color;
			color: c.$white;
			border-color: l.$color;
		}

		/* stylelint-disable declaration-no-important */
		&.Mui-selected {
			background-color: transparent;
			color: inherit;
			border: 2px solid l.$color !important;

			&:hover,
			&:focus {
				background-color: transparent;
			}
		}

		&.Mui-disabled {
			color: inherit !important;
			background-color: transparent !important;
			border-color: transparent !important;
			opacity: 1;
		}

		/* stylelint-enable */
	}
}

.woocommerce ul.products li.product.product-category {
	margin-bottom: tool.calculate-rem(32);

	mark {
		display: none;
	}

	a {
		text-decoration-color: #000;
		text-decoration-line: underline;

		/* stylelint-disable declaration-block-no-redundant-longhand-properties */
		text-decoration-style: solid;
		text-decoration-thickness: 1px;
		text-decoration-skip-ink: none;
		text-underline-offset: 5px;
		display: flex;
		align-items: center;
		flex-direction: column;

		img {
			width: 100px;
			height: 100px;
			border: 0;
			border-radius: 50%;
		}
	}
}
