@use "Settings/colors"as c;
@use "Settings/typography" as t;

@use "Tools"as tool;

$input_font-size: 16 !default;
$input_font-weight: 400 !default;
$label_font-weight: 600 !default;
$input_color: c.$base-1000 !default;
$input_color--placeholder: c.$base-600 !default;
$input_background-color--error: c.$error-lighter !default;
$input_background-color--pass: #edf7f1 !default;
$input_border-thickness: 1.5px !default;
$input_border-color: c.$base-400 !default;
$input_border-color--error: c.$error-darker !default;
$input_border-color--pass: c.$success !default;
$input_border-color--focus: c.$base-1000 !default;
$input_border: $input_border-thickness solid $input_border-color !default;
$input_border-radius: 2px !default;
$input_padding: tool.calculate-rem(12) tool.calculate-rem(40) tool.calculate-rem(12) tool.calculate-rem(16) !default;

// Checkbox
$checkbox_border-thickness: 1.5px !default;
$checkbox_border-color: c.$base-300 !default;
$checkbox_border: $checkbox_border-thickness solid $checkbox_border-color !default;
$checkbox_tick-color: c.$primary !default;

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="date"],
select,
textarea {
	font-family: t.$font__inter;
	color: $input_color;
	border: $input_border;
	border-radius: $input_border-radius;
	max-width: 100%;
	line-height: 24px;
	width: 100%;
	padding: $input_padding;
	font-weight: $input_font-weight;
	@include tool.font-size($input_font-size);

	&::placeholder {
		color: $input_color--placeholder;
	}

	&:focus {
		border-color: $input_border-color--focus;
	}
}

.woocommerce form .form-row.woocommerce-invalid .select2-container,
.woocommerce form .form-row.woocommerce-invalid input.input-text,
.woocommerce form .form-row.woocommerce-invalid select {
	border: $input_border-thickness solid $input_border-color--error;
	background-color: rgba($input_background-color--error, .1);
}
