/* stylelint-disable declaration-no-important */
@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/buttons"as b;
@use "Settings/typography"as t;

@use "Tools"as tool;

// Todo: All button types should be unnested or constructed with a tool or function to also apply to the gutenberg buttons. NAOW
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.wp-block-button__link,
.btn {
	@include tool.btn;

	&.btn-medium,
	.btn-medium > & {
		border-width: b.$border--full_thickness--medium;

		@media (min-width: g.$breakpoint-s) {
			padding: b.$padding-btn_medium;
			line-height: b.$line-height-btn_medium;
			@include tool.font-size(b.$font-size-btn_medium);
		}
	}

	&.btn-large,
	.btn-large > & {
		padding: b.$padding-btn_large;
		line-height: b.$line-height-btn_large;
		border-width: b.$border--full_thickness--large;
		@include tool.font-size(b.$font-size-btn_large);

		@media (max-width: g.$breakpoint-s) {
			padding: b.$padding-btn_medium;
			line-height: b.$line-height-btn_medium;
			@include tool.font-size(b.$font-size-btn_medium);
		}
	}

	&.btn-type-5,
	&.btn-type-6 {
		&.btn-medium,
		&.btn-large {
			padding-right: 0;
			padding-left: 0;
		}
	}

	&.single_add_to_cart_button {
		padding-top: tool.calculate-rem(17);
		padding-bottom: tool.calculate-rem(17);
	}

	.arrow-left > &,
	&.arrow-left {
		&:before {
			margin: tool.calculate-rem(-2.5) tool.calculate-rem(b.$btn-arrow-margin-horizontal) 0 0;
			width: tool.calculate-rem(b.$btn-arrow-size);
			height: tool.calculate-rem(b.$btn-arrow-size);
			@include tool.arrow;
		}
	}

	.arrow-right > &,
	&.arrow-right {
		position: relative;
		min-width: 36px;
		min-height: 36px;

		&:after {
			width: tool.calculate-rem(b.$btn-arrow-size);
			height: tool.calculate-rem(b.$btn-arrow-size);
			margin: tool.calculate-rem(-2.5) 0 0 tool.calculate-rem(b.$btn-arrow-margin-horizontal);
			@include tool.arrow;
		}
	}

	&.btn-medium.arrow-left,
	.btn-medium.arrow-left > &,
	&.btn-medium.arrow-right,
	.btn-medium.arrow-right > & {
		&:before,
		&:after {
			width: tool.calculate-rem(b.$btn-arrow-size-btn_medium);
			height: tool.calculate-rem(b.$btn-arrow-size-btn_medium);
		}

		&:before {
			margin: tool.calculate-rem(-2.5) tool.calculate-rem(b.$btn-arrow-margin-horizontal-btn_medium) 0 0;
		}

		&:after {
			margin: tool.calculate-rem(-2.5) 0 0 tool.calculate-rem(b.$btn-arrow-margin-horizontal-btn_medium);
		}
	}

	&.btn-large.arrow-left,
	.btn-large.arrow-left > &,
	&.btn-large.arrow-right,
	.btn-large.arrow-right > & {
		&:before,
		&:after {
			width: tool.calculate-rem(b.$btn-arrow-size-btn_large);
			height: tool.calculate-rem(b.$btn-arrow-size-btn_large);
		}

		&:before {
			margin: tool.calculate-rem(-3) tool.calculate-rem(b.$btn-arrow-margin-horizontal-btn_large) 0 0;
		}

		&:after {
			margin: tool.calculate-rem(-3) 0 0 tool.calculate-rem(b.$btn-arrow-margin-horizontal-btn_large);
		}
	}
}

.btn-type-2 {
	@include tool.btn-type(2);
}

.btn-type-3 {
	@include tool.btn-type(3);
}

.btn-type-4 {
	@include tool.btn-type(4);
}

.btn-type-5,
.btn-link {
	@include tool.btn-type(5);
}

.btn-type-6 {
	@include tool.btn-type(6);
}

.btn-type-7 {
	@include tool.btn-type(7);
}

.full-width-button {
	width: 100%;
	text-align: center;

	@media (min-width: g.$breakpoint-m) {
		width: auto;
		margin-top: 0;
		text-align: left;
	}

	&.cta-secondary {
		margin-left: 0;
	}
}

.btn-link {
	transition: .2s ease-in-out color;
	text-decoration: none;
	letter-spacing: t.$letter-spacing;
	font-weight: 300;
	padding: 0;
	margin: 0;
	text-transform: none;

	&[href="#"] {
		cursor: pointer;
	}
}

/** Dark mode
	* This is activated when a parent element has the class "dark-mode"
**/

.dark-mode {
	button,
	input[type="button"],
	input[type="reset"],
	input[type="submit"],
	.wp-block-button__link,
	.woocommerce-mini-cart__buttons a.button,
	.btn {
		color: b.$color--dark-mode;
		background-color: b.$background-color--dark-mode;
		border-color: b.$border-color--dark-mode;

		&:hover,
		&:focus {
			background-color: b.$background-color--dark-mode-hover;
			border-color: b.$border-color--dark-mode-hover;
		}

		&:focus {
			outline-color: c.$base-300;
		}

		&:active,
		&:active:focus {
			background-color: b.$background-color--dark-mode-active;
			border-color: b.$border-color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$disabled_color--dark-mode !important;
			background-color: c.$base-900 !important;
			border-color: c.$base-900 !important;
		}
	}

	.btn-type-2 {
		color: b.$type-2_color--dark-mode;
		background-color: b.$type-2_background-color--dark-mode;
		border-color: b.$type-2_border-color--dark-mode;

		&:hover,
		&:focus {
			background-color: b.$type-2_background-color--dark-mode-hover;
			border-color: b.$type-2_border-color--dark-mode-hover;
			color: b.$type-2_color--dark-mode-hover;
		}

		&:active,
		&:active:focus {
			background-color: b.$type-2_background-color--dark-mode-active;
			border-color: b.$type-2_border-color--dark-mode-active;
			color: b.$type-2_color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-2_color--dark-mode-disabled !important;
			background-color: b.$type-2_background-color--dark-mode-disabled !important;
			border-color: b.$type-2_border-color--dark-mode-disabled !important;
		}
	}

	.btn-type-3 {
		color: b.$type-3_color--dark-mode;
		background-color: b.$type-3_background-color--dark-mode;
		border-color: b.$type-3_border-color--dark-mode;

		&:hover,
		&:focus {
			background-color: b.$type-3_background-color--dark-mode-hover;
			border-color: b.$type-3_border-color--dark-mode-hover;
			color: b.$type-3_color--dark-mode-hover;
		}

		&:active,
		&:active:focus {
			background-color: b.$type-3_background-color--dark-mode-active;
			border-color: b.$type-3_border-color--dark-mode-active;
			color: b.$type-3_color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-3_color--dark-mode-disabled !important;
			background-color: b.$type-3_background-color--dark-mode-disabled !important;
			border-color: b.$type-3_border-color--dark-mode-disabled !important;
		}
	}

	.btn-type-4 {
		color: b.$type-4_color--dark-mode;
		background-color: b.$type-4_background-color--dark-mode;
		border-color: b.$type-4_border-color--dark-mode;

		&:hover,
		&:focus {
			background-color: b.$type-4_background-color--dark-mode-hover;
			border-color: b.$type-4_border-color--dark-mode-hover;
			color: b.$type-4_color--dark-mode-hover;
		}

		&:active,
		&:active:focus {
			background-color: b.$type-4_background-color--dark-mode-active;
			border-color: b.$type-4_border-color--dark-mode-active;
			color: b.$type-4_color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-4_color--dark-mode-disabled !important;
			background-color: b.$type-4_background-color--dark-mode-disabled !important;
			border-color: b.$type-4_border-color--dark-mode-disabled !important;
		}
	}

	.btn-type-5,
	.btn-link {
		background-color: b.$type-5_background-color;
		border: 0;
		border-color: transparent;
		border-bottom:
			b.$border--bottom_thickness solid
			b.$type-5_border-color--dark-mode;
		border-radius: 0;
		padding-left: 0;
		padding-right: 0;
		color: b.$type-5_color--dark-mode;

		&:visited {
			border: 0;
			color: b.$type-5_color--dark-mode;
		}

		&:hover,
		&:focus {
			border: 0;
			border-bottom:
				b.$border--bottom_thickness solid
				b.$type-5_border-color--dark-mode-hover;
			background-color: b.$type-5_background-color;
			color: b.$type-5_color--dark-mode-hover;
		}

		&:focus {
			border: 0;
			border-bottom:
				b.$border--bottom_thickness solid
				b.$type-5_border-color--dark-mode-focus;
			background-color: b.$type-5_background-color;
			color: b.$type-5_color--dark-mode-focus;
			outline: none;
		}

		&:active,
		&:active:focus {
			border: 0;
			border-bottom:
				b.$border--bottom_thickness solid
				b.$type-5_border-color--dark-mode-active;
			background-color: b.$type-5_background-color;
			color: b.$type-5_color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-5_color--disabled !important;
			background-color: b.$type-5_background-color !important;
			border-color: b.$type-5_border-color--disabled !important;
		}
	}

	.btn-type-6 {
		background-color: b.$type-6_background-color;
		border: 0;
		border-color: transparent;
		border-bottom:
			b.$border--bottom_thickness solid
			b.$type-6_border-color--dark-mode;
		border-radius: 0;
		padding-left: 0;
		padding-right: 0;
		color: b.$type-6_color--dark-mode;

		&:visited {
			border: 0;
			color: b.$type-6_color--dark-mode;
		}

		&:hover,
		&:focus {
			border: 0;
			border-bottom:
				b.$border--bottom_thickness solid
				b.$type-6_border-color--dark-mode-hover;
			background-color: b.$type-6_background-color;
			color: b.$type-6_color--dark-mode-hover;
		}

		&:focus {
			border: 0;
			border-bottom:
				b.$border--bottom_thickness solid
				b.$type-6_border-color--dark-mode-focus;
			background-color: b.$type-6_background-color;
			color: b.$type-6_color--dark-mode-focus;
			outline: none;
		}

		&:active,
		&:active:focus {
			border: 0;
			border-bottom:
				b.$border--bottom_thickness solid
				b.$type-6_border-color--dark-mode-active;
			background-color: b.$type-6_background-color;
			color: b.$type-6_color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-6_color--disabled !important;
			background-color: b.$type-6_background-color !important;
			border-color: b.$type-6_border-color--disabled !important;
		}
	}

	.btn-type-7 {
		background-color: b.$type-7_background-color--dark-mode;
		border: b.$border--full_thickness solid b.$type-7_border-color--dark-mode;
		padding-left: 8px;
		padding-right: 8px;
		color: b.$type-7_color--dark-mode;
		outline: b.$outlin-size solid transparent;

		&:visited {
			color: b.$type-7_color--dark-mode;
		}

		&:hover,
		&:focus {
			background-color: b.$type-7_background-color--dark-mode-hover;
			color: b.$type-7_color--dark-mode-hover;
		}

		&:focus {
			outline-color: c.$base-300;
		}

		&:active,
		&:active:focus {
			background-color: b.$type-7_background-color--dark-mode-hover;
			color: b.$type-7_color--dark-mode-active;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-7_color--dark-mode-disabled !important;
			background-color: b.$type-7_background-color--dark-mode-disabled !important;
			border-color: b.$type-7_border-color--dark-mode-disabled !important;
		}
	}
}

.module-button-container {
	margin: -.5rem;
	padding-top: .5rem;
	padding-bottom: .5rem;
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.module-button-wrapper {
		padding: .5rem;

		a.btn.cta-primary,
		a.btn.cta-secondary {
			margin: 0;
		}
	}

	&.full-width-button-container {
		margin: -.5rem 0;

		@media (min-width: g.$breakpoint-m) {
			margin: -.5rem;
		}

		.module-button-wrapper {
			width: 100%;
			padding: .5rem 0;

			@media (min-width: g.$breakpoint-m) {
				width: auto;
				padding: .5rem;
			}
		}

		a.btn.full-width-button.cta-primary,
		a.btn.full-width-button.cta-secondary {
			margin: 0;
		}
	}
}

.content.center .module-button-container {
	justify-content: center;
}

