// Gutenberg styles. Should be converted to a more scalable approach. See the comment in the top of the document.
.wp-block-buttons {
	&.stack-on-mobile {
		flex-direction: column;

		@media (min-width: g.$breakpoint-m) {
			flex-direction: row;
		}
	}
}

.wp-block-button.is-style-secondary .wp-block-button__link {
	@extend .btn-type-2;
}

.wp-block-button.is-style-primary-outline .wp-block-button__link {
	@extend .btn-type-3;
}

.wp-block-button.is-style-secondary-outline .wp-block-button__link {
	@extend .btn-type-4;
}

.wp-block-button.is-style-primary-underline {
	width: max-content;

	.wp-block-button__link {
		@extend .btn-type-5;
	}
}

.wp-block-button.is-style-secondary-underline {
	width: max-content;

	.wp-block-button__link {
		@extend .btn-type-6;
	}
}

// gutenberg related a tag fixes for hover and focus states
a.wp-block-button__link {
	outline-offset: 0;
}

a.wp-block-button__link,
a.btn-type-2,
a.btn-type-3,
a.btn-type-4,
a.btn-type-7,
a.disabled {
	outline-color: transparent;

	&:focus {
		outline-color: b.$outlin-color-2;
	}
}

// this is needed to prevent outline of the entire link. design only has a bottom line
a.btn-type-5,
a.btn-type-6 {
	outline-color: transparent;

	&:focus {
		outline-color: transparent;
	}
}

// ghost buttons have a different outline colour
a.btn-type-7 {
	&:focus {
		outline-color: b.$outlin-color;
	}
}

.wp-block-button.disabled .wp-block-button__link {
	&:focus {
		outline: none !important;
	}
}

.wp-block-button.is-style-ghost {
	width: max-content;

	.wp-block-button__link {
		@extend .btn-type-7;
	}
}

/* stylelint-disable selector-max-id */
// WooCommerce Buttons - Pressed - Icon and Spacing
.woocommerce .site .site-content #respond input#submit.loading,
.woocommerce .site .site-content a.button.loading,
.woocommerce .site .site-content button.button.loading,
.woocommerce .site .site-content input.button.loading {
	padding-right: tool.calculate-rem(42);

	&:after {
		content: " ";
		display: block;
		position: absolute;
		margin: auto 0;
		top: 0;
		bottom: 0;
		right: tool.calculate-rem(8);
		width: tool.calculate-rem(20);
		height: tool.calculate-rem(20);
		border-radius: 50%;
		border: 2px solid c.$white;
		border-color: c.$white transparent;
		background-color: transparent;
		mask-image: unset;
		transition: unset;
		animation: qala-dual-ring 1.2s linear infinite;
	}

	&.single_add_to_cart_button:after {
		right: tool.calculate-rem(15);
	}
}

@keyframes qala-dual-ring {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

// fix menu link text color flashing on hover
.menu-item-type-post_type.btn {
	transition: all .4s ease, color 0s;
}
