@use "Settings/colors"as c;
@use "Settings/grid"as g;
@use "Settings/typography"as t;

@use "Components/navigation/header"as h;

@use "Tools"as tool;

$font: t.$font__heading !default;
$color: c.$white !default;
$background-color: c.$primary !default;
$height: 51px !default;
$height--desktop: 70px !default;

.search-open .search-toggle svg {
	fill: c.$primary;
}

.nav-search-container {
	position: relative;
	display: none;
	color: $color;
	background-color: $background-color;
	z-index: -1;
	height: $height;
	width: 100%;

	@media (min-width: g.$breakpoint-l) {
		height: $height--desktop;
	}

	.form-group {
		margin-bottom: 0;
	}

	.form-control {
		border: 0;
		line-height: 1.56;
		letter-spacing: t.$letter-spacing;
		@include tool.font-size( 18 );

		@media (min-width: g.$breakpoint-l) {
			line-height: 1.33;
			letter-spacing: t.$letter-spacing--large;
			@include tool.font-size( 24 );
		}

		&:focus {
			outline: none;
			box-shadow: none;
		}
	}

	.form-control,
	button {
		font-family: $font;
		height: $height;
		color: $color;
		background-color: $background-color;
		padding: 20px 0;

		@media (min-width: g.$breakpoint-l) {
			height: $height--desktop;
		}

		&::placeholder {
			color: $color;
		}
	}

	button {
		position: absolute;
		display: none; // remove search button
		right: 0;
		top: 0;
		border: 0;
	}
}
