@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

// svgs can not be filled with CSS variables, so need hex
$navigation__navigation-item__icon_color: c.$base-500 !default;
$navigation__navigation-item__icon_color--active: c.$primary !default;
$navigation__navigation-item__icon_color__hex: #8f8f8f !default;
$navigation__navigation-item__icon_color__hex--active: #0070f0 !default;
$navigation__navigation-item_border-color: c.$base-500 !default;

.woocommerce-account .woocommerce {
	.woocommerce-MyAccount-navigation {
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			width: 23%;
		}

		ul {
			display: flex;
			flex-direction: column;
			padding-left: 0;

			@media (min-width: g.$breakpoint-m) {
				display: inherit;
			}
		}

		.open li {
			display: flex;
		}

		.open .is-active {
			border-bottom: 0;
		}

		.open .is-active:after {
			transform: rotate(180deg);
			margin-top: -2px;

			@media (min-width: g.$breakpoint-m) {
				transform: none;
				margin-top: 3px;
			}
		}
	}

	.woocommerce-MyAccount-content {
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			width: 67%;
		}

		h2 {
			@include tool.font-size(20);
		}

		mark {
			background-color: transparent;
			font-weight: 500;
		}
	}

	.woocommerce-MyAccount-navigation-link {
		border-bottom: 1px dotted $navigation__navigation-item_border-color;
		margin-bottom: 0;
		padding: tool.calculate-rem(18) 0;
		order: 1;
		display: none;

		@media (min-width: g.$breakpoint-m) {
			display: flex;
			padding: tool.calculate-rem(15) 0;
		}

		&:first-child {
			border-top: 1px dotted $navigation__navigation-item_border-color;
		}

		&:before {
			content: none;
		}

		&:after {
			height: 100%;
		}

		a {
			color: $navigation__navigation-item__icon_color;
			width: 100%;
			word-break: break-word;

			@media (min-width: g.$breakpoint-m) {
				@include tool.font-size(16);
			}

			&:hover,
			&:focus {
				color: $navigation__navigation-item__icon_color--active;
			}
		}
	}

	.woocommerce-MyAccount-navigation-link.is-active {
		display: flex;
		order: 0;
		border-top: 1px dotted #8f8f8f;

		@media (min-width: g.$breakpoint-m) {
			border-top: 0;

			&:after {
				display: none;
			}
		}

		&:after {
			margin-top: 3px;
