	.woocommerce-MyAccount-navigation-link--customer-logout {
		&:before {
			content: url("data:image/svg+xml; utf8, <svg xmlns='http://www.w3.org/2000/svg' width='20' height='20' viewBox='0 0 23 16'><path d='M18.002 12.053L22.054 8l-4.052-4.053M21.653 8H7.75m6.5 2.69v3.81h-13v-13h13v3.88' fill='none' fill-rule='evenodd' stroke='#{ tool.encodecolor( $navigation__navigation-item__icon_color__hex) }' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5'/></svg>");
