	.woocommerce-MyAccount-content h3 {
		margin-top: 0;
	}

	.woocommerce-Address {
		width: 100%;
		border: solid 2px $navigation__navigation-item_border-color;
		padding: tool.calculate-rem(23);
		margin-bottom: tool.calculate-rem(21);

		@media (min-width: g.$breakpoint-m) {
			width: 48%;
		}

		h3 {
			margin-top: 0;
		}

		address {
			font-style: normal;
		}
	}

	.woocommerce-address-fields__field-wrapper {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.woocommerce-address-fields {
		.form-row {
			width: 100%;

			@media (min-width: g.$breakpoint-m) {
				width: 48%;
				float: none;
			}
		}

		/* stylelint-disable */
		#billing_address_2_field,
		#shipping_address_2_field {

			@media (min-width: g.$breakpoint-m) {
				display: flex;
				align-items: flex-end;

				.woocommerce-input-wrapper {
					width: 100%;
				}
			}
		}

		/* stylelint-enable */
	}

	.woocommerce-EditAccountForm.edit-account {
		.woocommerce-form-row {
			width: 100%;

			@media (min-width: g.$breakpoint-m) {
				width: 48%;
			}
		}

		label {
			font-weight: 500;
			@include tool.font-size(12);
		}

		span em {
			font-style: normal;
			color: c.$base-500;
			margin-top: tool.calculate-rem(15);
			@include tool.font-size(12);
		}

		fieldset {
			border: 0;
			padding: 0;
		}

		fieldset legend {
			padding-top: tool.calculate-rem(10);
			@include tool.font-size(15);
		}
	}

	.woocommerce-orders-table__cell-order-actions .button.view {
		border: 0;
		background-color: transparent;
		color: c.$primary;
		font-weight: 300;
		padding: 0;
		text-transform: none;
		text-decoration: underline;
	}

	table.my_account_orders {
		border: 0;

		@media (min-width: g.$breakpoint-s) {
			border: solid 1px #d6dee9;
			border-radius: 4px;
		}
	}

	table.my_account_orders thead {
		display: none;

		@media (min-width: g.$breakpoint-s) {
			display: table-header-group;
		}
	}

	table.my_account_orders tr {
		display: block;
		padding: 1rem;
		@include tool.font-size(13);

		@media (min-width: g.$breakpoint-s) {
			display: table-row;
			@include tool.font-size(14);
		}

		&:not(:last-of-type) {
			margin-bottom: 1rem;

			@media (min-width: g.$breakpoint-s) {
				margin-bottom: 0;
			}
		}
	}

	table.my_account_orders td {
		padding: .1rem;
		text-align: right;
		letter-spacing: t.$letter-spacing;

		@media (min-width: g.$breakpoint-s) {
			padding: 1rem .5rem;
			text-align: left;
		}

		&:before {
			content: attr(data-title);
			font-weight: 500;
			float: left;
			@include tool.font-size(12);

			@media (min-width: g.$breakpoint-s) {
				content: none;
			}
		}
	}

	.woocommerce-orders-table__row {
		border: solid 1px c.$primary-variant--botticelli;
		border-radius: 4px;

		@media (min-width: g.$breakpoint-s) {
			border: 0;
		}
	}

	th.woocommerce-orders-table__header {
		font-weight: 400;
		margin-top: 20px;
		@include tool.font-size(12);

		@media (min-width: g.$breakpoint-s) {
			height: 50px;
		}
	}

	.woocommerce-orders-table__cell {
		display: block;
		width: 100%;
		font-weight: 300;
		border: 0;

		@media (min-width: g.$breakpoint-s) {
			border-top: 1px solid c.$primary-variant--botticelli;
			display: table-cell;
			width: auto;
		}
	}

	.woocommerce-Message.woocommerce-Message--info {
		background-color: transparent;
		border: 1px solid c.$primary-variant--botticelli;
	}

	.woocommerce-password-strength {
		color: c.$base-500;
		background-color: transparent;
		padding-left: 0;
		text-align: left;
		font-weight: 500;
		@include tool.font-size(12);
	}

	.woocommerce-password-hint {
		@include tool.font-size(12);
	}

	address {
		font-weight: 300;
		letter-spacing: t.$letter-spacing;
	}

	.shop_table.order_details {
		th {
			font-weight: 400;
		}

		td,
		td .product-quantity,
		small {
			font-weight: 300;
		}

		tfoot th {
			font-weight: 300;
		}
	}
}

.woocommerce-account .entry-title {
	@include tool.font-size(18);

	@media (min-width: g.$breakpoint-m) {
		@include tool.font-size(20);
	}
}

.woocommerce-edit-address .woocommerce-MyAccount-content {
	p {
		@include tool.font-size(16);
	}

	h3 {
		font-weight: 500;
		@include tool.font-size(17);
	}

	.woocommerce-Address {
		border: 1px solid c.$primary-variant--botticelli;
	}
}

.woocommerce-edit-account .woocommerce-MyAccount-content {
	.woocommerce-Button {
		margin-left: 5px;
	}
}

.woocommerce-account .woocommerce .u-columns.col2-set {
	.col-1.woocommerce-Address {
		border: 1px solid $navigation__navigation-item_border-color;
	}

	.u-column2.col-2.woocommerce-Address {
		border: 1px solid $navigation__navigation-item_border-color;
		padding: tool.calculate-rem(23);
	}
}
