/**
 * This is a CSS hack which makes iframes responsive.
 * The padding-top is calculated based on the ratio of the iframe.
 * For example videos: 9 / 16.
 *
 */

.responsive-oembed {
	position: relative;
	overflow: hidden;
	padding-top: 56.25%;

	&.embed-responsive-16by9 {
		padding-top: 56.25%;
	}

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border: 0;
	}
}
