@use "Settings/grid"as g;

@use "Tools"as tool;

.qala-newsletter-pattern {
	.mobile-align-center {

		@media (max-width: g.$breakpoint-m) {
			text-align: center;
		}
	}

	.mc4wp-form .mc4wp-form-fields p {
		&:last-child {
			margin-bottom: 0;
		}
	}

	.icon-is-white {
		img {
			filter: brightness(0) invert(1);
		}
	}

	.wp-block-columns {
		margin-bottom: unset;
	}

	h3 {
		margin-bottom: .5em;
	}

	&.alignfull {
		.wp-block-cover__inner-container > .alignwide {
			padding-left: 30px;
			padding-right: 30px;
		}
	}
}
