@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

@use "Components/pages/woocommerce/checkout" as ch;

// =======================
// ====== CART ITEM ======
// =======================

// Thumbnail
$cart-item__thumbnail_max-width: 83px !default;
$cart-item__thumbnail_border-thickness: 1px !default;
$cart-item__thumbnail_border-color: c.$base-100 !default;
$cart-item__thumbnail_border: $cart-item__thumbnail_border-thickness solid $cart-item__thumbnail_border-color !default;

// Details
$cart-item__details_padding-top: .15rem !default;
$cart-item__details_padding-right: 0 !default;
$cart-item__details_padding-bottom: 0 !default;
$cart-item__details_padding-left: 1.5625rem !default;
$cart-item__details_padding: $cart-item__details_padding-top $cart-item__details_padding-right $cart-item__details_padding-bottom $cart-item__details_padding-left !default;
$cart-item__details__name_text-transform: none !default;
$cart-item__details__name_color: c.$base-900 !default;
$cart-item__details__name_font-size: 13 !default;
$cart-item__details__price_font-weight: 700 !default;

// ====================
// ====== FOOTER ======
// ====================
$foot_border-color: $cart-item__thumbnail_border-color !default;
$foot_font-family: t.$font__heading !default;

// Subtotal
$foot__subtotal_color: c.$base-500 !default;

// Total
$foot__total_font-size: 15 !default;
$foot__total_font-size--desktop: 16 !default;
$foot__total_color: c.$base-900 !default;
$foot__total_text-transform: none !default;
$foot__total_font-family: t.$font__content !default;
$foot__total_padding-bottom: 0 !default;
$foot__total_line-height: 1.6 !default;
$foot__total_line-height--desktop: 1.53 !default;
$foot__total_letter-spacing: t.$letter-spacing !default;
$foot__total_letter-spacing--desktop: t.$letter-spacing !default;

#order_review_heading { // stylelint-disable-line selector-max-id, selector-id-pattern
	display: none;
}

.woocommerce-checkout-review-order-wrapper {
	order: 1;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	flex-basis: 100%;

	@media (min-width: g.$breakpoint-m) {
		display: block;
		order: 0;
	}
}

.order-review-heading {
	order: 4;
	width: 100%;
	flex-basis: 100%;
	padding-top: g.$gutters;
	border-top: ch.$billing-fields_border;

	@media (min-width: g.$breakpoint-m) {
		order: 0;
		padding-top: 0;
		border-top: 0;
	}
}

.change-cart-button-wrap {
	display: flex;
	justify-content: flex-end;

	.order-review-head__change-cart-button {
		background: transparent;
		padding: 10px;
		border: unset;
		color: #000;
		text-transform: unset;
		font-weight: 300;
		@include tool.font-size(13);

		&:hover,
		&:focus {
			background: transparent;
			text-decoration: underline;
			border: unset;
		}
	}
}

.usps {
	order: 1;
	width: 100%;
	flex-basis: 100%;

	@media (min-width: g.$breakpoint-m) {
		order: 0;
	}
}

.woocommerce-checkout-review-order {
	width: 100%;
	border: 0;
	padding: 0;
	order: 5;

	@media (min-width: g.$breakpoint-m) {
		padding: g.$gutters;
		border: 1px solid $foot_border-color;
		order: 0;
	}
}

body.woocommerce-checkout .woocommerce table.shop_table {
	margin-bottom: g.$gutters;
}

.woocommerce .woocommerce-checkout-review-order-table {
	thead,
	td.product-total {
		display: none;
	}

	tbody tr:first-child td {
		padding-top: 0;
	}

	tbody tr td {
		padding: 10px 0;
	}

	tr.cart_item {
		display: none;

		&.custom-order-review {
			display: table-row;
		}
	}

	tr.cart_item td {
		@extend .meta;

		text-transform: none;
		width: 100%;
		border: 0;
		@include tool.font-size(13);

		td {
			border-top: 0;
		}
	}

	.product-review-item {
		display: flex;
		align-items: flex-start;
	}

	.product-thumbnail img {
		max-width: $cart-item__thumbnail_max-width;
		border: $cart-item__thumbnail_border;
	}

	.product-details {
		padding: $cart-item__details_padding;
	}

	.product-name-link a {
		@extend .meta;

		text-transform: $cart-item__details__name_text-transform;
		color: $cart-item__details__name_color;
		@include tool.font-size($cart-item__details__name_font-size);
	}

	.product-price {
		font-weight: $cart-item__details__price_font-weight;
	}
}

.woocommerce table.shop_table .cart-subtotal,
.woocommerce table.shop_table .woocommerce-shipping-totals,
.woocommerce table.shop_table .order-total,
.woocommerce table.shop_table .tax-rate,
.woocommerce table.shop_table .fee {
	color: $foot__subtotal_color;
	font-family: $foot_font-family;

	ul li {
		@extend .meta;

		font-weight: 700;
		padding: 0;
		margin: 0;

		&:before {
			display: none;
		}
	}

	p,
	a {
		@include tool.font-size( 15 );
	}

	th,
	td {
		@extend .meta;

		font-weight: 700;
		padding: 5px 0;
		border-top: 0;
	}

	th {
		text-align: left;
	}

	td {
		text-align: right;
	}
}

.woocommerce table.shop_table .woocommerce-shipping-totals {
	th,
	td {
		padding-bottom: 1rem;
	}
}

.woocommerce table.shop_table .tax-rate {
	th,
	td {
		padding-top: 1rem;
		border-top: 1px solid $foot_border-color;
	}
}

.woocommerce table.shop_table .cart-subtotal th,
.woocommerce table.shop_table .cart-subtotal td {
	padding-top: g.$gutters;
}

.woocommerce table.shop_table .order-total {
	color: $foot__total_color;

	td,
	th {
		text-transform: $foot__total_text-transform;
		font-family: $foot__total_font-family;
		padding-bottom: $foot__total_padding-bottom;
		line-height: $foot__total_line-height;
		letter-spacing: $foot__total_letter-spacing;
		@include tool.font-size($foot__total_font-size);

		@media (min-width: g.$breakpoint-m) {
			line-height: $foot__total_line-height--desktop;
			letter-spacing: $foot__total_letter-spacing--desktop;
			@include tool.font-size($foot__total_font-size--desktop);
		}
	}
}

.woocommerce table.shop_table .change-cart-button-row {
	td {
		border: unset;
	}
}

/* stylelint-disable */
body .woocommerce ul#shipping_method li {
	margin: 0;
}

/* stylelint-enable */
body .woocommerce .cart-collaterals .cart_totals,
.woocommerce-page .cart-collaterals .cart_totals {
	width: 100%;
	max-width: tool.calculate-rem(540);
}

/* Don't know if I should create a new file for overall reviews or not */
.wc-block-components-sort-select {
	margin-bottom: tool.calculate-rem(12);
}

.wc-block-components-review-sort-select {
	text-align: right;
}

.wc-block-components-sort-select__label {
	margin-bottom: tool.calculate-rem(4);
	display: inline-block;
	font-weight: 400;
}

.wc-block-components-review-list.wc-block-components-review-list {
	margin: 0;
	padding-left: tool.calculate-rem(20);
}

.wc-block-components-review-list-item__item {
	margin: 0 0 tool.calculate-rem(48);
	list-style: none;
}

.wc-block-components-review-list-item__info {
	display: grid;
	grid-template-columns: 1fr;
	margin-bottom: tool.calculate-rem(24);
}

.wc-block-components-review-list-item__meta {
	display: flex;
	align-items: center;
	flex-flow: row wrap;
	grid-row: 1;

	&:after {
		order: 3;
		content: "";
		flex-basis: 100%;
	}
}

.wc-block-components-review-list-item__item--has-image {
	.wc-block-components-review-list-item__info {
		grid-template-columns: calc(tool.calculate-rem(48) + tool.calculate-rem(16)) 1fr;
	}

	.wc-block-components-review-list-item__meta {
		grid-column: 2;
	}
}

.wc-block-components-review-list-item__image {
	align-items: center;
	display: flex;
	grid-column: 1;
	grid-row: 1/3;
	justify-content: center;
	position: relative;
	height: tool.calculate-rem(48);
	width: tool.calculate-rem(48);

	> img {
		display: block;
		max-height: 100%;
		object-fit: contain;
	}
}

.wc-block-components-review-list-item__rating {
	order: 2;

	> .wc-block-components-review-list-item__rating__stars {
		font-size: tool.calculate-rem(16);
		display: block;
		top: 0;
		overflow: hidden;
		position: relative;
		height: tool.calculate-rem(16);
		width: tool.calculate-rem(70);
		line-height: 1;
		font-family: star; // stylelint-disable-line font-family-no-missing-generic-family-keyword
		font-weight: 400;

		&:before {
			content: "";
			width: tool.calculate-rem(70);
			height: tool.calculate-rem(14);
			float: left;
			top: 0;
			left: 0;
			position: absolute;
			background-image: url("../../../qala-theme/assets/images/icons/action/star-empty.svg");
			background-size: tool.calculate-rem(14);
			background-repeat-x: repeat;
		}

		span {
			overflow: hidden;
			float: left;
			top: 0;
			left: 0;
			position: absolute;
			padding-top: tool.calculate-rem(24);
		}

		span:before {
			content: "";
			width: 100%;
			height: tool.calculate-rem(14);
			top: 0;
			left: 0;
			position: absolute;
			background-image: url("../../../qala-theme/assets/images/icons/action/star-filled.svg");
			background-size: tool.calculate-rem(14);
			background-repeat-x: repeat;
		}
	}
}

.wc-block-components-review-list-item__author,
.wc-block-components-review-list-item__product {
	display: block;
	font-weight: 700;
	order: 1;
	margin-right: tool.calculate-rem(8);
}

.wc-block-components-review-list-item__product + .wc-block-components-review-list-item__author {
	font-weight: 400;
	order: 4;
}

.wc-block-components-review-list-item__published-date {
	order: 5;
}

.wc-block-components-review-list-item__product + .wc-block-components-review-list-item__author + .wc-block-components-review-list-item__published-date {
	padding-left: tool.calculate-rem(8);
	position: relative;

	&:before {
		content: "";
		display: inline-block;
		margin-left: tool.calculate-rem(-8);
		border-right: 1px solid;
		opacity: .5;
		height: tool.calculate-rem(16);
		vertical-align: middle;
		position: absolute;
		top: calc(50% + .1em);
		transform: translateY(-50%);
	}
}
