@use "sass:color" as *;

// Primary colors
$primary: var(--wp--preset--color--primary-500) !default;
$primary-100: var(--wp--preset--color--primary-100) !default;
$primary-200: var(--wp--preset--color--primary-200) !default;
$primary-300: var(--wp--preset--color--primary-300) !default;
$primary-400: var(--wp--preset--color--primary-400) !default;
$primary-500: var(--wp--preset--color--primary-500) !default;
$primary-600: var(--wp--preset--color--primary-600) !default;

// Secondary colors
$secondary: var(--wp--preset--color--secondary-500) !default;
$secondary-100: var(--wp--preset--color--secondary-100) !default;
$secondary-200: var(--wp--preset--color--secondary-200) !default;
$secondary-300: var(--wp--preset--color--secondary-300) !default;
$secondary-400: var(--wp--preset--color--secondary-400) !default;
$secondary-500: var(--wp--preset--color--secondary-500) !default;
$secondary-600: var(--wp--preset--color--secondary-600) !default;

// Gray shades
$white: var(--wp--preset--color--white) !default;
$base-100: var(--wp--preset--color--base-100) !default;
$base-200: var(--wp--preset--color--base-200) !default;
$base-300: var(--wp--preset--color--base-300) !default;
$base-400: var(--wp--preset--color--base-400) !default;
$base-500: var(--wp--preset--color--base-500) !default;
$base-600: var(--wp--preset--color--base-600) !default;
$base-700: var(--wp--preset--color--base-700) !default;
$base-800: var(--wp--preset--color--base-800) !default;
$base-900: var(--wp--preset--color--base-900) !default;
$base-1000: var(--wp--preset--color--base-1000) !default;
$black: var(--wp--preset--color--black) !default;

// Action colors
$success: #77a464 !default;
$success-300: #0cd6aa !default;
$alert: #ec1322 !default;
$error-lighter: #feecee !default; // Used as error notice background
$error-text-color: #1c1c1e !default;
$error-darker: #f68e95 !default;
$alerts-success-48: #91cba8 !default;
$info-notice-background: #ebf4fe !default;
$info-notice-border: #85b9f5 !default;
$info-notice-text-color: #1c1c1e !default;
$success-notice-background: #edf7f1 !default;
$success-notice-border: #91cba8 !default;
$success-notice-text-color: #1c1c1e !default;

// =============
// Lightness for colors
// Example of usage: .class { background-color: adjust( $primary-500, $lightness: $light-8 ) }
// Documentation https://sass-lang.com/documentation/modules/color
// $strength-48: 48%;

// Preset variable with lightness
// example: $primary__light-40: adjust( $primary-500, $lightness: $strength-40 );

// $primary__dark-48: adjust($primary-500, $blackness: $strength-48);
$primary__dark-48: #003776 !default;

// =============
// Opacity

// TODO: Better names, suggestion in Qala color stylesheet
$primary-500-opacity-8: rgb(0 112 240 / 8%) !default;
$primary-500-opacity-16: rgb(0 112 240 / 16%) !default;
$primary-500-opacity-32: rgb(0 112 240 / 32%) !default;
$primary-500-opacity-48: rgb(0 112 240 / 48%) !default;
$base-500-opacity-48: rgb(199 199 204 / 48%) !default;
$white-opacity-12: rgb(255 255 255 / 12%) !default;
$white-opacity-48: rgb(255 255 255 / 48%) !default;
$success-opacity-48: rgb(66 190 101 / 48%) !default;

// =============
// COLOR VARIATIONS
// Colors derived from the primary color could instead be adjusted like the below example.
// This makes changing the color scheme much easier,
// if the approach makes sense in relation to project processes.
// This site can be used to understand the conversion: https://razorltd.github.io/sasscolourfunctioncalculator/
// =============

// $primary-variant--chambray: adjust($primary, $hue: 5, $saturation: -4.62%, $lightness: -19.41%) !default;
$primary-variant--chambray: #03378a !default;

// $primary-variant--midnight: adjust($primary, $hue: -4, $saturation: 47.57%, $lightness: -42.55%) !default;
$primary-variant--midnight: #000c17 !default;

// $primary-variant--botticelli: adjust($primary, $hue: 1, $saturation: -15.37%, $lightness: 33.73%) !default;
$primary-variant--botticelli: #a5caf7 !default;

// todo: Not really sure how to approach the below options in the most optimal way
$light-scheme__background-color: $white !default;
$light-scheme__text-color: $black !default;
$light-scheme__border-color: $base-100 !default;
$dark-scheme__background-color: $primary-variant--midnight !default;
$dark-scheme__text-color: $white !default;
$dark-scheme__border-color: $primary-variant--chambray !default;
$dark-scheme__border-color-btn-hover: $primary-500 !default;
$dark-scheme__button-color: $white !default;
