@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography" as t;

@use "Tools"as tool;

@use "Components/headings"as *;

$background: c.$base-100 !default;
$text-color: c.$base-900 !default;

.product-cat-additional-content {
	background-color: $background;
	padding-top: g.$gutters * .5;
	padding-bottom: g.$gutters * .5;

	@media (min-width: g.$breakpoint-m) {
		padding-top: 85px;
		padding-bottom: 85px;
	}

	.additional-content-heading {
		font-weight: bold;
		line-height: 1.53;
		letter-spacing: t.$letter-spacing;
		margin-top: 0;
		@include tool.font-size(17);

		&.additional-content-heading-links {
			@extend .section-heading;
		}
	}

	.column-2 p {
		columns: 200px 2;

		@media (min-width: g.$breakpoint-s) {
			column-gap: 65px;
		}
	}

	.additional-content-text {
		font-weight: 300;
		line-height: 1.73;
		letter-spacing: t.$letter-spacing;
		color: $text-color;

		@include tool.font-size(15);
	}

	.additional-content-links {
		ul {
			list-style: none;
			margin: 0;
			padding-left: 2px;
			padding-top: 12px;
			columns: 170px 6;
		}

		li {
			padding-right: .875em;
			padding-bottom: 1px;
			break-inside: avoid;
			page-break-inside: avoid;
		}

		li:nth-child(8n) {
			break-after: always;
		}

		a {
			line-height: 1.73;
			letter-spacing: t.$letter-spacing;
			text-decoration: underline;

			@include tool.font-size(15);
		}
	}

	.highlighted-item-heading {
		font-weight: 700;
		line-height: 1.53;
		letter-spacing: t.$letter-spacing;
		color: $text-color;
		display: block;
		margin-top: g.$gutters * .5;
		text-decoration: underline;

		@include tool.font-size(17);
	}
}
