@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Elements" as *;

@use "Tools"as tool;

$page-404_max-width: calculate-rem(545) !default;
$page-404__page-title_color: c.$primary-variant--chambray !default;

.error-404 {
	max-width: $page-404_max-width;
	margin-left: auto;
	margin-right: auto;
	padding: g.$gutters g.$gutters g.$gutters * 2;
	text-align: center;

	@media (min-width: g.$breakpoint-m) {
		padding: g.$gutters * 2 0;
	}

	.page-title {
		@extend .h3; // The heading itself is h1, but following h3 styling here for design purposes.
	}

	.page-text {

		@media (min-width: g.$breakpoint-m) {
			margin-bottom: g.$gutters * 2;
		}
	}

	.btn-404 {
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			width: auto;
		}
	}
}

.image-404 {
	margin-bottom: g.$gutters * 1.5;

	svg {
		margin-top: tool.calculate-rem(16);
		width: tool.calculate-rem(543);
		max-width: 100%;

		@media (min-width: g.$breakpoint-m) {
			margin-top: 0;
			height: tool.calculate-rem(310);
		}
	}
}
