@use "Settings/colors" as c;
@use "Settings/grid"as g;
@use "Settings/buttons"as b;
@use "Settings/typography"as t;
@use "Tools"as tool;

@use "Components/pages/woocommerce/product" as p;

.summary-upsells {
	width: 100%;
	padding: g.$gutters * .5 p.$product_wrap_inner_elements_horizontal_padding_mobile;

	@media (min-width: g.$breakpoint-m) {
		padding: 0;
	}

	> h4 {
		margin: 2rem 0 p.$product_wrap_inner_elements_horizontal_padding_mobile;
		font-weight: 600;
		@include tool.font-size(14);
	}
}

.woocommerce ul.upsells__products {
	display: flex;
	flex-flow: column wrap;
	margin: 0 0 p.$product_wrap_inner_elements_horizontal_padding_mobile;
	padding: p.$product_wrap_inner_elements_horizontal_padding_mobile 0;
	border-bottom: 1px solid c.$base-300;
	border-top: 1px solid;

	li.product--upsell {
		border-bottom: 1px solid c.$base-300;
		float: none;
		display: flex;
		align-items: center;
		flex-direction: unset;
		margin: 0 0 p.$product_wrap_inner_elements_horizontal_padding_mobile;
		padding: 0 0 p.$product_wrap_inner_elements_horizontal_padding_mobile;
		width: 100%;

		.product--upsell__image {
			flex: 0 0 50px;
			margin-right: 10px;
		}

		.product--upsell__info {
			flex-grow: 1;
			margin-right: 10px;
		}

		.product--upsell__title {
			padding: 0;
			font-weight: 400;
			@include tool.font-size(14);
		}

		.price {
			margin: 0;
			@include tool.font-size(14);
		}

		.button {
			@include tool.btn-type(3);

			padding: .5em 1em;
			margin-top: 0 !important; // stylelint-disable-line declaration-no-important
			width: auto;
		}

		&:last-child {
			margin-bottom: 0;
			padding-bottom: 0;
			border-bottom: 0;
		}
	}
}
