@use "Settings/grid" as g;
@use "Tools" as tools;

// Loop seperatly to make sure that these styles are seperate from the more importan styles.
@each $modifier,
	$breakpoint in g.$map-grid-breakpoints {
	// generate basic style for all columns
	@for $i from 1 through g.$nr_of_columns {
		.col#{ $modifier }-#{ $i } {
			width: 100%;
			padding: 0 #{ g.$gutters-smallscreen-horizontal } #{ g.$gutters-smallscreen-vertical };

			@media (min-width: g.$gutters-smallscreen-breakpoint) {
				padding: 0 #{ g.$gutters } #{ g.$gutters };
			}
		}
	}
}

@each $modifier,
	$breakpoint in g.$map-grid-breakpoints {
	// Generate breakpoint-speicif style for grid columns
	@include tools.create-col-classes($modifier, g.$nr_of_columns, $breakpoint);

	// Generate reverse-classes
	@include tools.create-mq($breakpoint) {
		.grid__reverse-order#{ $modifier } {
			flex-direction: row-reverse;
		}
	}
}

.container {
	max-width: g.$container_size;
	width: 100%;
	margin: 0 auto;
	@include tools.standard-horizontal-padding;

	&--fluid {
		width: 100%;
		max-width: g.$container_fluid_size;
		margin: 0 auto;
		@include tools.standard-horizontal-padding;
	}

	&--alignfull {
		max-width: none;
	}
}

.grid {
	display: flex;
	flex-wrap: wrap;
	margin: 0 #{ -(g.$gutters-smallscreen-horizontal) };
	align-items: center;

	@media (min-width: g.$gutters-smallscreen-breakpoint) {
		margin: 0 #{ -(g.$gutters) };
	}
}

// Helper classes for .grid
.grid__vert-align-top {
	align-items: flex-start;
}

.grid__no-bottom-gutters {
	>[class*="col-"] {
		padding-bottom: 0;
	}
}

.grid__center {
	justify-content: center;
}
