@use "Settings/grid"as g;
@use "Settings/spacing"as s;

@use "Tools" as tool;

/* stylelint-disable max-nesting-depth */
.square-grid-pattern {
	@include tool.standard-vertical-padding;

	&:not(.block-editor-block-list__block) {
		display: grid;
		gap: 1em;

		@media (min-width: g.$breakpoint-m) {
			padding-right: 2rem;
			padding-left: 2rem;
		}

		@media (min-width: g.$breakpoint-s) {
			display: grid;
			grid-template-columns: 1fr 1fr;
			gap: 2em;
		}

		h2 {

			@media (max-width: g.$breakpoint-xl - 1px) {
				font-size: 1.625rem;
			}

			@media (max-width: g.$breakpoint-m - 1px) {
				font-size: .8rem;
			}

			@media (max-width: 500px) {
				font-size: .6rem;
			}
		}

		.wp-block-cover {
			min-height: auto;
			height: max-content;
			display: flex;
			align-items: stretch;

			&:after {
				content: "";
				padding-bottom: 100%;
			}

			.wp-block-cover__inner-container {
				height: unset;

				@media (max-width: 400px) {
					align-items: flex-start;
				}
			}
		}

		.wp-block-cover__inner-container {
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			position: relative;

			.wp-block-buttons {
				position: absolute;
				bottom: 1rem;
			}
		}
	}

	&.block-editor-block-list__block {
		.wp-block-group__inner-container {
			display: flex;
			flex-wrap: wrap;
			gap: 2%;

			.wp-block-cover {
				flex: 1 0 49%;
				margin-top: 0;
			}

			.block-list-appender.wp-block {
				width: 100%;
				margin-left: auto;
				margin-right: auto;
			}

			.block-editor-rich-text__editable {
				width: 100%;
			}
		}
	}
}

/* stylelint-enable max-nesting-depth */
