@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

$quote_color: c.$primary-variant--botticelli !default;

.wp-block-qala-blocks-testimonials {
	max-width: g.$container_size_narrow;
	margin-left: auto;
	margin-right: auto;
}

.testimonials {
	@include tool.standard-vertical-padding;
}

.testimonial {
	padding: 5px 15px 15px;
}

.testimonial-inner {
	padding: 25px;
	box-shadow: 0 2px 10px 0 rgb(0 0 0 / 12%);
	color: c.$black;
}

.testimonial-quote {
	border-left: 0;
	position: relative;
	line-height: 25px;

	@media (min-width: g.$breakpoint-s) {
		margin: 2.6em 0 2.2em;
	}

	&:before {
		content: "”";
		font-weight: 500;
		color: $quote_color;
		position: absolute;
		top: -15px;
		@include tool.font-size(64);
	}

	p {
		letter-spacing: t.$letter-spacing;
		font-weight: 300;
		padding: 0;
		@include tool.font-size(15);
	}
}

.testimonial-person {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	padding-top: 1rem;
	border-top: 1px dotted rgb(0 0 0 / 12%);
	color: c.$base-500;
}

.testimonial-person-image {
	border-radius: 100px;
	margin-right: 1rem;

	img {
		height: 32px;
		width: 32px;
		border-radius: 50%;
	}
}

.testimonial-person-info {
	flex: 1;

	p {
		line-height: 1.54;
		margin: 0;
		font-weight: 500;
		@include tool.font-size(12);
	}
}
