@use "Settings/grid" as g;

@use "Tools" as tool;

.product-list-with-cta-pattern {
	padding-right: 1.5rem;
	padding-left: 1.5rem;
	@include tool.standard-vertical-padding;

	&.has-background {
		padding-right: 1.5rem;
		padding-left: 1.5rem;
	}

	.product-list-with-cta-pattern__heading {
		margin-top: 0;
		margin-bottom: 1.5rem;

		@media (min-width: g.$breakpoint-m) {
			margin-bottom: 3rem;
		}
	}
}
