@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;
@use "Settings/spacing" as s;

@use "Tools" as tool;

$loader_background_color: c.$white;
$loader_dot_color: c.$primary;

.elastic-search {
	margin: s.$standard-vertical-spacing--desktop 0;
	min-height: 240px;
	position: relative;

	@keyframes ellipsis1 {
		0% {
			transform: scale(0);
		}

		100% {
			transform: scale(1);
		}
	}

	@keyframes ellipsis3 {
		0% {
			transform: scale(1);
		}

		100% {
			transform: scale(0);
		}
	}

	@keyframes ellipsis2 {
		0% {
			transform: translate(0, 0);
		}

		100% {
			transform: translate(24px, 0);
		}
	}

	&__search-box {
		margin-bottom: 2rem;
	}

	&__loader {
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: $loader_background_color;
		display: flex;
		padding-top: 60px;
		justify-content: center;

		&.is-hidden {
			display: none;
		}
	}

	&__loader-ellipsis {
		display: inline-block;
		position: relative;
		width: 80px;
		height: 80px;
	}

	&__loader-ellipsis div {
		position: absolute;
		top: 33px;
		width: 13px;
		height: 13px;
		border-radius: 50%;
		background: $loader_dot_color;
		animation-timing-function: cubic-bezier(0, 1, 1, 0);
	}

	&__loader-ellipsis div:nth-child(1) {
		left: 8px;
		animation: ellipsis1 .6s infinite;
	}

	&__loader-ellipsis div:nth-child(2) {
		left: 8px;
		animation: ellipsis2 .6s infinite;
	}

	&__loader-ellipsis div:nth-child(3) {
		left: 32px;
		animation: ellipsis2 .6s infinite;
	}

	&__loader-ellipsis div:nth-child(4) {
		left: 56px;
		animation: ellipsis3 .6s infinite;
	}
}
