@use "Settings/colors"as c;
@use "Settings/grid"as g;
@use "Settings/typography"as t;
@use "Settings/z-index"as z;

@use "Tools"as tool;

$top-header__background: c.$base-100 !default;
$top-header__text: c.$base-900 !default;
$top-header__separator: c.$base-300 !default;
$top-header__link: c.$base-900 !default;
$top-header__link--hover: c.$primary !default;
$top-header-mobile__separator: c.$base-300 !default;
$top-header__mobile_background: #f5f5f5 !default;

.top-header {
	display: none;
	background: $top-header__background;

	@media (min-width: g.$breakpoint-l) {
		display: block;
		position: relative;
		z-index: z.$above-overlay;
	}

	.top-header-inner {
		display: flex;
		width: 100%;
		justify-content: space-between;
		flex-direction: row-reverse;
		align-items: center;
		padding-top: 10px;
		padding-bottom: 10px;
	}
}

.top-header-information {
	margin: 0;
	padding: 0;
	line-height: 1.46;
	letter-spacing: t.$letter-spacing;
	color: $top-header__text;
	@include tool.font-size(13);
}

.top-header-actions {
	margin: 0;
	padding: 0;
	display: flex;
	list-style: none;

	li {
		display: flex;
		align-items: center;

		&:before {
			content: "";
			width: 1px;
			height: 100%;
			background: $top-header__separator;
			margin-right: 22px;
			margin-left: 22px;
		}

		&:first-child:before {
			display: none;
		}
	}

	a,
	.qala-multimarket-open-selector-button {
		line-height: 1.46;
		letter-spacing: t.$letter-spacing;
		color: $top-header__text;
		padding: 2px 0;
		@include tool.font-size(13);
	}
}

// Mobile version
.top-header-mobile {
	list-style: none;
	padding: 0;
	margin: 0;
	background-color: $top-header__mobile_background;

	@media (min-width: g.$breakpoint-l) {
		display: none;
	}

	li {
		display: block;
		border-top: 1px solid $top-header-mobile__separator;
		padding: 0;

		&:last-child {
			border-bottom: 1px solid $top-header-mobile__separator;
		}
	}

	a,
	li > span,
	.qala-multimarket-open-selector-button,
	.cart-button {
		display: block;
		line-height: 1.46;
		letter-spacing: t.$letter-spacing;
		color: $top-header__link;
		padding: 1rem 2rem;
		@include tool.font-size(16);

		&:hover,
		&:focus {
			color: $top-header__link--hover;
		}
	}

	.qala-multimarket-open-selector-button {
		border: 0;
		background-color: transparent;
		display: flex;
		font-weight: 300;
		cursor: pointer;
	}

	.qala-multimarket-open-selector-button span {
		padding: 0;
		font-weight: normal;
		font-weight: 300;
	}

	.site-header-right-column__button {
		margin: 0;
		display: block;

		button {
			color: $top-header__link;
			font-weight: 400;

			&:hover,
			&:focus {
				color: $top-header__link--hover;
			}
		}

		.qala-wishlist-button,
		.cart-button {
			position: relative;
		}

		/* stylelint-disable max-nesting-depth */
		.cart-button {
			.count {
				left: 44px;
				top: 20px;
			}
		}

		.qala-wishlist-button {
			.count {
				top: 14px;
				left: 46px;
			}
		}

		/* stylelint-enable */
		svg {
			margin-right: 12px;
		}

		&:hover,
		&:active,
		&:active:focus,
		&:active:focus:hover,
		&:focus,
		&:focus-within,
		&:visited {
			border-top: 1px solid $top-header-mobile__separator;
		}
	}

	.qala-wishlist-button {
		&:hover,
		&:active,
		&:active:focus,
		&:focus {
			padding: 1rem 2rem;
		}
	}
}

.ac-nav-collapse.sub-menu-opened .top-header-mobile {
	display: none;
}
