@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

$padding: 0 !default;
$padding--desktop: 3.75em 0 !default;
$margin-bottom: .75em !default;
$margin-bottom--desktop: $margin-bottom !default;
$background-color: c.$primary-variant--botticelli !default;
$image-overlay_color: rgb(0 0 0 / 60%) !default;

// =====================
// ====== CONTENT ======
// =====================
$content_color: c.$primary-variant--chambray !default;
$content_color--with-hero-image: c.$white !default;
$content__heading_color: c.$primary-variant--midnight !default;
$content__heading_font-size: 18 !default;
$content__heading_font-size--desktop: 24 !default;
$content__toggle-accordion_color: c.$primary-variant--chambray !default;
$content__toggle-accordion_color--with-hero-image: c.$white !default;
$content__read-more_color: c.$primary !default;
$content__link_color: c.$primary !default;

.archive-hero {
	padding: $padding;
	margin-bottom: $margin-bottom;
	color: $content_color;
	background-color: $background-color;

	@media (min-width: g.$breakpoint-l) {
		padding: $padding--desktop;
		margin-bottom: $margin-bottom--desktop;
	}

	h1 {
		color: $content__heading_color;
		text-transform: none;
		margin-top: 0;
		@include tool.font-size( $content__heading_font-size );

		@media (min-width: g.$breakpoint-l) {
			@include tool.font-size( $content__heading_font-size--desktop );
		}
	}
}

.archive-link-list {
	display: flex;
	flex-wrap: wrap;
	max-width: 400px;

	@media (min-width: g.$breakpoint-l) {
		margin-left: auto;
	}

	a {
		display: inline-block;
		flex: 1 0 50%;
		max-width: 50%;
		color: $content__link_color;
		line-height: 1.86;
		letter-spacing: t.$letter-spacing;
		font-weight: normal;
		text-decoration: underline;
		@include tool.font-size( 13 );

		@media (min-width: g.$breakpoint-l) {
			@include tool.font-size( 15 );
		}
	}

	h4 {
		flex: 1 0 100%;
		margin-top: 1.2em;
		margin-bottom: 1.2em;
		color: $content__heading_color;
		font-weight: 700;
		line-height: 1.6;
		letter-spacing: t.$letter-spacing--large;
		font-family: t.$font__content;
		text-transform: uppercase;
		@include tool.font-size( 11 );

		@media (min-width: g.$breakpoint-l) {
			@include tool.font-size( 13 );
		}
	}
}

.headings-container {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: g.$gutters-smallscreen-horizontal 0;

	@media (min-width: g.$breakpoint-l) {
		justify-content: initial;
		margin-bottom: 1em;
	}

	.headings-aligner {
		flex: 1 0 auto;
		display: flex;
		align-items: center;
		max-width: 100%;
		flex-wrap: wrap;
	}

	h1 {
		display: inline-block;
		margin-bottom: 0;
	}
}

.page-description,
.term-description {
	padding-top: 1em;

	@media (min-width: g.$breakpoint-l) {
		padding-top: 0;
	}
}

.product-count {
	padding-left: .6em;
	white-space: nowrap;
	letter-spacing: t.$letter-spacing--large;
	color: $content__heading_color;
	font-family: t.$font__heading;
	align-self: flex-end;
	line-height: 1.7;
	font-weight: 600;
	display: none;
	@include tool.font-size( 13 );

	@media (min-width: g.$breakpoint-l) {
		display: inline-block;
	}
}

.logo-container {
	width: 100%;
	text-align: center;

	@media (min-width: g.$breakpoint-l) {
		text-align: right;
	}

	img {
		max-width: 90px;

		@media (min-width: g.$breakpoint-l) {
			max-width: 150px;
		}
	}
}

.expandable-text,
.expandable-text p {
	line-height: 1.86;
	letter-spacing: t.$letter-spacing;
	@include tool.font-size( 13 );

	@media (min-width: g.$breakpoint-l) {
		@include tool.font-size( 15 );
	}
}

button.expandable-text__link {
	display: none;
}

// Expandable-text. Only in desktop
@media (min-width: g.$breakpoint-l) {
	.expandable-text {
		position: relative;
		max-height: 1.5em * 3;
		overflow-y: hidden;
		transition: max-height 250ms cubic-bezier(0, 1, 0, 1);

		& p:last-of-type {
			margin-bottom: 0;
		}

		&.expanded {
			max-height: 999px;
			transition: max-height 250ms cubic-bezier(1, 0, 1, 0);

			&:after {
				opacity: 0;
			}
		}
	}

	button.expandable-text__link {
		display: inline-flex;
		align-items: center;
		font-weight: 600;
		letter-spacing: t.$letter-spacing--large;
		color: $content__read-more_color;
		text-transform: uppercase;
		text-decoration: none;
		margin-top: 1em;
		border-bottom: 2px solid $content__read-more_color;
		border-top: 0;
		border-left: 0;
		border-right: 0;
		visibility: hidden;
		background-color: transparent;
		padding: 3px 0;
		@include tool.font-size( 13 );

		.show-less {
			display: none;
		}

		&:active,
		&:active:focus,
		&:focus,
		&:hover,
		&:hover:focus:active {
			color: $content__read-more_color;
			text-decoration: none;
			cursor: pointer;
		}

		&.expanded {
			.show-less {
				display: inline-block;
			}

			.show-more {
				display: none;
			}
		}
	}
}

// hide some content on mobile behind a toggle button.
.archive-mobile-toggled-content {
	max-height: 0;
	height: 100%;
	overflow: hidden;
	transition: max-height .5s ease;

	@media (min-width: g.$breakpoint-l) {
		max-height: none;
		transition: none;
		overflow: visible;
	}

	&.expanded {
		max-height: 600px;
		padding-bottom: 30px;
	}
}

.archive-mobile-toggled-btn {
	padding: 1em;
	margin-right: -1em;
	cursor: pointer;
	background-color: transparent;
	border: 0;

	@media (min-width: g.$breakpoint-l) {
		display: none;
	}

	&:focus,
	&:active,
	&:hover {
		border: 0;
	}

	svg {
		height: 10px;
		width: 15px;
		transition: transform .3s ease-in-out;

		path {
			stroke: $content__toggle-accordion_color;
		}
	}

	&.expanded svg {
		transform: rotate(180deg);
	}
}

.archive-hero.hero-with-logo {
	padding: 2em 0;

	@media (min-width: g.$breakpoint-l) {
		padding: 3.75em 0;
	}

	h1 {
		margin-top: .6em;
		text-align: center;
		display: block;
		flex: 1 0 100%;
		margin-bottom: .7em;
		letter-spacing: t.$letter-spacing--large;
		@include tool.font-size( 24 );

		@media (min-width: g.$breakpoint-l) {
			margin-bottom: 0;
			text-align: left;
			display: inline;
			flex: none;
		}
	}

	.toggle-wrapper {
		text-align: center;
	}

	.archive-mobile-toggled-btn {
		margin-right: 0;

		svg {
			height: 12px;
			width: 23px;
		}

		svg path {
			stroke: $content__toggle-accordion_color--with-hero-image;
		}
	}
}

.headings-aligner .wc-crumbs {
	width: 100%;
	display: none;

	@media (min-width: g.$breakpoint-m) {
		display: block;
	}

	a,
	a:hover {
		color: $content__link_color;
		text-decoration: none;
		margin-bottom: 20px;
	}

	.crumbs-separator {
		margin: 0 2px;
		color: $content__link_color;
	}
}

/* Extra styling if background on hero is set */
.archive-hero.hero-background {
	background-size: cover;
	position: relative;

	h1,
	h4,
	.archive-link-list a,
	.product-count,
	.term-description {
		color: $content_color--with-hero-image;
	}

	.container {
		position: relative;
		z-index: 2;
	}

	&:before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background-color: $image-overlay_color;
		z-index: 1;
	}

	.archive-mobile-toggled-btn svg path {
		stroke: $content__toggle-accordion_color--with-hero-image;
	}
}

.archive-hero .grid {
	.col-l-6 {
		padding: 0 g.$gutters-smallscreen-horizontal;

		@media (min-width: g.$breakpoint-m) {
			padding: 0 g.$gutters;
		}
	}
}
