@use "Settings/colors"as c;
@use "Settings/grid" as g;
@use "Tools" as tool;

$variation_option__separator__color: c.$base-400 !default;
$inactive_price__color: c.$base-600 !default;

.woo-variation-swatches .product-type-variable-subscription .variable-items-wrapper {
	width: 100%;
	flex-flow: column nowrap;

	li.variable-item {
		width: 100%;
		border: 0;
		border-radius: 0;
		box-shadow: unset;
		margin: 0;
		justify-content: flex-start;
		border-top: 1px solid $variation_option__separator__color;
		padding: 4px 0;
		height: auto;

		&:first-child {
			border-top: 0;
			padding-top: 0;
		}

		&:last-child {
			border-bottom: 1px solid $variation_option__separator__color;
		}

		&:not(.radio-variable-item):hover {
			box-shadow: none;
		}

		.variable-item-span {
			display: block;
			text-align: left;

			/* stylelint-disable-next-line declaration-no-important */
			padding: 10px 0 10px 35px !important;
		}

		&.selected {
			box-shadow: unset;
			background-color: transparent;
		}

		&.selected .variable-item-span-button:before {
			background: c.$black;
		}

		&.selected .variable-item-span-button:after {
			opacity: 1;
		}
	}

	.variable-item-span-button {
		color: c.$black;
		padding: 14px 0 14px 32px;
		height: auto;
		width: auto;
		line-height: 1.2;
		letter-spacing: 0;
		font-weight: 400;
		position: relative;
		@include tool.font-size( 14 );
	}

	.variable-item-span-button:before {
		content: "";
		display: block;
		width: 22px;
		height: 22px;
		border: 1px solid c.$black;
		position: absolute;
		left: 0;
		top: 7px;
		border-radius: 50%;
		transition: background .2s ease-in-out;
	}

	.variable-item-span-button:after {
		content: "";
		display: block;
		text-align: center;
		width: 9px;
		height: 4px;
		border-bottom: 2px solid c.$white;
		border-left: 2px solid c.$white;
		position: absolute;
		left: 6px;
		top: 15px;
		border-radius: 0;
		transition: opacity .2s ease-in-out;
		transform: rotate(-45deg);
		opacity: 0;
		pointer-events: none;
	}
}

.product-type-variable-subscription {
	.summary-inner-wrapper span.subscription-details {
		display: inline-block;
		line-height: 1.4;
		letter-spacing: 0;
		font-weight: 500;
		margin-top: .2rem;
		color: c.$primary-500;
		@include tool.font-size( 20 );
	}
}

.product-type-subscription,
.product-type-variable-subscription {
	.summary-inner-wrapper span.subscription-details {
		line-height: 1.4;
		@include tool.font-size( 16 );
	}
}

.product-type-subscription .summary-inner-wrapper {
	span.subscription-details {
		color: c.$primary-500;
	}
}

.woocommerce div.product.product-type-variable-subscription .variations_form .woocommerce-variation-add-to-cart {
	display: block;
}

.woo-variation-swatches .variable-items-wrapper .variable-item:not(.radio-variable-item):focus {
	box-shadow: 0 0 0 1px rgb(0 0 0 / 15%);
}

// Using more selectors here to increase specificity required to override price stuff
.woocommerce.single-product div.product.product-type-subscription .summary-inner-wrapper,
.woocommerce.single-product div.product.product-type-variable-subscription .summary-inner-wrapper {
	p.price ins .amount {
		color: c.$alert;
	}

	p.price del,
	p.price del .amount {
		color: $inactive_price__color;
	}

	.price,
	.price .from,
	.price del {
		@include tool.font-size( 20 );
	}

	p.price del {
		text-decoration: c.$alert line-through;
		@include tool.font-size( 20 );

		bdi,
		bdi span {
			color: $inactive_price__color;
			@include tool.font-size( 20 );
		}
	}
}

.woocommerce div.product.product-type-variable-subscription form.cart .variations td.value:before {
	display: none;
}

span.wcsatt-options-prompt-text-label,
.subscription-product-content--subscription-options {
	display: block;
	width: 100%;
	border-bottom: 1px solid c.$black;
	color: c.$black;
	font-weight: 500;
	padding: 10px 25px 10px 0;
	margin-bottom: 10px;
	@include tool.font-size( 16 );

	@media (min-width: g.$breakpoint-m) {
		padding: 10px 0;
	}
}

.wcsatt-options-product-prompt .wcsatt-options-prompt-text,
span.wcsatt-options-prompt-text-label {
	margin-bottom: 0;
}

.subscription-product-content--subscription-options {
	+ .variations tr > .label {
		display: none;
	}
}

.subscription-product-content--sale-label {
	background-color: c.$alert;
	display: none;
	width: auto;
	padding: 6px 18px;
	border-radius: 26px;
	color: c.$white;
	margin: -10px 0 15px;
	@include tool.font-size( 14 );

	&.active {
		display: inline-block;
	}
}

.limited-subscription-notice.notice {
	letter-spacing: 0;
	color: c.$alert;
	font-weight: 500;
	margin-bottom: 0;
	@include tool.font-size( 14 );
}

.wcsatt-options-prompt-radios,
ul.wcsatt-options-product {
	margin-bottom: 1em;
	padding: 0;

	.one-time-option,
	.subscription-option,
	.wcsatt-options-prompt-radio {
		border-top: 1px solid $variation_option__separator__color;
		padding: 0;

		label {
			cursor: pointer;
			display: block;
			padding: 14px 0;
			position: relative;
		}

		.subscription-option-details,
		.one-time-option-details,
		.wcsatt-options-prompt-action {
			padding: 0 0 0 35px;
			display: flex;
		}

		.subscription-option-details:before,
		.one-time-option-details:before,
		.wcsatt-options-prompt-action:before {
			border: 1px solid c.$black;
			border-radius: 50%;
			content: "";
			display: block;
			height: 22px;
			left: 0;
			position: absolute;
			top: 12px;
			transition: background-color .2s ease-in-out;
			width: 22px;
		}

		.subscription-option-details:after,
		.one-time-option-details:after,
		.wcsatt-options-prompt-action:after {
			content: "";
			display: block;
			text-align: center;
			width: 9px;
			height: 4px;
			border-bottom: 2px solid c.$white;
			border-left: 2px solid c.$white;
			position: absolute;
			left: 7px;
			top: 20px;
			border-radius: 0;
			transition: opacity .2s ease-in-out;
			transform: rotate(-45deg);
			opacity: 0;
			pointer-events: none;
		}

		input[type="radio"]:checked + .subscription-option-details:before,
		input[type="radio"]:checked + .one-time-option-details:before,
		input[type="radio"]:checked + .wcsatt-options-prompt-action:before {
			background-color: c.$black;
		}

		input[type="radio"]:checked + .subscription-option-details:after,
		input[type="radio"]:checked + .one-time-option-details:after,
		input[type="radio"]:checked + .wcsatt-options-prompt-action:after {
			opacity: 1;
		}

		&:first-child {
			border-top: 0;
		}

		&:last-child {
			border-bottom: 1px solid $variation_option__separator__color;
		}

		input {
			opacity: 0;
			position: absolute;
		}

		// input:selected
		&:after {
			content: "";
			display: block;
			text-align: center;
			width: 9px;
			height: 4px;
			border-bottom: 2px solid c.$white;
			border-left: 2px solid c.$white;
			position: absolute;
			left: 6px;
			top: 15px;
			border-radius: 0;
			transition: opacity .2s ease-in-out;
			transform: rotate(-45deg);
			opacity: 0;
			pointer-events: none;
		}
	}
}

.woocommerce div.product ul.wcsatt-options-product span.no-price.one-time-price,
.woocommerce div.product ul.wcsatt-options-product span.price,
.woocommerce div.product ul.wcsatt-options-product span.price ins .woocommerce-Price-amount {
	color: c.$black;
	font-weight: 400;
	height: auto;
	letter-spacing: 0;
	line-height: 1.2;
	padding: 0;
	position: relative;
	width: auto;
	@include tool.font-size( 14 );
}

.woocommerce div.product ul.wcsatt-options-prompt-radios .wcsatt-options-prompt-action {
	color: c.$black;
	font-weight: 400;
	height: auto;
	letter-spacing: 0;
	line-height: 1.2;
	width: auto;
	@include tool.font-size( 14 );
}

.wcsatt-options-product-prompt {
	margin-bottom: 0;
}

.has-subscription-plans {
	.summary-inner-wrapper {
		display: flex;
		flex-direction: column;
	}

	.woocommerce-variation-price {
		display: block;
	}

	.product_title + p.price * {
		@include tool.font-size( 22 );
	}

	.product_title + p.price small.wcsatt-sub-options {
		line-height: 1.2;
	}

	&.product-type-variable {
		.product_title + p.price .wcsatt-options-wrapper {
			display: none;
		}

		.product_title + p.price > span.price {
			display: none;
		}

		.woocommerce-variation-price span.price {
			display: none;
		}

		.woocommerce-product-details__short-description p:last-child {
			margin-bottom: 1rem;
		}
	}
}

.woocommerce div.product.has-subscription-plans form.cart .woocommerce-variation-add-to-cart {
	display: block;
}

.wcsatt-options-wrapper-grouped .wcsatt-options-product-wrapper {
	margin-top: 1rem;
}

.wcsatt-options-product-prompt.wcsatt-options-product-prompt-radio {
	margin-bottom: 1rem;
}
