@use "Settings/typography" as t;
@use "Settings/grid" as g;
@use "Tools" as tools;

$font-size: 15 !default;
$font-size--desktop: 17 !default;
$line-height: 1.53 !default;
$line-height--desktop: 1.6 !default;
$letter-spacing: t.$letter-spacing !default;
$letter-spacing--desktop: t.$letter-spacing !default;
$font-weight: t.$base__font-weight !default;
$font-style: normal !default;
$font-stretch: normal !default;
$margin: 0 0 1.5em 0 !default;
$small_font-size: $font-size - 2 !default;
$small_font-size--desktop: $font-size--desktop - 2 !default;
$small_font-weight: 300 !default;
$small_font-style: normal !default;
$small_font-stretch: normal !default;
$small_line-height: 1.73 !default;
$small_line-height--desktop: .54 !default;
$small_letter-spacing: t.$letter-spacing !default;
$small_letter-spacing--desktop: t.$letter-spacing !default;
$emphasis_font-style: italic !default;
$strong_font-weight: 700 !default;

body:not(.wp-admin) {
	font-family: t.$font__content;
	color: t.$color__content;
	line-height: t.$base__line-height;
	display: flex;
	flex-direction: column;
	height: 100vh; /* Avoid the IE 10-11 `min-height` bug. */
	position: static;
	overflow-x: hidden; /* needed for ACF Modules margin-left: calc(50% - 50vw); code see https://cloudfour.com/thinks/breaking-out-with-viewport-units-and-calc/ */

	&.admin-bar {
		height: calc(100vh - 32px);
	}

	&.mobile-menu-open {
		overflow: hidden;
	}
}

.site,
.site-footer {
	flex-shrink: 0;
}

.site {
	flex: 1 0 auto;
}

.site-main {
	overflow: hidden;
}

p {
	margin: $margin;
	font-weight: $font-weight;
	font-style: $font-style;
	font-stretch: $font-stretch;
	line-height: $line-height;
	letter-spacing: $letter-spacing;

	@include tools.font-size($font-size);

	@media (min-width: g.$breakpoint-m) {
		line-height: $line-height--desktop;
		letter-spacing: $letter-spacing--desktop;

		@include tools.font-size($font-size--desktop);
	}
}

small {
	font-weight: $small_font-weight;
	font-style: $small_font-style;
	font-stretch: $small_font-stretch;
	line-height: $small_line-height;
	letter-spacing: $small_letter-spacing;

	@include tools.font-size($small_font-size);

	@media (min-width: g.$breakpoint-s) {
		line-height: $small_line-height--desktop;
		letter-spacing: $small_letter-spacing--desktop;

		@include tools.font-size($small_font-size--desktop);
	}
}

dfn,
cite,
em,
i {
	font-style: $emphasis_font-style;
}

strong,
b {
	font-weight: $strong_font-weight;
}
