@use "Settings/colors" as c;
@use "Settings/grid" as g;
@use "Settings/typography" as t;

@use "Tools" as tool;

.woocommerce .product-type-composite {
	.summary-inner-wrapper {
		max-width: unset;
	}

	.pagination_elements {
		margin: unset;
		padding: unset;
		font-weight: 400;
		@include tool.font-size(14);
	}

	.composite_component:not(:last-child) {
		margin-bottom: 20px;
		padding-bottom: 20px;
		border-bottom: 1px solid c.$base-400;

		.component_title_toggled {
			margin-top: 20px;
		}
	}

	.composite_form:not(.paged) .component .component_inner {
		padding-bottom: unset;
	}

	.component .component_summary .content {
		padding-top: unset;
	}

	.component_selections {
		margin-top: 15px;
	}

	.composited_product_title_wrapper {
		.selected_option_label_wrapper {
			display: none;
		}

		.composited_product_title {
			margin-bottom: unset;
			font-weight: 600;
			line-height: 1.4;
			letter-spacing: t.$letter-spacing--large;

			@include tool.font-size(14);
		}
	}

	.details p {
		color: c.$base-600;
		line-height: 1.4;
		font-weight: 300;
		letter-spacing: t.$letter-spacing;
		margin-bottom: unset;
		@include tool.font-size(14);
	}

	.component_content {
		margin-top: 20px !important; // stylelint-disable-line declaration-no-important
	}

	.component .composited_product_title_wrapper {
		padding-top: unset;
	}

	.component_section_title,
	.summary_title {
		font-weight: 600;
		line-height: 1.4;
		margin-bottom: 5px;
		letter-spacing: t.$letter-spacing--large;
		@include tool.font-size(14);
	}

	.component_title {
		margin-bottom: unset;
		font-weight: 600;
		line-height: 1.4;
		text-transform: uppercase;
		letter-spacing: t.$letter-spacing--large;
		@include tool.font-size(16);
	}

	.component_description {
		margin-bottom: unset;

		p {
			color: c.$base-600;
			line-height: 1.4;
			font-weight: 300;
			letter-spacing: t.$letter-spacing;
			margin-bottom: unset;
			@include tool.font-size(14);
		}
	}

	.price {
		line-height: 1;
		width: 100%;
		padding-top: 10px;
		color: c.$base-900;
		@include tool.font-size(16);

		@media (min-width: g.$breakpoint-m) {
			font-weight: 500;
			margin-top: 0;
			margin-bottom: 1.45rem;
			padding-left: 0;
			padding-right: 0;
		}

		.amount {
			color: c.$base-900;
			font-weight: 500;
			font-family: t.$font__content;
			@include tool.font-size(13);

			@media (min-width: g.$breakpoint-s) {
				@include tool.font-size(14);
			}
		}
	}

	.price ins {
		text-decoration: none;
		margin-right: .5rem;

		.amount {
			color: c.$primary;
		}
	}

	.price del {
		opacity: 1;
		font-weight: 300;
		@include tool.font-size(14);

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size(15);
		}

		.amount {
			color: c.$base-900;
			font-weight: 300;
			@include tool.font-size(14);

			@media (min-width: g.$breakpoint-s) {
				@include tool.font-size(15);
			}
		}
	}

	.composite_form:not(.paged) .composite_wrap {
		padding-top: unset;
	}

	.component_option_thumbnails_container {
		margin: unset;
		padding: unset;
		display: grid;
		grid-template-columns: 1fr 1fr;

		@media (min-width: g.$breakpoint-l) {
			grid-template-columns: 1fr 1fr 1fr;
		}
	}

	.component_option_thumbnail_container {
		text-align: left;
		padding: unset;
		margin: unset;

		.image,
		.summary_element_image {
			padding: unset;
			width: 100%;
		}

		.thumbnail_description {
			padding: unset;
		}

		.thumbnail_title,
		.summary_element_title {
			font-weight: 500;
			line-height: 1.4;
			margin-bottom: 5px;
			margin-top: 10px;
			letter-spacing: t.$letter-spacing--large;
			@include tool.font-size(12);
		}

		.thumbnail_buttons button,
		.summary_element_button a {
			@include tool.font-size(12);
		}
	}

	.component_option_radio_button_container .radio_button_description .title {
		font-weight: 500;
		line-height: 1.4;
		margin-bottom: unset;
		margin-top: unset;
		letter-spacing: t.$letter-spacing--large;
		@include tool.font-size(12);
	}

	.component .cart .variations td.label {
		padding: unset;
	}

	.woocommerce-info,
	.woocommerce-message,
	.woocommerce-error, {
		background: c.$error-lighter;
		padding: 15px;

		&:before {
			display: none;
		}

		ul {
			padding: unset;
			@include tool.font-size(12);
		}
	}

	.composite_navigation_inner {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 10px;
		align-items: center;
	}

	.composite_form .composite_navigation.paged .next,
	.composite_form .composite_navigation.paged .prev {
		color: c.$white;
		background-color: c.$primary;
		float: unset;
		margin-right: unset;
		margin-left: unset;
		max-width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-radius: 6px;
		padding: 10px;
		font-weight: 400;
		@include tool.font-size(14);

		&:hover,
		&:focus {
			background-color: c.$primary-600;
		}

		&.inactive {
			color: c.$white;
			background-color: c.$base-200;
		}

		&:after,
		&:before {
			right: unset;
			left: unset;
			position: relative;
			top: unset;
			transform: unset;
			margin: unset;
		}
	}

	.summary_elements {
		margin: unset;
		padding: unset;
	}

	.summary_element_wrapper_inner {
		text-align: left;
		padding: unset;
		margin: unset;
		display: grid;
		grid-template-columns: auto 1fr;
		align-items: center;

		.summary_element_image {
			padding: unset;
			width: 100%;
			order: -1;
			grid-row: span 4;
			max-width: 140px;
			margin-right: 20px;
		}

		.summary_element_title {
			font-weight: 500;
			line-height: 1.4;
			letter-spacing: t.$letter-spacing--large;
			margin: unset;
			@include tool.font-size(12);
		}

		.summary_element_content {
			margin: unset;
		}

		.summary_element_button a {
			@include tool.font-size(12);
		}
	}

	.composite_pagination .pagination_element_current .element_link {
		color: c.$primary;
	}

	.composite_pagination .element_link {
		color: c.$black;
	}

	span.component_option_each {
		color: c.$primary;
		@include tool.font-size(12);
	}

	.component_wrap .quantity {
		margin-top: 10px !important; // stylelint-disable-line declaration-no-important

		input {
			border: 1px solid c.$base-400;
			font-weight: 300;
			@include tool.font-size(14);
		}
	}

	.composite_form .component_title_toggled {
		width: 100%;

		.component_title_text {
			padding-left: unset;
			display: flex;
			align-items: center;
			width: 100%;

			&:before {
				display: none;
			}

			&:after {
				content: "";
				width: 7px;
				height: 7px;
				border-right: 1px solid;
				border-bottom: 1px solid;
				transform: rotate(45deg) scale(-1, -1);
				position: relative;
				margin-left: auto;
			}
		}
	}

	.composite_form .component.closed .component_title_toggled .component_title_text:after {
		transform: rotate(45deg);
	}
}
