@use "Settings/grid" as g;

/* stylelint-disable max-nesting-depth */
.wp-block-product-best-sellers {
	&.swiper-initialized {
		.swiper-slide {
			transition: opacity .2s ease-in-out;
			opacity: 0;
			pointer-events: none;

			&.swiper-slide-visible:not(.swiper-slide-prev),
			&.swiper-slide-active {
				opacity: 1;
				pointer-events: all;
			}
		}
	}
}

/* stylelint-enable */
