@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/z-index" as z;
@use "Settings/general" as gr;

@use "Tools" as tool;

// todo: Not sure where this one is used, so im only converting it to work.
.modal {
	z-index: z.$header;
	text-align: center;
	padding: 0;

	&:before {
		content: "";
		display: inline-block;
		height: 100%;
		vertical-align: middle;
		margin-right: -4px;
		pointer-events: none;
	}

	.close {
		opacity: 1;
		outline: none;
	}

	.map-close-icon:before,
	.map-close-icon:after {
		background-color: c.$secondary;
	}
}

.modal-backdrop {
	z-index: z.$below-overlay;

	&.in {
		opacity: .6;
	}
}

.modal-dialog {
	display: inline-block;
	text-align: left;
	vertical-align: middle;
}

.modal-header {
	border: 0;
	padding: 1.5rem 2rem 0;

	.close {
		margin-top: .5rem;
	}
}

.modal-content {
	border: 0;
	border-radius: 0;
	box-shadow: gr.$box-shadow__bottom;
}

.modal-body {
	padding: 1.5rem 2rem;
}

.c-social-share__modal-list {
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	margin-bottom: 0;
	margin-left: -.5em;
	margin-right: -.5rem;

	.c-social-share__item {
		margin: .5rem;
	}
}
