@use "Settings/colors"as c;
@use "Settings/typography" as t;
@use "Tools" as tools;

*,
*:before,
*:after {
	box-sizing: border-box;
}

html {
	@include tools.base-font-size(t.$base__font-size);
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
}

*[role="button"] {
	cursor: pointer;
}
