@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;

@use "Tools" as tool;

@use "Components/headings" as *;
@use "Components/pages/woocommerce/product" as p;

// ================================
// ====== ADD TO CART BUTTON ======
// ================================
$button_background-color: c.$primary !default;
$button_padding-top: 15px !default;
$button_padding-right: 0 !default;
$button_padding-bottom: 15px !default;
$button_padding-left: 0 !default;
$button_padding: $button_padding-top $button_padding-right $button_padding-bottom $button_padding-left !default;
$button_padding-top--desktop: 17px !default;
$button_padding-right--desktop: 0 !default;
$button_padding-bottom--desktop: 17px !default;
$button_padding-left--desktop: 0 !default;
$button_padding--desktop: $button_padding-top--desktop $button_padding-right--desktop $button_padding-bottom--desktop $button_padding-left--desktop !default;
$button_letter-spacing: t.$letter-spacing--large !default;
$button_font-size: 11 !default;
$button_letter-spacing--desktop: t.$letter-spacing--large !default;
$button_font-size--desktop: 13 !default;
$max-width: 765px !default;

.woocommerce div.product form.cart {
	background-color: transparent;
	margin-bottom: 30px;
	box-shadow: none;
	width: 100%;
	display: block;
	padding-left: p.$product_wrap_inner_elements_horizontal_padding_mobile;
	padding-right: p.$product_wrap_inner_elements_horizontal_padding_mobile;

	@media (min-width: g.$breakpoint-m) {
		padding: 0;
	}

	&.variations_form {
		display: block;
	}

	// Such Woo.
	&:before,
	&:after {
		content: none;
	}

	.woocommerce-variation-add-to-cart {
		display: flex;
	}

	.add-to-cart-wrapper {
		display: flex;
		flex-grow: 1;
		justify-content: space-between;
		align-items: center;
	}

	.single_add_to_cart_button {
		float: none; // WooCoomerce 2019
		display: inline-block;
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			flex: 1 0 auto;
			display: inline-block;
			text-align: center;
		}
	}

	.variations tbody tr {
		display: flex;
		flex-direction: column;
		margin-bottom: 16px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	.variations td.label {
		order: 0;

		label,
		.label-value {
			@extend .meta;

			text-transform: none;
		}
	}

	.variations td.value {
		position: relative;

		select {
			appearance: none;
			margin-right: 0;
		}

		&:before {
			display: none;
		}
	}

	.variations th.label {
		text-align: left;
	}

	.variations .reset_variations {
		display: none !important; // stylelint-disable-line declaration-no-important
	}

	div.product-addon-totals ul {
		padding: 0;
	}

	div.product-addon-totals .wc-pao-subtotal-line p.price {
		margin: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0;
	}
}

.woocommerce-variation-price {
	display: none;
}

.woocommerce-variation-description p {
	margin-top: 0;
	font-weight: 300;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.54;
	letter-spacing: t.$letter-spacing;
	@include tool.font-size(13);

	@media (min-width: g.$breakpoint-m) {
		margin-top: inherit;
		line-height: 1.73;
		letter-spacing: t.$letter-spacing;
	}
}

form.composite_form {
	width: 100%;
	padding-left: p.$product_wrap_inner_elements_horizontal_padding_mobile !important; /* stylelint-disable-line declaration-no-important */

	@media (min-width: g.$breakpoint-m) {
		padding-left: 0 !important; /* stylelint-disable-line declaration-no-important */
	}

	@media (min-width: g.$breakpoint-xl) {
		max-width: $max-width;
	}
}
