@use "sass:math";

@use "Settings/grid"as g;
@use "Settings/spacing" as s;

@use "functions" as func;

@function is-px($value) {

	@return is-number($value) and index("px", unit($value)) != null;
}

@mixin font-size($sizeValue: 16) {
	font-size: func.calculate-rem($sizeValue);

	@if is-px($sizeValue) {
		// px value passed, which will cause an incorrect style of rempx
		@error "px found in font-size value #{$sizeValue}, please enter a number without `px` otherwise font-size: pxrem will be compiled and the font-size will be incorrect.";
	}
}

@mixin base-font-size($sizeValue: 16) {
	font-size: #{math.div($sizeValue, 16) * 100 + "%"};
}

// Todo: This could also be refactored in to a generic class which is applied in markup to reduce repeated css output. Worth thinking about.
@mixin standard-vertical-padding() {
	padding-top: s.$standard-vertical-spacing;
	padding-bottom: s.$standard-vertical-spacing;

	@media (min-width: g.$breakpoint-m) {
		padding-top: s.$standard-vertical-spacing--desktop;
		padding-bottom: s.$standard-vertical-spacing--desktop;
	}
}

@mixin small-vertical-padding() {
	padding-top: s.$small-vertical-spacing;
	padding-bottom: s.$small-vertical-spacing;

	@media (min-width: g.$breakpoint-m) {
		padding-top: s.$small-vertical-spacing--desktop;
		padding-bottom: s.$small-vertical-spacing--desktop;
	}
}

@function if-important($important) {

	@return #{if($important, "!important", "")};
}

@mixin standard-horizontal-padding($important: false) {
	padding-left: g.$gutters-smallscreen-horizontal if-important($important);
	padding-right: g.$gutters-smallscreen-horizontal if-important($important);

	@media (min-width: g.$gutters-smallscreen-breakpoint) {
		padding-left: g.$gutters if-important($important);
		padding-right: g.$gutters if-important($important);
	}
}

@mixin create-mq($breakpoint) {

	@if $breakpoint == 0 {
		@content;
	} @else { // stylelint-disable at-rule-empty-line-before

		@media screen and (min-width: $breakpoint * 1px) {
			@content;
		}
	}
}

@mixin create-col-classes($modifier, $nr_of_columns, $breakpoint) {
	@include create-mq($breakpoint) {

		@for $i from 1 through $nr_of_columns {
			.col#{ $modifier }-#{ $i } {
				flex-basis: math.div(100, math.div($nr_of_columns, $i)) * 1%;
				max-width: math.div(100, math.div($nr_of_columns, $i)) * 1%; // Flexbug fix.
			}
		}
	}
}

@mixin arrow( $arrow-color: currentColor ) {
	content: "";
	transition: background-color .4s ease;
	background-color: $arrow-color;
	mask-repeat: no-repeat;
	mask-position: center;
	mask-size: contain;
	display: inline-block;
	vertical-align: middle;
	mask-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.02273 13.4249H17.5084L12.6065 18.4576C12.2071 18.8677 12.2071 19.5323 12.6065 19.9423C12.8064 20.1476 13.0677 20.25 13.3295 20.25C13.5914 20.25 13.8527 20.1476 14.0526 19.9423L20.7003 13.1173C20.8921 12.9209 21 12.6537 21 12.3749C21 12.0962 20.8921 11.8295 20.7003 11.6326L14.0526 4.80752C13.6532 4.39749 13.0059 4.39749 12.6065 4.80752C12.2071 5.21755 12.2071 5.8822 12.6065 6.29223L17.5084 11.3249H4.02273C3.45818 11.3249 3 11.7953 3 12.3749C3 12.9545 3.45818 13.4249 4.02273 13.4249Z' fill='%23000000'/%3E%3C/svg%3E");
}

@mixin cart( $arrow-color: currentColor ) {
	content: "";
	margin: -12px 0 -3px 15px;
	transition: background-color .4s ease;
	background-color: $arrow-color;
	mask-repeat: no-repeat;
	mask-position: center;
	mask-size: contain;
	display: inline-block;
	position: absolute;
	height: 26px;
	width: 26px;
	right: 15px;
	top: 50%;
	mask-image: url("data:image/svg+xml;base64,");
}
