@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

@use "Components/pages/single-page" as sp;

// todo: This should maybe be broken out in to smaller chunks for a less cluttered API

// ====================
// ====== HEADER ======
// ====================

// Image
$header__image_max-height: tool.calculate-rem(520) !default;
$header__image_overflow: hidden !default;
$header__image__img_width: 100% !default;
$header__image__img_height: auto !default;

// Categories
$header__categories_margin: 2.5em 0 0 0 !default;
$header__categories_margin--desktop: 5.5em 0 0 0 !default;
$header__categories_color: c.$primary !default;
$header__categories_color--hover: c.$primary-variant--chambray !default;
$header__categories__a_color: $header__categories_color !default;
$header__categories__a_color--hover: $header__categories_color--hover !default;
$header__categories__a_font-size: 17 !default;
$header__categories__a_text-decoration: none !default;
$header__categories__a_letter-spacing: t.$letter-spacing--large !default;
$header__categories__a_font-weight: 700 !default;

// todo: This should be a generic pill style which is extened here instead
// Categories - Pill style
$header__categories__a_is-style-pill: false !default;
$header__categories__a--pill_font-size: 14 !default;
$header__categories__a--pill_color: c.$white !default;
$header__categories__a--pill_color--hover: c.$white !default;
$header__categories__a--pill_background-color: c.$primary !default;
$header__categories__a--pill_background-color--hover: c.$primary-variant--chambray !default;
$header__categories__a--pill_border-color: $header__categories__a_color !default;
$header__categories__a--pill_border-width: 1px !default;
$header__categories__a--pill_border: $header__categories__a--pill_border-width solid $header__categories__a--pill_border-color !default;
$header__categories__a--pill_border-radius: 5em !default;
$header__categories__a--pill_padding: .7em .9em !default;
$header__categories__a--pill_transition: all .3s ease !default;

// Headings
$header__title_margin: 0 !default;
$header__title_margin--desktop: .5em 0 !default;
$header__subheading_margin: 0 0 1.5em 0 !default;
$header__subheading_margin--desktop: 0 0 1.5em 0 !default;

// =====================
// ====== Footer ======
// =====================

$footer_margin: 3em 0 !default;
$footer_margin--desktop: 3.6em 0 !default;

// Author
$footer__author_display: flex !default;
$footer__author_align-items: center !default;
$footer__author_padding: g.$gutters * .5 0 !default;
$footer__author_padding--desktop: g.$gutters g.$gutters * .5 !default;
$footer__author_border-top: 1px solid c.$base-300 !default;
$footer__author_border-bottom: 1px solid c.$base-300 !default;
$footer__author__img_max-width: 70px !default;
$footer__author__img_max-width--desktop: 100% !default;
$footer__author__img_border-radius: 50% !default;
$footer__author__bio_display: flex !default;
$footer__author__bio_flex-direction: column !default;
$footer__author__bio_margin-left: g.$gutters * .5 !default;
$footer__author__bio_margin-left--desktop: g.$gutters !default;
$footer__author__bio__heading_display: block !default;
$footer__author__bio__heading_margin-top: .5em !default;
$footer__author__bio__heading_margin-bottom: .3em !default;
$footer__author__bio__heading_letter-spacing: t.$letter-spacing--large !default;
$footer__author__bio__heading_letter-spacing--desktop: t.$letter-spacing--large !default;
$footer__author__bio__heading_margin-top--desktop: .9em !default;
$footer__author__bio__heading_margin-bottom--desktop: .2em !default;
$footer__author__bio__name_display: block !default;
$footer__author__bio__name_margin: 0 !default;
$footer__author__bio__name__a_font-size: 15 !default;
$footer__author__bio__name__a_font-size--desktop: 17 !default;
$footer__author__bio__name__a_font-weight: 500 !default;
$footer__author__bio__name__a_letter-spacing: t.$letter-spacing !default;
$footer__author__bio__name__a_line-height: 1.6 !default;
$footer__author__bio__name__a_text-decoration: none !default;
$footer__author__bio__description_font-size: 13 !default;
$footer__author__bio__description_font-size--desktop: 15 !default;
$footer__author__bio__description_display: block !default;
$footer__author__bio__description_margin: 0 !default;

.standard-post-header-image {
	max-height: $header__image_max-height;
	overflow: $header__image_overflow;

	img {
		width: $header__image__img_width;
		height: $header__image__img_height;
	}
}

.standard-post-container {
	max-width: 100%;
	padding: unset;
}

.entry-header .single-post-category {
	margin: $header__categories_margin;

	@media (min-width: g.$breakpoint-s) {
		margin: $header__categories_margin--desktop;
	}

	a {
		text-decoration: $header__categories__a_text-decoration;
		letter-spacing: $header__categories__a_letter-spacing;
		font-weight: $header__categories__a_font-weight;

		@if $header__categories__a_is-style-pill {
			color: $header__categories__a--pill_color;
			background-color: $header__categories__a--pill_background-color;
			padding: $header__categories__a--pill_padding;
			border: $header__categories__a--pill_border;
			border-radius: $header__categories__a--pill_border-radius;
			transition: $header__categories__a--pill_transition;

			@include tool.font-size($header__categories__a--pill_font-size);

			&:hover,
			&:active {
				color: $header__categories__a--pill_color--hover;
				background-color: $header__categories__a--pill_background-color--hover;
			}
		} @else { // stylelint-disable-line
			color: $header__categories__a_color;
			@include tool.font-size($header__categories__a_font-size);

			&:hover,
			&:active {
				color: $header__categories__a_color--hover;
			}
		}
	}

	.single-post-category__separator:last-of-type {
		display: none;
	}
}

.entry-footer {
	margin: $footer_margin;
	max-width: g.$container_size_narrow;
	margin-left: auto;
	margin-right: auto;
	@include tool.standard-horizontal-padding;

	.single-post-author-bio-wrapper {
		display: $footer__author_display;
		align-items: $footer__author_align-items;
		padding: $footer__author_padding;
		border-top: $footer__author_border-top;
		border-bottom: $footer__author_border-bottom;

		@media (min-width: g.$breakpoint-s) {
			padding: $footer__author_padding--desktop;
		}
	}

	.single-post-author-image img {
		max-width: $footer__author__img_max-width;
		border-radius: $footer__author__img_border-radius;

		@media (min-width: g.$breakpoint-s) {
			max-width: $footer__author__img_max-width--desktop;
		}
	}

	.single-post-author-bio {
		display: $footer__author__bio_display;
		flex-direction: $footer__author__bio_flex-direction;
		margin-left: $footer__author__bio_margin-left;

		@media (min-width: g.$breakpoint-s) {
			margin-left: $footer__author__bio_margin-left--desktop;
		}

		.section-heading {
			display: $footer__author__bio__heading_display;
			margin-top: $footer__author__bio__heading_margin-top;
			margin-bottom: $footer__author__bio__heading_margin-bottom;
			letter-spacing: $footer__author__bio__heading_letter-spacing;

			@media (min-width: g.$breakpoint-s) {
				letter-spacing: $footer__author__bio__heading_letter-spacing--desktop;
				margin-top: $footer__author__bio__heading_margin-top--desktop;
				margin-bottom: $footer__author__bio__heading_margin-bottom--desktop;
			}
		}
	}

	.author-bio {
		padding: 0;
		margin: 0;
	}

	.author-name {
		display: $footer__author__bio__name_display;
		margin: $footer__author__bio__name_margin;

		a {
			font-weight: $footer__author__bio__name__a_font-weight;
			letter-spacing: $footer__author__bio__name__a_letter-spacing;
			line-height: $footer__author__bio__name__a_line-height;
			text-decoration: $footer__author__bio__name__a_text-decoration;
			@include tool.font-size($footer__author__bio__name__a_font-size);

			@media (min-width: g.$breakpoint-m) {
				@include tool.font-size($footer__author__bio__name__a_font-size--desktop);
			}
		}
	}

	.author-description {
		display: $footer__author__bio__description_display;
		margin: $footer__author__bio__description_margin;
		@include tool.font-size($footer__author__bio__description_font-size);

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size($footer__author__bio__description_font-size--desktop);
		}
	}
}
