@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;

@use "Tools" as tools;

.wc-bookings-booking-form {
	border: unset;
	padding: unset;

	fieldset {
		border: unset;
		margin: unset;
		padding: unset;
	}

	.ui-datepicker-header {
		background: unset;
		border-radius: unset;
		border: unset;
		border-top: 1px solid c.$base-400;
		border-bottom: 1px solid c.$base-400;
		color: c.$black;
		text-transform: unset;
		font-family: t.$font__content;
		@include tools.font-size(t.$base__font-size);
	}

	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-title {
		padding: 20px 0;
		margin: 0 45px;
	}

	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-prev,
	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-next {
		top: 50%;
		background: c.$primary-500;
		border-radius: 50%;
		height: 40px;
		width: 40px;
		transform: translateY(-50%);
		justify-content: center;
		align-items: center;
		transition: background-color .35s ease;

		&:hover,
		&:focus {
			background: c.$primary-400;
		}

		&.ui-state-disabled {
			background: c.$base-500;
		}

		> span {
			filter: brightness(0) invert(1);
			background-size: 20px 20px;
		}
	}

	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-prev span {
		background-image: url("../../../qala-theme/assets/images/icons/navigation/chevron-left.svg");
		background-position: -2px center;
	}

	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-next span {
		background-image: url("../../../qala-theme/assets/images/icons/navigation/chevron-right.svg");
		background-position: 0 center;
	}

	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-prev-hover {
		left: 2px; // overwrite jquery smoothness styling that jumps button an ugly 1px on hover
	}

	.wc-bookings-date-picker .ui-datepicker .ui-datepicker-next-hover {
		right: 2px; // overwrite jquery smoothness styling that jumps button an ugly 1px on hover
	}

	.ui-widget-content {
		box-shadow: unset;
	}

	.wc-bookings-booking-cost {
		margin: unset;
		padding-left: unset;
		padding-right: unset;
		background: transparent;
	}

	.wc-bookings-date-picker .ui-datepicker th,
	.wc-bookings-date-picker .ui-datepicker td {
		border: unset;
		color: c.$black;
		font-weight: 400;
		font-family: t.$font__content;
		@include tools.font-size(14);
	}

	.ui-state-disabled,
	.ui-widget-content .ui-state-disabled,
	.ui-widget-header .ui-state-disabled {
		opacity: 1;
	}

	.wc-bookings-date-picker .ui-datepicker td.ui-datepicker-other-month {
		background: c.$base-100;
		color: c.$base-300;
	}

	/* stylelint-disable declaration-no-important  */
	.wc-bookings-date-picker .ui-datepicker td.bookable-range .ui-state-default {
		background: c.$success !important;

		&.ui-state-hover {
			color: c.$white !important;
		}
	}

	.wc-bookings-date-picker .ui-datepicker td.fully_booked span,
	.wc-bookings-date-picker .ui-datepicker td.fully_booked a {
		background-color: c.$error-lighter !important;
		color: c.$white !important;
	}

	.wc-bookings-date-picker .ui-datepicker td.bookable a {
		background-color: unset !important;
		color: c.$success !important;
		text-shadow: none;
		transition: all .35s ease;

		&:hover,
		&:focus {
			background-color: c.$success !important;
			color: c.$white !important;
		}
	}
	// stylelint-enable
	.wc-bookings-date-picker-date-fields {
		line-height: 45px;
		display: flex;
		justify-content: space-between;
	}

	.wc-bookings-date-picker-date-fields,
	.wc_bookings_field_duration {
		input {
			padding: 5px;
			height: 45px;
			font-size: 14px;
			border-radius: 0;
			border: 1px solid c.$black;
		}
	}
}
