@use "Settings/grid"as g;
@use "Settings/colors"as c;

@use "Tools"as tool;

@use "Elements/headings" as *;

.text-module-wrapper {
	position: relative;
	overflow: hidden;
	@include tool.standard-vertical-padding;

	p {
		max-width: 100%;
	}

	.module-heading {
		@extend h3;

		margin-top: 0;
	}

	.text-module-pattern {
		svg {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;

			@media (min-width: g.$breakpoint-l) {
				width: 100%;
				height: auto;
			}
		}

		&.text-module-pattern-desktop {
			display: none;

			@media (min-width: g.$breakpoint-l) {
				display: block;
			}
		}

		&.text-module-pattern-mobile {
			display: block;

			@media (min-width: g.$breakpoint-l) {
				display: none;
			}
		}
	}

	&.light {
		background-color: c.$light-scheme__background-color;
		color: c.$light-scheme__text-color;

		.module-heading,
		p {
			color: c.$light-scheme__text-color;
		}

		.text-module-pattern svg {
			opacity: .3;
		}
	}

	&.dark {
		background-color: c.$dark-scheme__background-color;
		color: c.$dark-scheme__text-color;

		.module-heading,
		a,
		p {
			color: c.$dark-scheme__text-color;
		}

		.cta-secondary {
			@include tool.btn-type(4);
		}
	}

	.text-module-left {
		z-index: 5;

		@media (min-width: g.$breakpoint-m) {
			padding-left: .5em;
		}

		p:first-of-type {
			margin-top: 0;

			@media (min-width: g.$breakpoint-m) {
				margin-top: .3em;
			}
		}
	}

	.text-module-left-column {
		z-index: 5;
		padding-bottom: 15px;

		@media (min-width: g.$breakpoint-m) {
			padding-bottom: g.$gutters * .5;
		}
	}

	.text-module {
		text-align: center;
		justify-content: center;
		display: flex;
		z-index: 5;

		p {
			margin-top: 0;
		}

		.module-heading {
			width: 100%;
			margin-bottom: .5em;

			@media (min-width: g.$breakpoint-m) {
				width: 70%;
				margin-bottom: .7em;
			}
		}

		.text-module-content {
			max-width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 100%;

			@media (min-width: g.$breakpoint-m) {
				max-width: 58%;
			}
		}
	}

	.module-button-container {
		width: auto;
	}
}
