@use "Settings/grid"as g;

@use "Tools"as tool;

.spotlight-pattern {
	padding: 0 #{ g.$gutters };
	@include tool.standard-vertical-padding;

	@media (max-width: 781px) {
		.wp-block-column:not(:only-child) {
			flex-basis: calc(50% - .5rem) !important; // stylelint-disable-line
			flex-grow: 0;
		}

		.wp-block-column:nth-child(n+3) {
			margin-top: 1.75rem;
		}

		.wp-block-column p {
			margin: 2rem 0 2.25rem;
		}
	}

	.wp-block-columns {
		gap: 1em;

		@media (min-width: 782px) {
			gap: 2em;
		}
	}

	.spotlight-pattern__heading {
		margin-top: 0;
	}

	.wp-block-column p {
		margin: 1rem 0 1.25rem;
	}

	&.alignfull {
		padding-right: 1.5rem;
		padding-left: 1.5rem;

		@media (min-width: g.$breakpoint-l) {
			padding-right: 3rem;
			padding-left: 3rem;
		}
	}
}
