/*
 * === Typography variables ===
 */

@use "Settings/colors" as c;

$color__content: c.$base-900 !default;
$color__heading: $color__content !default;
$font__heading: var(--wp--preset--font-family--inter) !default;
$font__content: var(--wp--preset--font-family--inter) !default;
$font__inter: var(--wp--preset--font-family--inter) !default;
$base__font-size: 16 !default;
$base__line-height: 1.53 !default;
$base__font-weight: 300 !default;
$letter-spacing: .005em !default;
$letter-spacing--large: .01em !default;
