@use "sass:color";
@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;
@use "Settings/z-index" as z;

@use "Tools" as tool;

@use "Objects/grid" as *;
@use "Components/navigation/header" as h;
@use "Components/navigation/search" as search;

$font: t.$font__heading !default;
$menu-item_font-weight: 400 !default;
$menu-item_text-transform: none !default;
$menu-item_text-transform_breakpoint-l: none !default;
$menu-item_letter_spacing: t.$letter-spacing !default;
$menu-item_letter_spacing_breakpoint-l: t.$letter-spacing !default;
$menu-item_font-size: 16 !default;
$menu-item_font-size_breakpoint-l: 16 !default;
$menu-item_color: c.$base-900 !default;
$menu-item_color--hover: c.$primary !default;
$menu-item_color--top-level-hover: $menu-item_color--hover !default;
$menu-item_color--current: $menu-item_color--hover !default;
$menu-item_padding: 0 !default;
$menu-item_padding--desktop: 0 .8em !default;
$menu-item_background-color--top-level-hover: transparent !default;
$menu-item__a_padding: 40px 0 !default;
$menu-item__a_padding--collapsed: 25px 0 !default;
$menu-item__after_is-enabled: false !default;
$menu-item__after_background-color: $menu-item_color--hover !default;
$menu-item__after_height: 4px !default;
$menu-item__after_width: 100% !default;
$menu-item__after_border-radius: 5px 5px 0 0 !default;
$menu-item__after_margin: #{$menu-item__after_height * -1} 0 0 0 !default;
$mobilemenu__overlay_color: c.$base-900 !default;
$mobilemenu__background-color: h.$background-color !default;
$sub-menu_font: t.$font__content !default;
$sub-menu_width: 210px !default;
$sub-menu_background-color: c.$white !default;

/* stylelint-disable-next-line */
$sub-menu_background-color--hover: rgba(189, 188, 188, 0.8) !default; // color.mix(rgba($sub-menu__menu-item_color, .6), $sub-menu_background-color);
$sub-menu_background-color--active: #959495 !default; // color.mix($sub-menu__menu-item_color, $sub-menu_background-color);
$sub-menu__menu-item_color: c.$base-900 !default;
$sub-menu__menu-item_color--hover: c.$primary !default;
$sub-menu__menu-item_background--hover: c.$base-200 !default;
$sub-menu__menu-item_border-color: c.$base-300 !default;
$sub-menu__menu-item--emphasized_color: c.$primary !default;
$sub-menu__menu-item--emphasized_background: c.$base-900 !default;
$megamenu_background-color: $sub-menu_background-color !default;
$megamenu_border-color: c.$base-300 !default;
$megamenu__menu-item_color: c.$base-900 !default;
$megamenu__overlay_color: c.$base-900 !default;
$megamenu__menu-item--emphasized_color: c.$white !default;

.ac-nav-collapse {
	display: block;
	width: auto;
	flex: 1;
	position: fixed;
	left: -100vw;
	bottom: 0;
	top: 0;
	order: 1;
	padding-top: 0;
	transition: left .3s;
	overflow: hidden;

	@media (min-width: g.$breakpoint-l) {
		display: flex;
		justify-content: center;
		order: 0;
		position: static;
		left: initial;
		top: initial;
		bottom: initial;
		transition: none;
		overflow: visible;

		&:before,
		&:after {
			content: none;
			transition: none;
		}
	}

	&:before {
		content: "";
		background-color: transparent;
		opacity: .8;
		position: fixed;
		z-index: -1;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		pointer-events: none;
		transition: background-color .3s;

		@media (min-width: g.$breakpoint-l) {
			display: none;
		}
	}

	&.mobile-open {
		left: 0;

		@media (min-width: g.$breakpoint-l) {
			left: initial;
		}

		&:before {
			background-color: $mobilemenu__overlay_color;
		}

		&:after {
			opacity: 1;
		}
	}

	.inner-menu-wrapper {
		background-color: $mobilemenu__background-color;
		padding: 1.25em 0;
		overflow: visible;
		width: 85vw;
		height: 100vh;
		overflow-y: auto;

		@media (min-width: g.$breakpoint-l) {
			background-color: transparent;
			padding: 0;
			width: auto;
			height: auto;
			overflow-y: visible;

			.site-branding {
				display: none;
			}
		}

		.site-branding {
			padding: 0 2rem;
			margin-bottom: 1.5em;
		}

		.site-branding img {
			height: 100%;
			max-height: h.$height;
			width: auto;
		}
	}

	ul:not(.top-header-mobile) {
		flex-flow: column;
		list-style: none;

		@media (min-width: g.$breakpoint-l) {
			flex-flow: initial;
		}
	}

	.nav-menu {
		display: flex;
		flex-direction: column;
		padding: 0 0 .9375em;
		margin: 0;

		@media (min-width: g.$breakpoint-l) {
			padding: 0;
			flex-direction: row;
		}

		.menu-item {
			float: none;
			padding: $menu-item_padding;
			border-bottom: .5px solid $sub-menu__menu-item_border-color;

			@media (min-width: g.$breakpoint-l) {
				padding: $menu-item_padding--desktop;
				border: 0;
			}

			&:last-child {
				border: 0;
			}
		}

		.menu-item > a {
			flex: 1;
			padding: 0 0 0 2rem;
			font-family: $font;
			font-weight: $menu-item_font-weight;
			line-height: 1.56;
			letter-spacing: $menu-item_letter_spacing;
			text-transform: $menu-item_text_transform;
			color: $menu-item_color;
			@include tool.font-size( $menu-item_font-size );

			@media (min-width: g.$breakpoint-l) {
				letter-spacing: $menu-item_letter_spacing_breakpoint-l;
				display: inline-flex;
				align-items: center;
				line-height: 1.46;
				text-transform: $menu-item_text_transform_breakpoint-l;
				padding: 0;
				@include tool.font-size( $menu-item_font-size_breakpoint-l );
			}

			@media (max-width: g.$breakpoint-l) {
				padding: 1.5rem 2rem;
			}

			&:hover,
			&:focus {
				color: $menu-item_color--hover;
			}
		}

		.current-page-ancestor.megamenu,
		.current-menu-item.megamenu {

			@media (min-width: g.$breakpoint-l) {
				&:after {
					content: "";
					border-radius: 5px 5px 0 0;
					display: block;
					width: 100%;
					height: 4px;
					background-color: $sub-menu_background-color;
				}
			}
		}

		.menu-item.current-menu-item > a {
			color: $sub-menu__menu-item_color--hover;
		}

		.menu-item.menu-opened > a {
			color: $sub-menu__menu-item_color--hover;

			@media (max-width: g.$breakpoint-l) {
				color: $sub-menu__menu-item_color--hover;
				opacity: .5;
				padding: 1.5rem 0;
				width: 100%;
			}
		}

		.show-sub-menu .sub-menu-depth-0,
		.show-sub-menu.sub-menu-depth-0,
		.show-sub-menu.sub-menu-depth-1 {
			display: block;
		}

		.sub-menu-depth-0,
		.sub-menu-depth-1 {

			@media (min-width: g.$breakpoint-l) {
				letter-spacing: t.$letter-spacing;
				line-height: 1.5;
				font-weight: 500;
				display: none;
				position: absolute;
				top: 100%;
				opacity: 0;
				box-shadow: 0 10px 10px -1px rgba(50 50 50 / 10%);
				@include tool.font-size(12);
			}

			@media (max-width: g.$breakpoint-l) {
				flex: 1;
				padding: 0 2rem;
				font-family: $sub-menu_font;
				font-weight: 600;
				line-height: 1.56;
				letter-spacing: t.$letter-spacing;
				color: $menu-item_color;
				@include tool.font-size(15);
			}
		}

		.sub-menu-depth-0 > .menu-item,
		.sub-menu-depth-1 > .menu-item {

			@media (min-width: g.$breakpoint-l) {
				padding: 0;
				border-bottom: 1px solid $sub-menu__menu-item_border-color;
			}
		}

		> .menu-item:last-of-type {

			@media (min-width: g.$breakpoint-l) {
				border-bottom: 0;
			}
		}

		.btn {
			align-self: center;

			@media (max-width: g.$breakpoint-l - 1) {
				align-self: center;
				margin: 1.5rem 0;
				text-align: center;
				width: calc(100% - 4rem);
			}
		}

		.btn a {
			color: inherit;
		}
	}

	// general style for .menu-item
	.menu-item {
		display: block;
		position: relative;
		border-bottom: 0;
		padding: 0;

		@media (min-width: g.$breakpoint-l) {
			padding: 0;
			border-bottom: solid 1px $sub-menu__menu-item_border-color;
		}

		&:last-of-type {
			border-bottom: 0;
		}

		> a {
			display: block;
			text-decoration: none;
			white-space: nowrap;

			@media (min-width: g.$breakpoint-l) {
				padding: 0 24px;
			}
		}
	}

	// Specific style for top level menu items
	.nav-menu > .menu-item {

		@media (min-width: g.$breakpoint-l) {
			display: flex;
			flex-direction: column;
			height: 100%;
			transition: height .4s linear;

			@if $menu-item__after_is-enabled {
				&.current-menu-item:after {
					content: "";
					display: block;
					border-radius: $menu-item__after_border-radius;
					margin: $menu-item__after_margin;
					width: $menu-item__after_width;
					height: $menu-item__after_height;
					background-color: $menu-item__after_background-color;
				}
			}
		}
	}

	.nav-menu > .menu-item > a {
		position: relative;
		transition: color .2s linear, background-color .2s linear;

		@media (min-width: g.$breakpoint-l) {
			padding: $menu-item__a_padding;
		}
	}

	// Hover and active style for top level menu item
	.nav-menu > .menu-item:hover > a {
		color: $menu-item_color--top-level-hover;

		@media (min-width: g.$breakpoint-l) {
			background-color: $menu-item_background-color--top-level-hover;
		}
	}

	// Toggle button for sub-menus
	.toggle-sub-menu {
		transition: none;
		padding: .75em;
		line-height: 1;
		cursor: pointer;
		display: block;
		background-color: transparent;
		font-weight: 300;
		border: 0;
		overflow: hidden;
		width: auto;
		@include tool.font-size(24);

		@media (min-width: g.$breakpoint-l) {
			display: none;
		}

		&:focus {
			border: 0;
			box-shadow: none;

			&:before {
				content: "";
				position: absolute;
				width: 110%;
				height: .5px;
				top: 100%;
				right: 0;
			}
		}

		&:after {
			display: inline-block;
			content: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='iso-8859-1'%3F%3E%3Csvg version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='20' height='15' viewBox='0 0 284.929 284.929' style='enable-background:new 0 0 284.929 284.929;' xml:space='preserve'%3E%3Cg%3E%3Cpath d='M282.082,76.511l-14.274-14.273c-1.902-1.906-4.093-2.856-6.57-2.856c-2.471,0-4.661,0.95-6.563,2.856L142.466,174.441 L30.262,62.241c-1.903-1.906-4.093-2.856-6.567-2.856c-2.475,0-4.665,0.95-6.567,2.856L2.856,76.515C0.95,78.417,0,80.607,0,83.082 c0,2.473,0.953,4.663,2.856,6.565l133.043,133.046c1.902,1.903,4.093,2.854,6.567,2.854s4.661-0.951,6.562-2.854L282.082,89.647 c1.902-1.903,2.847-4.093,2.847-6.565C284.929,80.607,283.984,78.417,282.082,76.511z'/%3E%3C/g%3E%3C/svg%3E%0A");
			color: $menu-item_color;
			transform: rotate(270deg);
		}

		&:active:after,
		&:active:focus:after,
		&:focus:after,
		&:hover:after {
			background-color: transparent;
			border: 0;
			color: $menu-item_color;
		}

		&.sub-menu-open {
			&:after {
				content: url("data:image/svg+xml,%3Csvg  width='24' height='21' viewBox='0 0 24 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 12L5 12' stroke='%23252D3A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12 19L5 12L12 5' stroke='%23252D3A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
				transform: rotate(360deg);
			}
		}
	}

	// general style for .sub-menu
	.sub-menu {
		display: none;
		padding-left: 0;
		font-family: $font;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 1.46;
		letter-spacing: t.$letter-spacing--large;
		color: #323031;
		@include tool.font-size(11);

		@media (min-width: g.$breakpoint-l) {
			z-index: 10;
			background-color: $sub-menu_background-color;
			font-family: $sub-menu_font;
			text-transform: none;
			min-width: $sub-menu_width;
			font-weight: 300;
			box-shadow: 0 2px 4px 0 rgba(0 0 0 / 7%);
			@include tool.font-size(17);
		}

		.menu-item > a {

			@media (min-width: g.$breakpoint-l) {
				text-transform: none;
			}
		}

		.sub-menu {

			@media (min-width: g.$breakpoint-l) {
				display: block;

				li.current-menu-item a {
					text-decoration: underline;
				}

				a {
					line-height: 3.06;
					letter-spacing: t.$letter-spacing;

					&:hover,
					&:focus {
						text-decoration: underline;
					}
				}
			}
		}
	}

	// Regular dropdowns (hover for sub menus)
	.nav-menu > .menu-item:not(.megamenu):hover .sub-menu-depth-0 {

		@media (min-width: g.$breakpoint-l) {
			display: block;
			opacity: 1;
			visibility: visible;
		}
	}

	.nav-menu > .menu-item.megamenu:hover .sub-menu-depth-0 {

		@media (min-width: g.$breakpoint-l) {
			display: flex;
		}
	}

	.nav-menu .sub-menu-depth-0 > .menu-item:not(.megamenu):hover .sub-menu-depth-1 {

		@media (min-width: g.$breakpoint-l) {
			display: block;
			opacity: 1;
			visibility: visible;
			top: 0;
			pointer-events: all;
		}
	}

	// General style for top menu inside ac-nav-collapse (acts as a secondary menu for mobile)
	.top-menu {

		@media (min-width: g.$breakpoint-l) {
			// Dont show this on larger screens.
			display: none;
		}
	}

	.secondary-menu {
		display: flex;
		height: auto;
		padding: 0;
		flex-flow: column;
		list-style: none;
		margin-top: 10px;

		.menu-item {
			display: block !important; // stylelint-disable-line declaration-no-important
			margin-bottom: 0;
		}

		.menu-item > a {
			color: $sub-menu__menu-item_color;
			padding: .3rem 2rem;
			@include tool.font-size(14);

			@media (min-width: g.$breakpoint-l) {
				@include tool.font-size(16);
			}

			&:hover,
			&:focus {
				color: $sub-menu__menu-item_color--hover;
			}
		}
	}

	.menu-login-link,
	.secondary-menu {

		@media (min-width: g.$breakpoint-l) {
			display: none;
		}
	}
}

// Toggles between open and closed when hamburger menu is pressed
.mobile-nav-toggle {
	svg {
		display: block;

		&:last-child {
			display: none;
		}
	}
}

.menu-icon-toggle {
	svg {
		display: none;

		&:last-child {
			display: block;

			path {
				stroke: c.$white;
			}
		}
	}
}

// Sub-menus

// stylelint-disable-next-line no-duplicate-selectors
.ac-nav-collapse .nav-menu {
	.sub-menu a:hover {

		@media (min-width: g.$breakpoint-l) {
			background: $sub-menu__menu-item_background--hover;
			color: $sub-menu__menu-item_color--hover;
		}
	}

	// Sub menu depth 0 (1)
	.sub-menu-depth-0 .menu-item {
		> a {
			letter-spacing: t.$letter-spacing;
			line-height: 1.5;
			font-weight: 300;
			@include tool.font-size(16);

			@media (min-width: g.$breakpoint-l) {
				padding: 1.25em 1.75em;
				width: 100%;
				font-weight: normal;
				@include tool.font-size(15);
			}
		}
	}

	.sub-menu-depth-0 .menu-opened {
		> a {
			color: $menu-item_color--current;
		}
	}

	// Sub menu depth 1 (2)
	.sub-menu-depth-1 {
		padding-left: 1.25em;

		@media (min-width: g.$breakpoint-l) {
			display: block;
			position: absolute;
			top: 0;
			left: 100%;
			margin: 0;
			padding: 0;
			pointer-events: none;
		}
	}

	.sub-menu-depth-1 .menu-item {

		@media (min-width: g.$breakpoint-l) {
			padding: 0;
		}

		> a {
			font-weight: normal;
			text-transform: none;
			@include tool.font-size(14);

			@media (min-width: g.$breakpoint-l) {
				padding: 1.25em 1.75em;
				width: 100%;
				@include tool.font-size(15);
			}
		}
	}
}

// Top menu inside mobile nav

// stylelint-disable-next-line no-duplicate-selectors
.ac-nav-collapse .top-menu {
	display: none;
	text-align: left;

	.menu-item {
		padding: .5em 0;
	}

	li:before {
		display: none;
	}

	a {
		color: $menu-item_color;
	}
}

.admin-bar .ac-nav-collapse.mobile-open {
	.menu-opened {
		margin-top: 46px;
		height: calc(100vh - 46px);
	}
}

// Mobile open specific styling

// stylelint-disable-next-line no-duplicate-selectors
.ac-nav-collapse.mobile-open {
	z-index: 10;

	.site-branding {
		display: none;
	}

	/* stylelint-disable max-nesting-depth */
	.menu-login-link {
		margin: 0 2rem 20px;
		text-align: right;
		display: flex;

		a {
			color: $menu-item_color;
			display: flex;
			align-items: center;
			margin-left: auto;

			&:hover,
			&:focus {
				color: $menu-item_color--hover;
			}
		}

		svg {
			margin-right: 6px;
		}
	}

	.nav-menu.nav-menu-mobile {
		.menu-item {
			border: 0;
			border-top: 1px solid $sub-menu__menu-item_border-color;

			> a {
				font-weight: 400;
				padding: 1rem 2rem;
				white-space: normal;
			}
		}

		.menu-item.menu-opened > a {
			color: $menu-item_color;
			font-weight: 600;
			opacity: 1;
			text-align: left;
			padding: unset;
			display: flex;
			align-items: center;

			&:hover,
			&:focus {
				color: $menu-item_color--hover;
			}
		}
	}

	/* stylelint-enable */
	.menu-item {
		display: flex;
		flex-wrap: wrap;
	}

	.nav-menu > .menu-item:hover > a {
		color: $menu-item_color--hover;
	}

	.menu-opened {
		align-content: flex-start;
		width: 85vw;
		position: absolute;
		background: $mobilemenu__background-color;
		top: 0;
		bottom: 0;
		overflow-y: scroll;
		overflow-x: hidden;
		z-index: 10;
		animation: menu-sequence .2s;

		> a {
			top: 0;
			text-align: center;
			padding: 0 0 24px;
		}

		> .toggle-sub-menu {
			position: relative;
			order: -1;
		}

		// Adding flexboxes to all the elements to achieve full page layout
		.show-sub-menu {
			display: flex;
			flex-direction: column;
		}

		.show-sub-menu > ul {
			display: flex;
		}

		div.emphasized-nav-buttons {
			position: absolute;
			bottom: 0;
			width: 100%;
			z-index: 10;
			padding-bottom: 24px;
		}

		.sub-menu-depth-2,
		.sub-menu-depth-3 {
			margin-top: 0;
		}

		.show-sub-menu li.menu-opened {
			width: 85vw;
			position: fixed;
			top: 0;
			background: $mobilemenu__background-color;
			overflow-y: scroll;
			height: calc(100vh - 83px);
		}

		& ~ li {
			display: none !important; // stylelint-disable-line declaration-no-important
		}
	}

	.sub-menu-container,
	.sub-menu-depth-0,
	.sub-menu-depth-1,
	.sub-menu-depth-2,
	.sub-menu-depth-3 {
		flex-basis: 100%;
		width: 100%;
	}

	.nav-menu .menu-item.menu-opened {
		padding-bottom: 0;
		display: flex;
		flex-wrap: wrap;
	}

	.nav-menu .sub-menu-depth-0 {
		background-color: $sub-menu_background-color;
		padding: .35rem 0;

		> .menu-item {
			padding: 0;
		}
	}

	.nav-menu .sub-menu-depth-1 {
		padding-left: 0;
		padding-top: 1rem;
		padding-bottom: 1rem;

		@media (max-width: g.$breakpoint-l) {
			padding: 0;
		}

		> .menu-item {
			padding: 0;

			@media (min-width: g.$breakpoint-l) {
				padding: .5rem 2rem;
			}
		}

		> .menu-item > a {

			@media (min-width: g.$breakpoint-l) {
				padding: 0;
			}
		}
	}
}

// End .mobile-open

// Mega menu
.ac-nav-collapse .nav-menu .megamenu {
	// Everything related to mega menu should only occur on larger screens.
	@media (min-width: g.$breakpoint-l) {
		position: static;

		.sub-menu {
			min-width: 0;
		}

		.sub-menu-container {
			display: none;
			pointer-events: none;
			transition: opacity .3s ease-in-out;
			background: $megamenu_background-color;
			position: absolute;
			left: 0;
			right: 0;
			top: calc(100% - 1px);
			height: auto;
			border-top: 1px solid $megamenu_border-color;
		}

		.search-open & .sub-menu-container {

			@media (min-width: g.$breakpoint-l) {
				top: calc(100% - search.$height--desktop);
			}
		}

		// Sub menu depth 0 (1)
		&.open .sub-menu-container,
		&:hover .sub-menu-container {
			opacity: 1;
			pointer-events: all;
			display: flex;

			.sub-menu-depth-0 {
				display: flex;
				flex-direction: row;
				position: static;
				align-items: flex-start;
			}
		}

		.sub-menu-depth-0 {
			display: flex;
			padding-top: 4.2em;
			padding-bottom: 4.2em;
			width: 100%;
			justify-content: center;
			visibility: visible;
			opacity: 1;
			position: relative;

			.sub-menu,
			.menu-item a {
				transition: none;
			}
		}

		.sub-menu-depth-0 > .menu-item {
			padding: 0 2.5rem;
			border-bottom: 0;
		}

		.sub-menu-depth-0 > .menu-item > a {
			background: none;
			padding: 0;
			color: $megamenu__menu-item_color;
			font-weight: 600;
			line-height: 1.43;
			letter-spacing: t.$letter-spacing;
			text-transform: uppercase;
			@include tool.font-size(14);
		}

		.sub-menu-depth-0 > .menu-item > a:after {
			display: none;
		}

		// Sub menu depth 1 (2)
		.sub-menu-depth-1 {
			display: block;
			position: static;
			opacity: 1;
			visibility: visible;
			padding: 1.5em 0;
			box-shadow: none;
		}

		ul.sub-menu.sub-menu-depth-2,
		ul.sub-menu.sub-menu-depth-3,
		ul.sub-menu.sub-menu-depth-4 {
			box-shadow: unset;
			padding: 0 15px;
		}

		ul.sub-menu.sub-menu-depth-3,
		ul.sub-menu.sub-menu-depth-4 {
			box-shadow: unset;
			padding: 0 15px;
		}

		.sub-menu-depth-2,
		.sub-menu-depth-3,
		.sub-menu-depth-4 {
			.menu-item {
				a {
					background: none;
					padding: 0;

					&:hover,
					&:focus {
						background: none;
					}
				}
			}
		}

		.sub-menu-depth-1 > .menu-item {
			padding: .5em 0;
			border: 0;
		}

		.sub-menu-depth-1 > .menu-item > a {
			color: $megamenu__menu-item_color;
			background: none;
			padding: 0;
			@include tool.font-size(16);
		}
	}

	&.menu-item-has-children > a:after {
		display: none;
	}

	.sub-menu {

		@media (min-width: g.$breakpoint-l) {
			display: block;

			&.sub-menu-depth-0 {
				box-shadow: none;
				display: flex;
				max-height: 80vh;
				overflow: auto;
			}
		}
	}

	.sub-menu-depth-0 {
		@extend .container--fluid;
	}

	.sub-menu-depth-1 {
		background-color: transparent;
	}
}

// Emphasized nav menu item
.site-header .ac-nav-collapse .nav-menu .sub-menu .emphasized-menu-item {
	a {
		font-weight: 300;
		line-height: 2;
		letter-spacing: t.$letter-spacing;
		color: $sub-menu__menu-item--emphasized_color;
		text-decoration: underline;
		width: auto;
		@include tool.font-size(15);

		@media (min-width: g.$breakpoint-l) {
			font-size: .75rem;
			letter-spacing: t.$letter-spacing--large;
			color: $sub-menu__menu-item--emphasized_color;
			text-transform: uppercase;
			border-bottom: 1.5px solid $sub-menu__menu-item--emphasized_color;
			text-decoration: none;
			font-weight: 700;
		}

		&:hover,
		&:focus {
			text-decoration: none;
		}
	}
}

// Emphasized nav menu item for depth 0
.site-header .ac-nav-collapse .nav-menu .sub-menu-depth-0 > .emphasized-menu-item {
	display: none; // This enables and disables Emphasized-menu-item on mobile

	@media (min-width: g.$breakpoint-l) {
		display: block;
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		background: $sub-menu__menu-item--emphasized_background;
		text-align: center;
		padding: 0;

		a {
			font-weight: 600;
			padding: 1.43em 0;
			line-height: 1.43;
			letter-spacing: t.$letter-spacing;
			color: $sub-menu__menu-item--emphasized_color;
			text-transform: uppercase;
			border: 0;
			width: 100%;
			justify-content: center;
			@include tool.font-size(14);
		}
	}
}

.site-header .ac-nav-collapse .nav-menu .sub-menu-depth-0 .emphasized-nav-buttons .emphasized-menu-item {

	@media (max-width: g.$breakpoint-l) {
		display: block;
		left: 0;
		right: 0;
		background: $megamenu_background-color;
		text-align: center;
		margin: 0 2rem;
		padding: 0;

		a {
			font-weight: 600;
			padding: 1.43em 0;
			line-height: 1.43;
			letter-spacing: t.$letter-spacing;
			color: $sub-menu__menu-item--emphasized_color;
			text-decoration: none;
			border: 0;
			width: 100%;
			@include tool.font-size(14);
		}
	}
}

// Emphasized nav menu item for depth 0 in megamenu
.site-header .ac-nav-collapse .nav-menu .megamenu .sub-menu-depth-0 > .emphasized-menu-item {

	@media (min-width: g.$breakpoint-l) {
		a {
			color: $megamenu__menu-item--emphasized_color;
			position: relative;

			&:after {
				display: inline-block;
				content: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 12H19' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12 5L19 12L12 19' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
				height: 24px;
				padding-left: 10px;
			}
		}
	}
}

// Hide menu images if it's not a megamenu or if it's not a menu item on depth 0
.ac-nav-collapse .nav-menu .menu-item-image,
.ac-nav-collapse .nav-menu .megamenu .menu-item-with-image .menu-item-image {
	display: none;
}

// Menu item with image
.ac-nav-collapse .nav-menu .megamenu .sub-menu-depth-0 > .menu-item-with-image,
.ac-nav-collapse .nav-menu .megamenu .sub-menu-depth-0 .emphasized-nav-buttons > .menu-item-with-image {
	display: flex;
	flex-direction: column;

	> a {
		font-weight: 700;
		font-style: normal;
		line-height: 1.53;
		text-transform: none;
		letter-spacing: t.$letter-spacing;
		text-decoration: underline;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;

		@include tool.font-size(15);

		@media (min-width: g.$breakpoint-l) {
			@include tool.font-size(17);
		}
	}

	.menu-item-image-button {
		display: block;
		width: 100%;
		padding: 1rem 1.5rem;
		text-align: center;
	}

	&.accent a {
		color: $sub-menu__menu-item_color;

		.menu-item-image-button {
			background: $sub-menu_background-color;

			&:hover,
			&:focus {
				background-color: $sub-menu_background-color--hover;
			}

			&:active {
				background-color: $sub-menu_background-color--active;
			}
		}

		&:hover,
		&:focus {
			opacity: .8;
		}

		&:active {
			opacity: .6;
		}
	}

	& a {
		color: $sub-menu__menu-item_color;

		.menu-item-image-button {
			background: $sub-menu_background-color;

			&:hover,
			&:focus {
				background-color: $sub-menu_background-color--hover;
			}

			&:active {
				background-color: $sub-menu_background-color--active;
			}
		}

		.menu-item-image {
			&:hover,
			&:focus {
				opacity: .8;
			}

			&:active {
				opacity: .6;
			}
		}
	}

	.menu-item-image-wrap {
		width: 100%;
		background-position: 50%;
		background-size: cover;
		height: 95px;
		margin-bottom: 15px;

		@media (min-width: g.$breakpoint-l) {
			margin-bottom: 10px;
			height: auto;
			background-image: none !important; // stylelint-disable-line declaration-no-important
		}
	}

	.menu-item-image {
		display: none;
		margin-left: auto;
		margin-right: auto;

		@media (min-width: g.$breakpoint-l) {
			display: block;
		}
	}

	.toggle-sub-menu {
		display: none !important; // stylelint-disable-line declaration-no-important
	}
}

// Menu with only a mobile image.
.ac-nav-collapse .nav-menu .megamenu .sub-menu-depth-0 > .menu-item-with-mobile-image {
	> a {
		font-weight: 700;
		font-style: normal;
		line-height: 1.53;
		text-transform: none;
		letter-spacing: t.$letter-spacing;
		text-decoration: underline;
		width: 100%;
		@include tool.font-size(15);

		@media (min-width: g.$breakpoint-l) {
			background: none;
			padding: 0;
			font-weight: 600;
			line-height: 1.43;
			letter-spacing: t.$letter-spacing;
			text-transform: uppercase;
			text-decoration: none;
			@include tool.font-size(14);
		}
	}

	.toggle-sub-menu {
		align-self: flex-end;
	}

	.menu-item-image-wrap {
		width: 100%;
		background-position: 50%;
		background-size: cover;
		height: 95px;
		margin-bottom: 15px;

		@media (min-width: g.$breakpoint-l) {
			margin-bottom: 0;
			height: 0;
			background-image: none !important; // stylelint-disable-line declaration-no-important
		}

		img {

			@media (min-width: g.$breakpoint-l) {
				max-width: 300px;
			}
		}
	}
}

.site-header.js-is-sticky--change .ac-nav-collapse .nav-menu > .menu-item,
.site-header.js-is-stuck .ac-nav-collapse .nav-menu > .menu-item {

	@media (min-width: g.$breakpoint-l) {
		transition: height .4s linear;
		height: h.$height--sticky;
	}

	> a {
		padding: 1.5rem 2rem;

		@media (min-width: g.$breakpoint-l) {
			padding: $menu-item__a_padding--collapsed;
		}
	}

	&:after {
		transform: translateY(1px);
	}
}

@keyframes menu-sequence {
	0% {
		transform: translateX(-100%);
	}

	100% {
		transform: translateX(0);
	}
}

.site-header-right-column > .mobile-toggle {
	z-index: 10;
}

ul.nav-menu-desktop.nav-menu {
	display: none;

	@media (min-width: g.$breakpoint-m) {
		display: flex;
	}

	&.nav-no-menu-mobile {

		@media (min-width: g.$breakpoint-m) {
			display: flex;
		}
	}
}

.nav-menu-mobile {
	&.nav-menu {
		display: flex;
		height: auto;
		padding: 0;

		@media (min-width: g.$breakpoint-m) {
			display: none;
		}
	}

	button {
		&:active,
		&:active:focus {
			background-color: c.$secondary-100;
		}
	}
}
