@use "Settings/grid"as g;
@use "Settings/colors"as c;

@use "Tools"as tool;

.information-bar-wrapper {
	padding: 16px 0 18px;

	@media (min-width: g.$breakpoint-m) {
		padding: 20px 0;
	}

	&.light {
		background-color: c.$light-scheme__background-color;
		color: c.$light-scheme__text-color;
	}

	&.dark {
		background-color: c.$dark-scheme__background-color;
		color: c.$dark-scheme__text-color;
	}

	img {
		max-height: 16px;

		@media (min-width: g.$breakpoint-m) {
			max-height: 20px;
			margin-right: g.$gutters * .5;
		}
	}

	p {
		margin: 0;
		text-align: center;

		@media (min-width: g.$breakpoint-m) {
			text-align: left;
		}
	}

	small {

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size(14);
		}
	}
}
