@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;

@use "Tools"as tool;

@use "Elements/links" as l;

.ac-module-shop-categories {
	.shop-categories-wrapper {
		@include tool.standard-vertical-padding;

		&.light {
			background-color: c.$light-scheme__background-color;
		}

		&.dark {
			background-color: c.$dark-scheme__background-color;
		}
	}

	.shop-category-item-link {
		display: block;
		max-width: 85%;
		margin-left: auto;
		margin-right: auto;
		background: c.$white;
		box-shadow: 0 2px 10px 0 rgb(0 0 0 / 12%);
		transform: translateY(-50%);
		border: 0;
		color: t.$color__content;
		line-height: 1;
		letter-spacing: t.$letter-spacing--large;
		padding: 16px;
		font-weight: 400;
		@include tool.font-size(11);

		@media (min-width: g.$breakpoint-m) {
			@include tool.font-size(13);
		}

		&:hover,
		&:focus {
			color: l.$color--hover;
		}

		&.arrow-style-button {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		// This is the arrow.
		&.arrow-style-button svg {
			transition: transform .2s linear;
		}

		&.arrow-style-button svg path {
			fill: l.$color;
		}
	}

	.col-m-6 .shop-category-item-link {
		padding: 26px;
	}

	.shop-category-item:hover .arrow-style-button svg {
		transform: translateX(5px);
		transition: transform .2s linear;
	}
}
