@use "sass:color";

@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;
@use "Settings/z-index" as z;

@use "Tools"as tool;

$font-family: t.$font__heading !default;
$font-weight: 300 !default;
$line-height: 2 !default;
$letter-spacing: t.$letter-spacing !default;
$font-size: 13 !default;
$usp-bar__text-align: center !default;
$usp-bar__text-color: c.$white !default;
$usp-bar__background-color: c.$primary-600 !default;
$usp-bar__gap: 40px !default;
$usp-bar__usp-label-padding-left: 10px !default;
$usp-bar__usp-icon-color: $usp-bar__text-color !default;
$usp-bar__usp-icon-width: 20px !default;
$usp-bar__usp-icon-height: 20px !default;
$font-family--column: $font-family !default;
$font-weight--column: $font-weight !default;
$line-height--column: $line-height !default;
$letter-spacing--column: t.$letter-spacing !default;
$font-size--column: $font-size !default;
$usp-bar__padding--column: 0 !default;
$usp-bar__padding-desktop--column: 30px !default;
$usp-bar__background-color--column: transparent !default;
$usp-bar__text-color--column: #000 !default;
$usp-bar__usp-icon-color--column: $usp-bar__text-color--column !default;
$usp-bar__usp-padding--column: 0 !default;
$usp-bar__usp-margin--column: .5rem 0 !default;

.usp-bar {
	position: relative;
	z-index: z.$above-overlay;
	background-color: $usp-bar__background-color;
	overflow-x: hidden;

	.usp-container {
		display: flex;
		height: 40px;
		align-items: center;
		justify-content: center;
	}

	ul.usp-bar__list {
		list-style: none;
		margin: 0;
		display: flex;
		flex-flow: row nowrap;
		justify-content: flex-start;
		align-items: center;
		text-align: $usp-bar__text-align;
		padding: 0;

		@media (min-width: g.$breakpoint-l) {
			flex-wrap: wrap;
			justify-content: center;
			gap: $usp-bar__gap;
		}

		&.swiper-wrapper {

			@media (min-width: g.$breakpoint-l) {
				width: auto;
			}
		}

		li {
			display: flex;
			align-items: center;
			justify-content: center;
			font-family: $font-family;
			font-weight: $font-weight;
			line-height: $line-height;
			letter-spacing: $letter-spacing;
			padding: 0;
			list-style: none;
			@include tool.font-size( $font-size );

			&:before {
				display: none;
			}
		}

		li.swiper-slide {

			@media (min-width: g.$breakpoint-l) {
				width: auto;
			}
		}

		li.no-icon > span {
			padding-left: 0;
		}
	}

	svg {
		width: $usp-bar__usp-icon-width;
		min-width: $usp-bar__usp-icon-width;
		height: $usp-bar__usp-icon-height;

		path {
			fill: $usp-bar__usp-icon-color;
		}
	}

	span {
		color: $usp-bar__text-color;
		padding-left: $usp-bar__usp-label-padding-left;
	}
}

.usp-bar--column {
	background-color: $usp-bar__background-color--column;

	ul.usp-bar__list {
		text-align: left;
		background-color: $usp-bar__background-color--column;
		padding: $usp-bar__padding--column;

		@media (min-width: g.$breakpoint-m) {
			padding: $usp-bar__padding-desktop--column;
			flex-direction: column;
			align-items: flex-start;
		}

		li {
			display: flex;
			padding: $usp-bar__usp-padding--column;
			margin: $usp-bar__usp-margin--column;
			font-family: $font-family--column;
			font-weight: $font-weight--column;
			line-height: $line-height--column;
			letter-spacing: $letter-spacing--column;
			@include tool.font-size( $font-size--column );
		}
	}

	svg path {
		fill: $usp-bar__usp-icon-color--column;
	}

	span {
		color: $usp-bar__text-color--column;
	}
}

.secondary-menu {
	display: block;
	padding: 1em 2em;
	margin: unset;
	list-style: none;
	flex: 1;
	justify-content: flex-end;

	@media (min-width: g.$breakpoint-l) {
		display: none;
	}

	li {
		margin-bottom: 10px;

		a {
			color: c.$black;
			font-weight: 300;
			@include tool.font-size( $font-size );
		}
	}

	&.desktop {
		display: none;
		padding: unset;

		@media (min-width: g.$breakpoint-l) {
			display: flex;
		}

		li {
			margin-bottom: unset;

			&:not(:last-child) {
				margin-right: 20px;
			}
		}

		a {
			color: c.$white;

			&:hover,
			&:focus {
				text-decoration: underline;
			}
		}
	}
}
