@use "Settings/colors" as c;

// Disabled button
$disabled_color: c.$base-500 !default;
$disabled_background-color: c.$base-200 !default;
$disabled_border-color: $disabled_background-color !default;

// Standard button ( .btn ). Inherited to other buttons.
$font-size: 14 !default;
$font-size--desktop: 14 !default;
$font-weight: 500 !default;
$line-height: 1.1 !default;
$letter-spacing: 0 !default;
$text-transform: none !default;
$padding: 9px 12px !default;
$background-color: c.$primary !default;
$background-color--hover: c.$primary-400 !default;
$background-color--active: c.$primary-600 !default;
$background-color--disabled: $disabled_background-color !default;
$background-color--dark-mode: c.$primary-300 !default;
$background-color--dark-mode-hover: c.$primary-400 !default;
$background-color--dark-mode-active: c.$primary__dark-48 !default;
$color: c.$white !default;
$color--hover: c.$white !default;
$color--active: c.$white !default;
$color--dark-mode: c.$base-1000 !default;
$border-color: $background-color !default;
$border-color--hover: c.$primary-400 !default;
$border-color--active: c.$primary-600 !default;
$border-color--disabled: $disabled_border-color !default;
$border-color--dark-mode: c.$primary-300 !default;
$border-color--dark-mode-hover: c.$primary-400 !default;
$border-color--dark-mode-active: c.$primary__dark-48 !default;
$border-radius: 3px !default;
$border--full_thickness: 2px !default;
$border--full_thickness--medium: 2px !default;
$border--full_thickness--large: 2px !default;
$border--bottom_thickness: 2px !default;
$outlin-color: c.$success-300 !default;
$outlin-color-2: c.$alerts-success-48 !default;
$outlin-size: 3px !default;

// Button sizes
$font-size-btn_medium: 16 !default;
$font-size-btn_large: 18 !default;
$padding-btn_medium: 12px 24px !default;
$padding-btn_large: 12px 32px !default;
$line-height-btn_medium: 1.1 !default;
$line-height-btn_large: 1.1 !default;

// used calculate-rem so no px
$btn-arrow-size: 16 !default;
$btn-arrow-margin-horizontal: 18 !default;
$btn-arrow-size-btn_medium: 19 !default;
$btn-arrow-margin-horizontal-btn_medium: 24 !default;
$btn-arrow-size-btn_large: 22 !default;
$btn-arrow-margin-horizontal-btn_large: 32 !default;

// Button type 2 ( .btn-type-2 ) ( .secondary ) ** Secondary
$type-2_background-color: c.$primary-100 !default;
$type-2_border-color: c.$primary-100 !default;
$type-2_color: c.$primary-600 !default;
$type-2_background-color--hover: c.$primary-200 !default;
$type-2_border-color--hover: c.$primary-200 !default;
$type-2_color--hover: c.$primary-600 !default;
$type-2_background-color--active: c.$primary-500 !default;
$type-2_border-color--active: c.$primary-500 !default;
$type-2_color--active: c.$white !default;
$type-2_background-color--disabled: c.$base-100 !default;
$type-2_border-color--disabled: c.$base-100 !default;
$type-2_color--disabled: c.$base-500 !default;
$type-2_background-color--dark-mode: c.$primary-500-opacity-8 !default;
