$type-2_color--dark-mode-active: c.$primary-600 !default;
$type-2_background-color--dark-mode-disabled: c.$base-900 !default;
$type-2_border-color--dark-mode-disabled: c.$base-900 !default;
$type-2_color--dark-mode-disabled: c.$white-opacity-12 !default;

