@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;
@use "sass:color";

@use "Tools" as tool;

$color: c.$white !default;
$background_color: c.$primary-600 !default;
$link_color--hover: c.$white !default;
$input_color: c.$white !default;
$label_color: c.$base-300 !default;
$label_color--hover: c.$white !default;
$input_background_color: transparent !default;
$input_background_color--active: rgb(201 201 201 / 20%) !default;
$input_border_color: rgb(255 255 255 / 20%) !default;
$input_border_color--active: c.$white !default;
$link__icon_fill: c.$white !default;
$link__icon_fill--hover: $link__icon_fill !default;
$copyright_color: c.$white !default;
$copyright_border-color: c.$white !default;
$contact__svg_fill: c.$white !default;
$newsletter_border-color: c.$white !default;
$newsletter__submit-button_color: c.$black !default;
$newsletter__submit-button_color--hover: c.$white !default;
$newsletter__submit-button_background-color: c.$white !default;
$newsletter__submit-button_background-color--hover: $link_color--hover !default;
$newsletter__submit-button_border-color: $newsletter__submit-button_background-color !default;
$newsletter__submit-button_border-color--hover: $newsletter__submit-button_background-color--hover !default;
$newsletter__heading-color: c.$white !default;
$newsletter__heading-font-size: 24 !default;
$newsletter__heading-font-weight: lighter !default;
$newsletter__heading-text-transform: none !default;
$newsletter__heading-letter-spacing: 0 !default;
$link_transition: color .2s ease-in-out !default;
$payment_gateway_icon_links_padding: 12px !default;
$payment_gateway_icon_links_padding--desktop: 12px 0 !default;
$payment_gateway_icon_width: 32px !default; // use unset to use the image width
$payment_gateway_icon_height: 32px !default; // use unset to use the image height
$social_media_button_background_color: c.$white !default;
$social_media_button_svg_fill_color: c.$black !default;
$social_media_button_background_color--hover: transparent !default;
$social_media_button_svg_fill_color--hover: c.$white !default;
$social_media_button_width: 32px !default;
$social_media_button_height: 32px !default;
$social_media_button_svg_width: 18px !default;
$social_media_button_svg_height: 18px !default;
$footer_heading-font-weight: 600 !default;
$footer_heading-font-size: 11 !default;
$footer_heading-font-size-breakpoint-m: 13 !default;
$footer_heading-text-transform: uppercase !default;
$footer_heading-color: $color !default;

.site-footer {
	background-color: $background_color;
	color: $color;

	.grid {
		align-items: flex-start;
	}

	p,
	a {
		@include tool.font-size(14);

		@media (min-width: g.$breakpoint-m) {
			@include tool.font-size(15);
		}
	}

	a {
		color: $color;

		&:hover,
		&:focus {
			color: $link_color--hover;
			text-decoration: underline;
		}
	}

	p a {
		text-decoration: none;
	}
}

.footer-content {
	padding-top: tool.calculate-rem(25);
	padding-bottom: tool.calculate-rem(25);

	@media (min-width: g.$breakpoint-s) {
		padding-top: tool.calculate-rem(45);
		padding-bottom: tool.calculate-rem(45);
	}

	@media (min-width: g.$breakpoint-m) {
		padding-top: tool.calculate-rem(100);
		padding-bottom: tool.calculate-rem(80);
	}

	.footer-logo {
		margin-bottom: 2rem;
		text-align: left;
	}
}

.footer-heading {
	color: $footer_heading-color;
	line-height: 1.45;
	letter-spacing: t.$letter-spacing--large;
	font-weight: $footer_heading-font-weight;
	text-transform: $footer_heading-text-transform;
	margin-top: 0;
	@include tool.font-size($footer_heading-font-size);

	@media (min-width: g.$breakpoint-m) {
		@include tool.font-size($footer_heading-font-size-breakpoint-m);
	}
}

.footer-logo {
	margin-bottom: 10px;
	display: block;

	img {
		max-width: 160px;
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			max-width: 142px;
		}
	}
}

.footer-about-text {
	text-align: left;
	margin-bottom: 2.5em;

	a {
		transition: $link_transition;
	}
}

.footer-contact {
	.footer-heading {
		margin-top: 30px;
	}

	span {
		display: flex;
		align-items: center;
		width: 100%;
		margin-bottom: .46em;

		&:last-child {
			margin-bottom: 0;
		}
	}

	span svg {
		margin-right: 12px;
		max-width: 22px;
	}

	span svg path {
		fill: $contact__svg_fill;
	}

	a {
		color: $color;

		&:hover,
		&:focus {
			color: $link_color--hover;
			text-decoration: underline;
		}
	}

	span a {
		color: $color;
		display: flex;
		align-items: center;
		transition: $link_transition;

		&:hover,
		&:focus {
			color: $link_color--hover;
			text-decoration: underline;
		}
	}

	&.hide-icons span {
		margin-bottom: 1em;

		svg {
			display: none;
		}
	}
}

.footer-menu-wrapper {
	padding: 0;

	ul {
		list-style: none;
		padding-left: 0;
		display: flex;
		flex-flow: column nowrap;
		margin-bottom: 0;
	}

	ul ul {
		flex-direction: column;
	}

	ul ul li {
		width: 100%;
		flex-basis: 100%;
	}

	ul li {
		margin-bottom: 7px;
		width: auto;
		flex-basis: 100%;
	}

	ul li a {
		color: $color;
		transition: $link_transition;
		@include tool.font-size(13);

		@media (min-width: g.$breakpoint-m) {
			@include tool.font-size(15);
		}

		&:hover,
		&:focus {
			color: $link_color--hover;
			text-decoration: underline;
		}
	}
}

.footer-newsletter {
	a {
		text-decoration: underline;
	}

	input[type="submit"] {
		background-color: $newsletter__submit-button_background-color;
		color: $newsletter__submit-button_color;

		&:hover,
		&:focus {
			color: $newsletter__submit-button_color--hover;
		}
	}

	input[type="text"],
	input[type="email"],
	input[type="url"],
	input[type="tel"],
	textarea {
		background-color: $input_background_color;
		border-color: $input_border_color;
		margin-block-end: 1rem;
		color: $input_color;
		@include tool.font-size(14);
	}

	input[type="text"]::placeholder,
	input[type="email"]::placeholder,
	input[type="url"]::placeholder,
	input[type="tel"]::placeholder,
	textarea::placeholder {
		color: c.$base-300;
	}

	input[type="text"]:focus,
	input[type="email"]:focus,
	input[type="url"]:focus,
	input[type="tel"]:focus,
	textarea:focus {
		background-color: $input_background_color;
		outline: none;
	}

	input[type="text"]:focus,
	input[type="email"]:focus,
	input[type="url"]:focus,
	input[type="tel"]:focus,
	textarea:focus,
	input[type="text"]:active,
	input[type="email"]:active,
	input[type="url"]:active,
	input[type="tel"]:active,
	textarea:active {
		background-color: $input_background_color--active;
		border-color: $input_border_color--active;
	}

	input[type="text"]:not(:placeholder-shown),
	input[type="email"]:not(:placeholder-shown),
	input[type="url"]:not(:placeholder-shown),
	input[type="tel"]:not(:placeholder-shown),
	textarea:not(:placeholder-shown) {
		border-color: $input_border_color--active;
	}

	label {
		color: $label_color;
		display: inline-block;
		margin-bottom: 6px;
		@include tool.font-size(14);
	}

	.footer-heading {
		color: $newsletter__heading-color;
		font-size: tool.calculate-rem($newsletter__heading-font-size);
		font-weight: $newsletter__heading-font-weight;
		text-transform: $newsletter__heading-text-transform;
		letter-spacing: $newsletter__heading-letter-spacing;
	}
}

/**
 * Add this class if you want to have labels which float upwards when the input
 * is active/focused. The input and labels need to be inside their own .field-group
 * parent with input then label e.g. <div class="field-group"><input><label></div>.
 * The footer background-color should match the input background-color too,
 * so $input_background_color--active is not used here
 */

.footer-newsletter--fancy-labels {
	input[type="text"],
	input[type="email"],
	input[type="url"],
	input[type="tel"],
	textarea {
		padding: tool.calculate-rem(10) tool.calculate-rem(42.68) tool.calculate-rem(10) tool.calculate-rem(18.68);
		position: relative;
		z-index: 1;
		margin-block-end: 0;
	}

	input[type="text"]::placeholder,
	input[type="email"]::placeholder,
	input[type="url"]::placeholder,
	input[type="tel"]::placeholder,
	textarea::placeholder {
		opacity: 0;
	}

	input[type="text"]:focus,
	input[type="email"]:focus,
	input[type="url"]:focus,
	input[type="tel"]:focus,
	textarea:focus input[type="text"]:active,
	input[type="email"]:active,
	input[type="url"]:active,
	input[type="tel"]:active,
	textarea:active {
		background-color: $input_background_color;

		&::placeholder {
			opacity: 1;
		}
	}

	label {
		transform: translate(.75rem, -2.25rem);
		padding: 0 .5rem;
		margin: 0;
		transition: transform .2s ease-in-out;
		background-color: $background_color;
	}

	.field-group input:active ~ label,
	.field-group input:focus ~ label,
	.field-group input:not(:placeholder-shown) ~ label {
		color: $label_color--hover;
		position: relative;
		z-index: 2;
		transform: translate(.75rem, -3.75rem);
	}

	.tc-label {
		transform: none;
		padding-bottom: 8px;
		color: c.$white;
		display: flex;
		align-items: center;
		column-gap: 5px;
		margin-top: -6px;

		input {
			margin-right: 5px;
		}
	}

	.form-submit-right {
		position: relative;

		input[type="email"] {
			padding-right: tool.calculate-rem(120);
		}

		input[type="submit"] {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 24.5px;
			z-index: 3;
			padding: tool.calculate-rem(10) tool.calculate-rem(18);
			border-color: transparent;
			border-radius: 0;

			&:hover,
			&:focus,
			&:active {
				border-color: c.$white;
				background-color: c.$primary-500;
			}
		}

		input[type="checkbox"] {
			transform: none;
		}
	}
}

.footer-social-media {

	@media (min-width: g.$breakpoint-m) {
		order: 2;
	}
}

.social-media-buttons,
.payment-gateway-icons {
	list-style: none;
	padding-left: 0;
	display: flex;
	margin: 0;
	align-items: center;
	width: 100%;
	flex-wrap: wrap;
	line-height: 0;

	@media (min-width: g.$breakpoint-m) {
		li + li {
			margin-left: 2.5rem;
		}
	}

	li + li {
		margin-left: 1rem;
	}

	a svg path {
		fill: $link__icon_fill;
	}

	a:hover svg path {
		fill: $link__icon_fill--hover;
	}
}

.social-media-buttons {

	@media (min-width: g.$breakpoint-l) {
		justify-content: center;
	}
}

.payment-gateway-icons {
	justify-content: center;

	@media (min-width: g.$breakpoint-l) {
		justify-content: flex-end;
	}

	a {
		display: inline-block;
		padding: $payment_gateway_icon_links_padding;

		@media (min-width: g.$breakpoint-l) {
			padding: $payment_gateway_icon_links_padding--desktop;
		}
	}

	img {
		max-width: 100px;
		width: $payment_gateway_icon_width;
		height: $payment_gateway_icon_height;
	}

	li {
		display: flex;
		align-items: center;
		margin-bottom: 0;
	}

	.payment-gateway-item > span > svg {
		width: $payment_gateway_icon_width;
		height: $payment_gateway_icon_height;

		// min-height: $payment_gateway_icon_height;
	}
}

.footer-payment-social {
	display: flex;
	flex-direction: column;
	padding: tool.calculate-rem(20) 0;

	@media (min-width: g.$breakpoint-m) {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding-top: tool.calculate-rem(10);
	}

	div + div {
		margin-top: 1.5rem;

		@media (min-width: g.$breakpoint-m) {
			margin-top: 0;
		}
	}
}

.footer-copyright {
	color: $copyright_color;
	letter-spacing: t.$letter-spacing;

	.container {
		display: flex;
		justify-content: space-between;
		padding-bottom: g.$gutters-smallscreen-horizontal;
	}

	.footer-logo {
		margin-left: auto;
	}

	.footer-logo img {
		max-width: 70px;
		opacity: .2;
	}

	.aclp-toggle {
		margin: 0;
		padding: 0;
		border: 0;
		text-transform: none;
		font-weight: 300;
	}
}

.footer-column {
	&--menus {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 0;
	}

	&--menus > .col-m-6 {
		flex-basis: 100%;
		max-width: 100%;
		padding-bottom: 0;
	}

	&--menus .col-m-6,
	&--newsletter .col-m-6 {
		padding-left: 0;
	}

	&--menus-2 > .col-m-6 {
		flex-basis: 50%;
		max-width: 50%;
	}
}

.social-media-button a {
	background-color: $social_media_button_background_color;
	border-radius: 50%;
	color: c.$white;
	display: flex;
	justify-content: center;
	align-items: center;
	width: $social_media_button_width;
	height: $social_media_button_height;
	transition: background-color .2s ease-in-out;

	svg {
		height: $social_media_button_svg_width;
		width: $social_media_button_svg_height;
	}

	svg path {
		fill: $social_media_button_svg_fill_color;
		transition: fill .2s ease-in-out;
	}

	&:hover,
	&:focus {
		background-color: $social_media_button_background_color--hover;

		svg path {
			fill: $social_media_button_svg_fill_color--hover;
		}
	}
}

.footer-bottom-wrapper {
	width: 100%;
	border-top: 1px solid $copyright_border-color;
	padding-top: g.$gutters-smallscreen-horizontal;

	@media (min-width: g.$breakpoint-l) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	.footer-bottom-column {

		@media (min-width: g.$breakpoint-l) {
			flex-basis: 33.33%;
		}
	}

	.column {
		&--country-selector {
			display: flex;
			justify-content: center;
			margin-bottom: g.$gutters-smallscreen-horizontal;

			@media (min-width: g.$breakpoint-l) {
				display: block;
				margin-bottom: 0;
			}
		}

		&--country-selector .footer-country-selector {
			background-color: transparent;
			border-color: $input_border_color;

			/* stylelint-disable max-nesting-depth */
			span,
			img {
				width: 20px;
				height: 20px;
			}

			img {
				margin-right: 10px;
			}

			&:hover,
			&:focus,
			&:active {
				background-color: transparent;
				border-color: c.$white;
			}

			/* stylelint-enable */
		}

		&--info {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;

			@media (min-width: g.$breakpoint-l) {
				display: block;
			}
		}

		&--info .site-info {
			font-size: tool.calculate-rem(13);
			font-weight: 300;
			padding: 1rem 0;
			margin: 0;
			text-align: center;

			@media (min-width: g.$breakpoint-l) {
				padding: 0;
			}
		}
	}

	.info--legal-links {

		@media (min-width: g.$breakpoint-l) {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			margin-bottom: 6px;
		}

		a {
			font-size: tool.calculate-rem(13);
			font-weight: 300;
			transition: $link_transition;

			&:after {
				content: "|";
				display: inline-block;
				margin: 0 4px;
			}

			&:last-child:after {
				display: none;
			}
		}
	}
}
