////
/// Generic animations used throughout the project.
/// Component specific animations should instead live in the respective component.
/// @example How to include these animations in your component
/// 	@use "Objects/animations";
///		.animate-this {
/// 		animate: slide-right .2s forwards;
/// 	}
/// @group Animations
////

@keyframes slide-right {
	from {
		transform: translateX(100%);
	}

	to {
		transform: translateX(0%);
	}
}
