@use "Settings/colors" as c;
@use "Settings/typography" as t;
@use "Settings/grid" as g;

@use "Tools" as tool;

@use "Components/forms" as form;

.nf-form-title > * {
	margin-top: unset;
}

// Styling for all inputs
div.nf-form-content {
	input,
	input:not([type="button"]),
	input:not([type="button"]):focus,
	textarea,
	textarea:focus,
	select {
		background: transparent;
		border-color: form.$input_border-color;
		border-radius: form.$input_border-radius;
		border-width: form.$input_border-thickness;
		font-family: t.$font__inter;
		letter-spacing: t.$letter-spacing;
		font-weight: form.$input_font-weight;
		line-height: 1.5;
		color: c.$base-1000;
		resize: none;
		@include tool.font-size(form.$input_font-size);

		&:focus {
			border: form.$input_border-thickness solid c.$base-1000;
		}

		&::placeholder {
			color: c.$base-600;
		}
	}

	.nf-form-title h3 {
		margin-top: unset;
	}

	.nf-field-container {
		margin-bottom: 8px;
	}

	.terms-conditions {
		margin-top: 15px;
		margin-bottom: 30px;

		.checkbox-wrap {
			flex-wrap: nowrap;
			display: flex;
		}

		.nf-field-label {
			display: none;
		}

		.nf-field-element {
			order: 0;
			width: auto;
			margin-right: 5px;
			align-items: center;
			display: flex;
		}

		.nf-field-description {
			margin-right: unset;
		}

		.nf-field-description a,
		.nf-field-description p {
			margin-bottom: unset;
			font-size: 14px;
			line-height: 1.3;
		}
	}

	.align-right {

		@media (min-width: 600px) {
			text-align: right;
		}
	}

	// Override border
	.nf-form-content input:not([type="button"]) {
		border: form.$input_border;
	}

	// Input error background color
	.nf-error input,
	.nf-error input:not([type="button"]),
	.nf-error input:not([type="button"]):focus,
	.nf-error textarea {
		background: form.$input_background-color--error;
	}

	// Input pass background color
	.nf-pass input,
	.nf-pass input:not([type="button"]),
	.nf-pass input:not([type="button"]):focus,
	.nf-pass textarea {
		background: form.$input_background-color--pass;
	}

	// Error message styling
	.nf-error-wrap .nf-error-msg {
		padding-top: tool.calculate-rem(8);
	}

	// Inputs except checkboxes etc.
	input,
	input:not([type="button"]),
	textarea,
	.list-select-wrap .nf-field-element > div {
		padding: form.$input_padding;
	}

	// Styling for select input
	.list-select-wrap .nf-field-element > div {
		background: c.$base-200;
		border-color: form.$input_border-color;
	}

	// Styling labels
	label {
		font-family: t.$font__inter;
		font-weight: form.$label_font-weight;
		line-height: 1.375;
		letter-spacing: t.$letter-spacing;
		color: form.$input_color;
		height: 24px;
		@include tool.font-size(form.$input_font-size);
	}

	// For the image field dont set a height
	.listimage-wrap {
		ul {
			padding: 0;
		}

		label {
			height: auto;
		}
	}

	.label-above .nf-field-label {
		margin-bottom: tool.calculate-rem(8);
	}

	// Multi select
	.list-multiselect-wrap select {
		&.ninja-forms-field:focus {
			border: form.$checkbox_border;
		}
	}

	// Select and the country selector
	.list-select-wrap > div,
	.listcountry-wrap > div {
		border-radius: 2px;
		overflow: hidden;

		select {
			background-color: c.$base-200;
			color: c.$base-700;
			font-family: t.$font__inter;
			letter-spacing: t.$letter-spacing;
			font-weight: form.$input_font-weight;
			line-height: 1.5;
			border: 0;
			appearance: none;
			@include tool.font-size(form.$input_font-size);

			&:focus {
				background-color: c.$base-400;
			}
		}
	}

	// Select arrow svg
	.list-select-wrap div.nf-field-element,
	.listcountry-wrap div.nf-field-element {
		position: relative;

		&:after {
			content: "";
			display: block;
			position: absolute;
			right: tool.calculate-rem(20);
			top: 50%;
			transform: translateY(-50%);
			width: 12px;
			height: 7px;
			z-index: 10;
			pointer-events: none;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			background-image: url("data:image/svg+xml,%0A%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.92589 0.300254C8.73133 0.304849 8.54649 0.386234 8.41173 0.526639L5.00348 3.93488L1.59523 0.526639C1.52656 0.455922 1.4444 0.399703 1.35361 0.361312C1.26283 0.32292 1.16526 0.303136 1.06668 0.303132C0.920067 0.303168 0.776796 0.346946 0.655199 0.428864C0.533603 0.510783 0.439213 0.627117 0.384104 0.762982C0.328996 0.898848 0.315676 1.04807 0.345849 1.19154C0.376022 1.33502 0.448315 1.46623 0.55348 1.56839L4.4826 5.49752C4.62077 5.63562 4.80813 5.71321 5.00348 5.71321C5.19884 5.71321 5.38619 5.63562 5.52436 5.49752L9.45348 1.56839C9.56062 1.46557 9.63418 1.33277 9.66453 1.18742C9.69487 1.04206 9.68059 0.890921 9.62355 0.75382C9.56651 0.616719 9.46938 0.500053 9.34488 0.419115C9.22039 0.338178 9.07434 0.296747 8.92589 0.300254Z' fill='%2348484A'/%3E%3C/svg%3E%0A");
		}
	}

	// Submit & button
	input[type="button"],
	button {
		@include tool.btn;
	}
}

// Required field text
.nf-form-fields-required {
	margin-bottom: .75rem;
}

// Default padding on the form wrapper
.nf-form-cont {
	padding-block: 2rem;

	.five-sixths,
	.four-sixths,
	.one-fourth,
	.one-half,
	.one-sixth,
	.one-third,
	.three-fourths,
	.three-sixths,
	.two-fourths,
	.two-sixths,
	.two-thirds {

		@media (max-width: 600px) {
			width: 100% !important; // stylelint-disable-line declaration-no-important
			margin-left: unset !important; // stylelint-disable-line declaration-no-important
		}
	}
}

// Input error
.nf-error .ninja-forms-field {
	/* stylelint-disable-next-line */
	border-color: form.$input_border-color--error!important; // Because NF uses important
}

// Input error and pass icon
div.nf-error.field-wrap .nf-field-element:after,
div.nf-pass.field-wrap .nf-field-element:after {
	display: none;
}

// Input error
.nf-pass .ninja-forms-field {
	/* stylelint-disable-next-line */
	border-color: form.$input_border-color--pass!important; // Because NF uses important
}

// Error text under input styling
.nf-error-msg.nf-error-required-error {
	font-family: t.$font__inter;
	color: form.$input_border-color--error;
	font-weight: form.$input_font-weight;
	line-height: 1.4;
	letter-spacing: t.$letter-spacing;
	@include tool.font-size(14);
}

// Fieldset
.nf-repeater-fieldsets fieldset {
	border-style: solid;
	border-width: form.$input_border-thickness;
	padding: 1.5rem;
}

.nf-form-content button.nf-remove-fieldset,
button.nf-remove-fieldset {
	font-size: 2.5rem;
	height: 44px;
	width: 44px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: c.$base-1000;
	background-color: transparent;
	padding: 0;
	margin-top: -1.5rem;
	margin-right: -.75rem;

	&,
	&:hover,
	&:focus {
		border-color: transparent;
	}
}

// Single Checkbox
.checkbox-container.label-right .nf-field-label {
	padding-left: .27rem;

	label {
		height: 23px;
	}
}

// Multi checkbox
.listcheckbox-wrap .nf-field-element label {
	margin-left: 1.3rem;
}

// Form submit error
.nf-error-msg.nf-error-field-errors {
	margin-top: .75rem;
}

// Class: form--two-columns set on a column or group element to get special layout for contact us
.form--two-columns {
	.nf-form-cont {
		padding-inline: tool.calculate-rem(16);

		@media (min-width: g.$breakpoint-s) {
			padding-inline: tool.calculate-rem(40);
		}
	}

	div.nf-form-content nf-fields-wrap {
		flex-direction: row;
	}

	div.nf-form-content nf-fields-wrap nf-field {
		width: 100%;

		@media (min-width: g.$breakpoint-m) {
			width: calc(50% - 1rem);
		}

		&:nth-of-type(5) {
			width: 100%;
		}

		&:nth-of-type(6) {
			width: max-content;

			.label-right .nf-field-description {
				margin-right: 0;
			}
		}

		// Submit button
		&:last-of-type {
			width: fit-content;
			margin-left: auto;
		}
	}

	.submit-container {
		width: fit-content;
	}
}
