@use "Settings/colors" as c;
@use "Tools" as tool;

$text-stock__color: c.$success !default;
$text-stock__color--out-of-stock: c.$alert !default;
$text-stock__font-size: 14 !default;
$text-stock__margin--bottom: 20 !default;

.woocommerce div.product {
	.stock-wrapper {
		display: flex;
		align-items: center;
		margin-bottom: tool.calculate-rem($text-stock__margin--bottom);
		flex-wrap: wrap;
	}

	p.stock {
		margin: 0;
		font-weight: 500;
		color: $text-stock__color;
		width: 90%;
		@include tool.font-size( $text-stock__font-size );

		&.out-of-stock {
			color: $text-stock__color--out-of-stock;
		}
	}

	.stock-wrapper svg {
		width: 14px;
		height: 14px;
		margin-right: 9px;

		path {
			fill: $text-stock__color;
		}
	}

	.stock-wrapper.out-of-stock svg path {
		fill: $text-stock__color--out-of-stock;
	}
}
