@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;
@use "Elements/links" as l;

@use "Tools" as tool;

$sale-label_font-family: t.$font__heading !default;
$sale-label_background-color: c.$primary !default;
$sale-label_color: c.$white !default;
$sale-label_border-radius: 0 !default;
$sale-label_border-color: $sale-label_background-color !default;
$sale-label_top: 1.15em !default;
$sale-label_right: auto !default;
$sale-label_bottom: auto !default;
$sale-label_left: 1.15em !default;
$sale-label_padding: .3em .77em !default;
$sale-label_font-weight: 500 !default;
$sale-label_letter-spacing: t.$letter-spacing--large !default;
$sale-label_font-size: 14 !default;
$sale-label_font-size--desktop: 15 !default;
$sale-label_font-size--breakpoint-xl: 16 !default;
$img_border: c.$base-100 !default;
$title_font-weight: 500 !default;
$title_text-transform: none !default;
$title_font-size: 14 !default;
$title_font-size--desktop: 15 !default;
$title_font-size--breakpoint-xl: 16 !default;
$price_color: c.$base-900 !default;
$price_color--sale: c.$alert !default;
$price_font-size: 14 !default;
$price_font-size--desktop: 15 !default;
$price_font-size--breakpoint-xl: 16 !default;

.woocommerce ul.products li.product {
	font-family: $sale-label_font-family;

	span.onsale {
		border-radius: $sale-label_border-radius;
		top: $sale-label_top;
		right: $sale-label_right;
		bottom: $sale-label_bottom;
		left: $sale-label_left;
		margin: 0;
		min-height: auto;
		min-width: auto;
		line-height: normal;
		padding: $sale-label_padding;
		display: inline-table;
		font-weight: $sale-label_font-weight;
		color: $sale-label_color;
		background-color: $sale-label_background-color;
		letter-spacing: $sale-label_letter-spacing;
		@include tool.font-size( $sale-label_font-size );

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size( $sale-label_font-size--desktop );
		}

		@media (min-width: g.$breakpoint-xl) {
			@include tool.font-size( $sale-label_font-size--breakpoint-xl);
		}
	}

	a img {
		border: 1px solid $img_border;
		margin-bottom: .125em;
	}

	.woocommerce-loop-product__title,
	.woocommerce-loop-category__title {
		font-weight: $title_font-weight;
		text-transform: $title_text-transform;
		@include tool.font-size( $title_font-size );

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size( $title_font-size--desktop );
		}

		@media (min-width: g.$breakpoint-xl) {
			@include tool.font-size( $title_font-size--breakpoint-xl );
		}
	}

	.price {
		color: $price_color;
		display: flex;
		justify-content: flex-start;
		@include tool.font-size( $price_font-size );

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size( $price_font-size--desktop );
		}

		@media (min-width: g.$breakpoint-xl) {
			@include tool.font-size( $price_font-size--breakpoint-xl );
		}

		ins {
			text-decoration: none;
		}

		ins .woocommerce-Price-amount {
			color: $price_color--sale;
			letter-spacing: t.$letter-spacing--large;
		}

		.woocommerce-Price-currencySymbol {
			margin-left: 3px;
		}

		del .woocommerce-Price-amount {
			color: inherit;
			letter-spacing: t.$letter-spacing--large;
		}

		del {
			color: $price_color;
			margin-left: .5em;
			opacity: 1;
		}

		.woocommerce-Price-amount {
			color: $price_color;
			font-family: t.$font__content;
			letter-spacing: t.$letter-spacing--large;
			font-weight: 300;
		}
	}

	a.added_to_cart {
		margin-left: 5px;
		@include tool.font-size( $sale-label_font-size--desktop );
	}

	.woocommerce-loop-product__image_wrap {
		height: 0;
		width: 100%;
		padding-bottom: 100%;
		position: relative;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			top: 0;
			left: 0;
			position: absolute;
		}
	}
}

// overwrite WooCommerce CSS:
@media (max-width: 480px) {
	.wc-block-grid.has-3-columns .wc-block-grid__products {
		display: flex;
	}
}

.wc-block-grid ul.wc-block-grid__products li.wc-block-grid__product {
	font-family: $sale-label_font-family;
	text-align: left;
	margin: 0 0 tool.calculate-rem(24);

	.wc-block-grid__product-onsale {
		border-radius: $sale-label_border-radius;
		border-color: $sale-label_border-color;
		top: $sale-label_top;
		right: $sale-label_right;
		bottom: $sale-label_bottom;
		left: $sale-label_left;
		margin: 0;
		position: absolute;
		min-height: auto;
		min-width: auto;
		line-height: normal;
		padding: $sale-label_padding;
		display: inline-table;
		font-weight: $sale-label_font-weight;
		color: $sale-label_color;
		background-color: $sale-label_background-color;
		letter-spacing: $sale-label_letter-spacing;
		@include tool.font-size( $sale-label_font-size );

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size( $sale-label_font-size--desktop );
		}
	}

	a img {
		border: 1px solid $img_border;
		margin-bottom: .125em;
	}

	.wc-block-grid__product-title,
	.wc-block-grid__category-title,
	.woocommerce-loop-product__title {
		font-weight: $title_font-weight;
		text-transform: $title_text-transform;
		color: c.$base-800;
		@include tool.font-size( $title_font-size );

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size( $title_font-size--desktop );
		}

		a {
			color: c.$base-800;
		}
	}

	.wc-block-grid__product-price {
		color: $price_color;
		display: flex;
		justify-content: flex-start;
		font-weight: 300;
		@include tool.font-size( 11 );

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size( 13 );
		}

		ins {
			color: $price_color--sale;
			text-decoration: none;
		}

		ins .woocommerce-Price-amount {
			color: $price_color--sale;
			letter-spacing: t.$letter-spacing--large;
		}

		.woocommerce-Price-currencySymbol {
			margin-left: 3px;
		}

		del .woocommerce-Price-amount {
			color: inherit;
			letter-spacing: t.$letter-spacing--large;
		}

		del {
			color: $price_color;
			margin-left: .5em;
			opacity: 1;
		}

		.woocommerce-Price-amount {
			color: $price_color;
			font-family: t.$font__content;
			letter-spacing: t.$letter-spacing--large;
			font-weight: 300;
		}

		.subscription-details {
			text-transform: none;
			letter-spacing: t.$letter-spacing;
			padding-left: 4px;
		}
	}
}

.wc-block-grid__product .wc-block-grid__product-add-to-cart,
.wc-block-grid__product .wc-block-grid__product-onsale,
.wc-block-grid__product .wc-block-grid__product-price,
.wc-block-grid__product .wc-block-grid__product-rating {
	margin: 1em 0 0;
}

// Overlay image on product card
.product-card__images {
	position: relative;
	overflow: hidden;
}

.product-card__overlay-image {
	position: absolute;
	width: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	transition: opacity .15s ease-in .15s, visibility .2s ease-in .15s;
	overflow: hidden;

	// Only act on the hover if the visitor is using a device with hover.
	@media (hover: hover) and (pointer: fine) {
		&.hover {
			opacity: 1;
			pointer-events: auto;
			visibility: visible;
			transition: opacity .15s ease-in .15s, visibility .2s ease-in .15s;

			.attachment-woocommerce_thumbnail {
				transition: transform .15s ease-in .15s;
				transform: scale(1.05);
			}
		}
	}

	.attachment-woocommerce_thumbnail {
		transition: transform .15s ease-in .15s;
		transform: scale(1);
		object-fit: cover;
	}
}

.wc-block-grid__products {
	.wc-block-grid__product-image {
		text-decoration: none;
		display: block;
		position: relative;

		img {
			height: auto;
			width: 100%;
			max-width: 100%;
		}
	}
}

.wc-block-grid__products + .wc-block-components-pagination {
	margin: 0 auto tool.calculate-rem(32);
	text-align: center;

	.wc-block-components-pagination__page {
		width: 46px;
		height: 46px;
		border-radius: 50%;
		margin: 0 tool.calculate-rem(4);
		transition: all .2s ease-in-out;
		border: 2px solid transparent;
		color: t.$color__content;

		&:hover,
		&:focus {
			background-color: l.$color;
			color: c.$white;
			border-color: l.$color;
		}

		/* stylelint-disable declaration-no-important */
		&:disabled {
			color: inherit !important;
			background-color: transparent !important;
			border-color: transparent !important;
		}

		&--active,
		&--active:disabled {
			color: inherit !important;
			background-color: transparent !important;
			border-color: l.$color !important;
		}

		&--active[disabled] {
			font-weight: 400;
		}

		/* stylelint-enable */
	}
}

.wc-block-grid .wc-block-grid__products.swiper-wrapper {
	flex-wrap: nowrap;
}

.wc-block-grid__product .wc-block-grid__product-image:not(.wc-block-components-product-image),
.wc-block-grid__product .wc-block-grid__product-title {
	margin: 0 0 tool.calculate-rem(12);
}

/* stylelint-disable declaration-no-important */
.add-to-cart-button-bottom {
	li.product {
		display: flex !important;
		flex-direction: column;
		align-items: flex-start;
		height: auto;

		.woocommerce-loop-product__link {
			display: inline-block;
			margin-bottom: 1em; // can't do calc(auto + 1em) below, so give this margin-bottom
		}

		.button {
			margin-top: auto !important;
		}
	}

	.wc-block-grid__product {
		display: flex !important;
		flex-direction: column;
		align-items: flex-start;
		height: auto;

		.wc-block-grid__product-add-to-cart {
			padding-top: 1em;
			margin-top: auto;
		}
	}
}

/* stylelint-enable */
