@use "Settings/grid"as g;

@use "Tools"as tool;

$container--woo_max-width: tool.calculate-rem(1240) !default;
$entry-header__entry-title_margin-top: tool.calculate-rem(30) !default;

.standard-page-container {
	max-width: 100%;
	padding: unset;
}

.single-page {
	.entry-header .entry-title {
		margin-top: $entry-header__entry-title_margin-top;
	}
}

.woocommerce-page .single-page .container {
	max-width: $container--woo_max-width;
}
