@use "Settings/grid"as *;
@use "Settings/colors"as colors;

[class*="grid-slider"] {
	position: relative;

	.swiper-initialized & {
		justify-content: unset;
	}

	// Make sure that if this element is a grid we change the display from flex to block.
	&.grid {
		display: block;
	}

	[class*="col-"] {
		max-width: 81%;
	}
}

// Breakpoints available to the slider. Fetches the values defined in _grid.scss
$map-gridslider-breakpoints: (
	"-s": $grid_breakpoint-s,
	"-m": $grid_breakpoint-m,
	"-l": $grid_breakpoint-l,
);

// Destroy the flickity slider back into a regular grid on the specified modifier breakpoint.
@each $modifier,
	$breakpoint in $map-grid-breakpoints {
	.grid-slider#{ $modifier } {

		@media screen and (min-width: $breakpoint * 1px) {
			&.grid {
				display: flex;
			}

			[class*="col-"] {
				max-width: none;
			}
		}
	}

	// Also fix the surrounding containers padding to allow fullwidth slider.
	.slider-container#{ $modifier } {
		position: relative;

		@media screen and (max-width: calc($breakpoint - 1) * 1px) {
			&.container {
				padding: 0;
			}

			.grid {
				margin: 0;
			}
		}

		@media screen and (min-width: $breakpoint * 1px) {
			&:before {
				display: none;
			}
		}

		// Creates the right hand fade of content.
		&:before {
			content: "";
			height: 100%;
			width: 19%;
			background-image: linear-gradient(to right, transparent, colors.$light-scheme__background-color);
			position: absolute;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 50;

			.dark & {
				background-image: linear-gradient(to right, transparent, colors.$dark-scheme__background-color);
			}
		}

		&.swiper-initialized {
			overflow: hidden;
		}
	}
}
