@use "Settings/colors" as c;
@use "Settings/typography" as typography;
@use "Settings/grid"as g;

@use "Tools" as tool;

@use "Components/navigation/menu" as menu;

$icon_color: menu.$menu-item_color !default;
$icon_color--hover: $icon_color !default;
$count_color: c.$white !default;
$count_background-color: c.$primary-500 !default;
$multimarket_text-color: typography.$color__content !default;

.site-header-right-column {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding-right: 5px;

	button,
	&__button,
	&__button button,
	&__button .cart-button {
		padding: 0;
		border: 0;
		background-color: transparent;
		max-width: 100%;
		align-items: center;

		&:not(.mobile-nav-toggle) {
			display: flex;
		}

		&:hover,
		&:active,
		&:active:focus,
		&:active:focus:hover,
		&:focus,
		&:focus-within,
		&:visited {
			border: 0;
			background-color: transparent;
		}

		&:focus {
			outline: none;
		}

		svg {
			height: 24px;
			width: auto;

			@media (min-width: g.$breakpoint-l) {
				height: 20px;
			}

			@media (min-width: g.$breakpoint-xl) {
				height: 24px;
			}
		}

		button,
		svg,
		svg path {
			fill: $icon_color;

			&:hover,
			&:hover path {
				fill: $icon_color--hover;
			}
		}

		.cart-button {
			min-width: 100%;
		}

		.count {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 20px;
			width: 20px;
			border-radius: 50%;
			background-color: $count_background-color;
			color: $count_color;
			font-weight: 700;
			@include tool.font-size(11);
		}
	}

	.qala-multimarket-open-selector-button {
		color: $multimarket_text-color;
	}

	&__button {
		margin-left: 15px;

		@media (min-width: g.$breakpoint-xl) {
			margin-left: 20px;
		}
	}

	& > div {
		display: flex;

		&:first-child {
			.site-header-right-column__button {
				margin-left: unset;
			}
		}
	}

	.qala-wishlist-header .qala-wishlist-button svg {
		height: 24px;
		width: auto;

		@media (min-width: g.$breakpoint-l) {
			height: 20px;
		}

		@media (min-width: g.$breakpoint-xl) {
			height: 24px;
		}
	}

	.search-icon-hide {
		display: none;

		&.search-icon-show-mob {
			display: flex;

			@media (min-width: g.$breakpoint-l) {
				display: none;
			}
		}
	}
}
