@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;
@use "Settings/spacing"as s;

@use "Tools"as tool;

$site-main_padding: 0 0 s.$standard-vertical-spacing--desktop 0 !default;

// ====================
// ====== HEADER ======
// ====================
$header_background-color: c.$primary-variant--botticelli !default;
$header__container_display: flex !default;
$header__container_justify-content: space-between !default;
$header__container_justify-content--desktop: flex-start !default;
$header__container_align-items: center !default;
$header__h1_font-size: 20 !default;
$header__h1_font-size--desktop: 28 !default;
$header__h1_color: c.$primary-variant--chambray !default;
$header__h1_font-weight: 500 !default;
$header__h1_font-family: t.$font__content !default;
$header__h1_margin: 0 !default;
$header__h1_padding: 1rem 0 !default;
$header__h1_padding--desktop: 2.5rem 0 !default;
$header__span_font-size: 12 !default;
$header__span_font-size--desktop: 10 !default;
$header__span_color: c.$primary-variant--chambray !default;
$header__span_font-family: t.$font__content !default;
$header__span_margin-left: 0 !default;

// ==========================
// ====== RESULTS GRID ======
// ==========================
// todo: Maybe there should be a wrapper container for the results.
$search-results_padding: 0 !default;
$search-results__categories_padding: 1.2rem 0 !default;
$search-results__categories_padding--desktop: 2.5rem 0 !default;
$search-results__categories_border-bottom: 1px solid c.$primary-variant--botticelli !default;
$highlight-color: c.$primary-variant--botticelli !default;

// ===========================
// ====== SINGLE RESULT ======
// ===========================
// todo: Maybe there should be a number of styles for archive layouts that could be switched between with conditional logic instead of only exposing independent css properties
$search-results__result_margin: 1.5rem 0 0 0 !default;
$search-results__result_margin--desktop: 4rem 0 0 0 !default;
$search-results__result_display: flex !default;
$search-results__result_flex-wrap: nowrap !default;
$search-results__result_align-items: center !default;
$search-results__result_justify-content: flex-start !default;

// Image
$search-results__result__image_width: 100% !default;
$search-results__result__image_display: flex !default;
$search-results__result__image_align-items: center !default;
$search-results__result__image_padding-right: 0 !default;
$search-results__result__image_padding-right--desktop: inherit !default;
$search-results__result__image__img_border: solid 1px c.$base-100 !default;

// Header
$search-results__result__content__header_max-width: tool.calculate-rem(600) !default;

// Header - Content-type
$search-results__result__content__header__content-type_margin: 0 !default;
$search-results__result__content__header__content-type_padding: 0 0 .25rem 0 !default;
$search-results__result__content__header__content-type_letter-spacing: t.$letter-spacing--large !default;
$search-results__result__content__header__content-type_color: c.$primary-500 !default;
$search-results__result__content__header__content-type_font-weight: 500 !default;
$search-results__result__content__header__content-type_text-transform: uppercase !default;
$search-results__result__content__header__content-type_font-family: t.$font__content !default;
$search-results__result__content__header__content-type_font-size: 12 !default;
$search-results__result__content__header__content-type__a_letter-spacing: t.$letter-spacing--large !default;
$search-results__result__content__header__content-type__a_color: c.$primary-500 !default;
$search-results__result__content__header__content-type__a_font-weight: 500 !default;
$search-results__result__content__header__content-type__a_text-transform: uppercase !default;
$search-results__result__content__header__content-type__a_font-family: t.$font__content !default;

// Header - Title
$search-results__result__content__header__title_font-size: 15 !default;
$search-results__result__content__header__title_font-size--desktop: 20 !default;
$search-results__result__content__header__title_line-height: 1.45 !default;
$search-results__result__content__header__title_letter-spacing: t.$letter-spacing !default;
$search-results__result__content__header__title__a_font-weight: 500 !default;
$search-results__result__content__header__title__a_color: c.$base-1000 !default;
$search-results__result__content__header__title_margin: 0 0 .5rem !default;
$search-results__result__content__header__title_margin--desktop: 0 !default;

// Summary
$search-results__result__content__summary_max-width: $search-results__result__content__header_max-width !default;
$search-results__result__content__summary__p_margin: .5rem 0 !default;
$search-results__result__content__summary__a_font-size: 16 !default;
$search-results__result__content__summary__a_font-weight: 500 !default;
$search-results__result__content__summary__a_line-height: 1.63 !default;
$search-results__result__content__summary__a_letter-spacing: t.$letter-spacing !default;
$search-results__result__content__summary__a_font-family: t.$font__content !default;
$search-results__result__content__summary__a_text-decoration: underline !default;

// ========================
// ====== PAGINATION ======
// ========================
$pagination-nav_color: c.$primary !default;
$pagination-nav_color--hover: c.$primary-variant--chambray !default;
$pagination-nav_color--visited: $pagination-nav_color !default;
$pagination-nav_spacing: 1rem !default;

// ========================
// ====== NO RESULTS ======
// ========================
$no-result__search-button__svg_color: c.$primary !default;

.search-result-heading {
	background-color: $header_background-color;

	.container {
		display: $header__container_display;
		justify-content: $header__container_justify-content;
		align-items: $header__container_align-items;

		@media (min-width: g.$breakpoint-s) {
			justify-content: $header__container_justify-content--desktop;
		}
	}

	h1 {
		margin: $header__h1_margin;
		padding: $header__h1_padding;
		display: flex;
		font-weight: $header__h1_font-weight;
		color: $header__h1_color;
		font-family: $header__h1_font-family;
		@include tool.font-size($header__h1_font-size);

		@media (min-width: g.$breakpoint-s) {
			padding: $header__h1_padding--desktop;
			display: block;
			@include tool.font-size($header__h1_font-size--desktop);
		}
	}

	span {
		margin-left: $header__span_margin-left;
		white-space: nowrap;
		color: $header__span_color;
		font-family: $header__span_font-family;
		@include tool.font-size($header__span_font-size);

		@media (min-width: g.$breakpoint-s) {
			margin-left: calculate-rem($header__span_font-size--desktop);
		}
	}
}

.search-categories {
	padding: $search-results__categories_padding;
	border-bottom: $search-results__categories_border-bottom;

	@media (min-width: g.$breakpoint-s) {
		padding: $search-results__categories_padding--desktop;
	}
}

.search-results article {
	margin: $search-results__result_margin;
	display: $search-results__result_display;
	flex-wrap: $search-results__result_flex-wrap;
	align-items: $search-results__result_align-items;
	justify-content: $search-results__result_justify-content;

	@media (min-width: g.$breakpoint-s) {
		margin: $search-results__result_margin--desktop;
	}

	.entry-title {
		margin: $search-results__result__content__header__title_margin;

		@media (min-width: g.$breakpoint-s) {
			margin: $search-results__result__content__header__title_margin--desktop;
		}
	}

	.entry-summary {
		max-width: $search-results__result__content__summary_max-width;

		p {
			margin: $search-results__result__content__summary__p_margin;
		}

		a {
			text-decoration: $search-results__result__content__summary__a_text-decoration;
			font-family: $search-results__result__content__summary__a_font-family;
			font-weight: $search-results__result__content__summary__a_font-weight;
			line-height: $search-results__result__content__summary__a_line-height;
			letter-spacing: $search-results__result__content__summary__a_letter-spacing;
			@include tool.font-size($search-results__result__content__summary__a_font-size);
		}

		.content-wrapper {
			display: none;

			@media (min-width: g.$breakpoint-s) {
				display: block;
			}
		}
	}

	.entry-header {
		max-width: $search-results__result__content__header_max-width;
		margin-left: 0;
	}
}

.search-post-image {
	width: $search-results__result__image_width;
	display: $search-results__result__image_display;
	align-items: $search-results__result__image_align-items;
	padding-right: $search-results__result__image_padding-right;
	padding-left: 0;

	@media (min-width: g.$breakpoint-s) {
		// What is this for?
		padding-right: $search-results__result__image_padding-right--desktop;
	}

	a,
	img {
		width: $search-results__result__image_width;
	}

	img {
		border: $search-results__result__image__img_border;
	}
}

.search-post-content {
	h4 {
		margin: $search-results__result__content__header__content-type_margin;
		padding: $search-results__result__content__header__content-type_padding;
		letter-spacing: $search-results__result__content__header__content-type_letter-spacing;
		color: $search-results__result__content__header__content-type_color;
		font-weight: $search-results__result__content__header__content-type_font-weight;
		text-transform: $search-results__result__content__header__content-type_text-transform;
		font-family: $search-results__result__content__header__content-type_font-family;
		@include tool.font-size($search-results__result__content__header__content-type_font-size);

		a {
			letter-spacing: $search-results__result__content__header__content-type__a_letter-spacing;
			color: $search-results__result__content__header__content-type__a_color;
			font-weight: $search-results__result__content__header__content-type__a_font-weight;
			text-transform: $search-results__result__content__header__content-type__a_text-transform;
			font-family: $search-results__result__content__header__content-type__a_font-family;
			@include tool.font-size($search-results__result__content__header__content-type_font-size);
		}
	}

	h2 {
		line-height: $search-results__result__content__header__title_line-height;
		letter-spacing: $search-results__result__content__header__title_letter-spacing;
		@include tool.font-size($search-results__result__content__header__title_font-size);

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size($search-results__result__content__header__title_font-size--desktop);
		}

		a {
			font-weight: $search-results__result__content__header__title__a_font-weight;
			color: $search-results__result__content__header__title__a_color;
		}
	}

	.entry-header > * {
		padding-left: unset;
		padding-right: unset;
	}

	&:not(.has-image) {
		padding-left: 0;
	}
}

.search-highlight {
	background-color: $highlight-color;
}

$no-result_color: c.$primary-variant--chambray !default;
$no-result_background-color: c.$primary-variant--botticelli !default;
$no-result__link_color: c.$primary !default;
$no-result__link_font-weight: bold !default;
$no-result__search-button_width: 75px !default;

.search-no-results {
	.site {
		background: $no-result_background-color;
	}

	.form-group {
		display: flex;
		margin-bottom: 2rem;
	}

	.search-page {
		text-align: center;

		h1,
		p {
			color: $no-result_color;
		}

		h1 {
			word-break: break-word;
		}

		a {
			font-weight: $no-result__link_font-weight;
			color: $no-result__link_color;
		}

		form {
			max-width: 600px;
			margin: auto;
		}

		svg path {
			fill: $no-result__search-button__svg_color;
		}

		.btn {
			margin-left: $no-result__search-button_width * -1;
			width: $no-result__search-button_width;
			background-color: transparent;
			border: 0;
		}

		.form-control {
			padding-right: $no-result__search-button_width;
		}

		.back-home {
			margin: 0;
			padding-bottom: 1.5rem;
		}
	}
}

.search-results {
	.site-main {
		padding: $site-main_padding;
	}
}

.search-results .post-nav {
	ul {
		list-style-type: none;
		padding: 0;
		display: flex;
		flex-direction: row-reverse;
		justify-content: center;
	}

	.next {
		margin-right: $pagination-nav_spacing;

		a:hover {
			color: $pagination-nav_color--hover;
		}
	}

	.next,
	.previous {
		color: $pagination-nav_color;

		a:visited {
			color: $pagination-nav_color--visited;
		}

		a:visited:hover {
			color: $pagination-nav_color--hover;
		}
	}
}
