@use "Settings/colors" as c;
@use "Settings/typography" as t;
@use "Settings/grid" as g;

@use "Tools" as tool;

$border-left_color: c.$primary !default;
$border-left_style: solid !default;
$border-left_thickness: 4px !default;
$padding-left--desktop: 2em !default;
$margin--desktop: 2em 0 !default;
$padding-left: 1em !default;
$margin: 1em 0 !default;
$paragraph_font-size: 15 !default;
$paragraph_font-size--desktop: 16 !default;
$paragraph_font-family: t.$font__heading !default;
$paragraph_font-style: italic !default;
$paragraph_weight: 500 !default;
$paragraph_line-height: 1.4 !default;
$paragraph_letter-spacing: t.$letter-spacing !default;
$cite_font-size: 14 !default;
$cite_font-size--desktop: 15 !default;
$cite_font-family: t.$font__heading !default;
$cite_font-style: italic !default;
$cite_weight: 300 !default;
$cite_line-height: 1.4 !default;

// padding !important to override WP Core's is-large padding
blockquote {
	margin: $margin;
	position: relative;
	padding-left: $padding-left;

	@media (min-width: g.$breakpoint-s) {
		margin: $margin--desktop;
		padding-left: $padding-left--desktop;
	}

	* + * {
		margin-top: .75em;
	}

	&:after {
		content: "";
		border-left: $border-left_thickness $border-left_style $border-left_color;
		position: absolute;
		height: 100%;
		top: 0;
		left: 0;
	}

	p {
		font-family: $paragraph_font-family;
		font-style: $paragraph_font-style;
		font-weight: $paragraph_weight;
		line-height: $paragraph_line-height;
		letter-spacing: $paragraph_letter-spacing;
		margin: unset;

		@include tool.font-size($paragraph_font-size);

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size($paragraph_font-size--desktop);
		}
	}

	cite {
		font-family: $cite_font-family;
		font-style: $cite_font-style;
		font-weight: $cite_weight;
		line-height: $cite_line-height;

		@include tool.font-size($cite_font-size);

		@media (min-width: g.$breakpoint-s) {
			@include tool.font-size($cite_font-size--desktop);
		}
	}

	.entry-content > &:after {
		left: g.$gutters-smallscreen-horizontal;

		@media (min-width: g.$gutters-smallscreen-breakpoint) {
			left: g.$gutters;
		}
	}

	.entry-content > & p,
	.entry-content > & cite {
		padding-left: calc(#{g.$gutters-smallscreen-horizontal} + #{$padding-left});

		@media (min-width: g.$gutters-smallscreen-breakpoint) {
			padding-left: calc(#{g.$gutters-smallscreen-horizontal} + #{$padding-left--desktop});
		}
	}
}
