/* stylelint-disable declaration-no-important */
@use "sass:math";

@use "Settings/grid"as g;
@use "Settings/buttons"as b;
@use "Settings/colors"as c;

@use "mixins" as mixin;

@mixin btn() {
	background-color: b.$background-color;
	border: b.$border--full_thickness solid b.$border-color;
	border-radius: b.$border-radius;
	display: inline-block;
	color: b.$color;
	transition: all .4s ease;
	padding: b.$padding;
	text-transform: b.$text-transform;
	font-weight: b.$font-weight;
	cursor: pointer;
	letter-spacing: b.$letter-spacing;
	text-decoration: none;
	outline: b.$outlin-size solid transparent;
	line-height: b.$line-height;
	-webkit-font-smoothing: initial;
	outline-offset: 0;
	@include mixin.font-size(b.$font-size);

	@media (min-width: g.$breakpoint-s) {
		@include mixin.font-size(b.$font-size--desktop);
	}

	&:visited {
		color: b.$color;
	}

	&:hover,
	&:focus {
		box-shadow: none;
		border: b.$border--full_thickness solid b.$border-color--hover;
		background-color: b.$background-color--hover;
		color: b.$color--hover;
	}

	&:focus {
		outline: b.$outlin-size solid b.$outlin-color;
	}

	&:active,
	&:active:focus {
		box-shadow: none;
		border: b.$border--full_thickness solid b.$border-color--active;
		background-color: b.$background-color--active;
		color: b.$color--active;
		outline: none;
	}

	&.disabled,
	.disabled > &,
	&:disabled,
	&:disabled:hover,
	&.disabled:hover {
		box-shadow: none;
		color: b.$disabled_color !important;
		background-color: b.$disabled_background-color !important;
		border-color: b.$disabled_border-color !important;
		cursor: not-allowed;
	}

	&:focus:not(:focus-visible) {
		outline: none;
	}
}

@mixin btn-type($type) {
	outline-offset: 0;

	// .btn-type-2 ( search-friendly :) )
	@if $type == 2 {
		background-color: b.$type-2_background-color;
		border: b.$border--full_thickness solid b.$type-2_border-color;
		color: b.$type-2_color;

		&:visited {
