		&:active,
		&:active:focus {
			border: b.$border--full_thickness solid b.$type-2_border-color--active;
			background-color: b.$type-2_background-color--active;
			color: b.$type-2_color--active;
			outline: none;
		}

		&.disabled,
		&:disabled,
		&:disabled:hover,
		&.disabled:hover {
			color: b.$type-2_color--disabled !important;
			background-color: b.$type-2_background-color--disabled !important;
