		}
	}

	// .btn-type-5 ( search-friendly :) )
	@if $type == 5 {
		background-color: b.$type-5_background-color;
		border: 0;
		border-bottom: b.$border--bottom_thickness solid b.$type-5_border-color;
		border-radius: 0;
		padding-left: 0;
		padding-right: 0;
		color: b.$type-5_color;

		&:visited {
