			border-color: b.$type-7_border-color--hover;
		}

		&:focus {
			outline-color: c.$alerts-success-48;
			background-color: b.$type-7_background-color--focus;
			border-color: b.$type-7_border-color--focus;
			color: b.$type-7_color--focus;
		}

		&:active,
		&:active:focus {
			background-color: b.$type-7_background-color--hover;
