@use "Settings/colors" as c;
@use "Settings/grid" as g;

@use "Tools" as tool;

$bundled_product_images__background-color: #eee !default;
$bundle-product-content__price-subtitle__color: #656565 !default;

@use "Components/pages/woocommerce/product" as p;

div.bundled_product_summary,
.woocommerce div.product.bundled_product_summary {
	display: flex;
	flex-flow: row wrap;

	.required {
		color: c.$base-900;
		text-decoration: unset;
	}
}

/* stylelint-disable */
.woocommerce-page div.product div.bundled_product_images,
.woocommerce-page #content div.product div.bundled_product_images,
.woocommerce div.product div.bundled_product_images,
.woocommerce #content div.product div.bundled_product_images {
	flex-basis: 50px;
	width: 50px;
	height: 50px;
	background-color: $bundled_product_images__background-color;
}
/* stylelint-enable */
.bundle_form .bundled_product_summary:not(.thumbnail_hidden) .details {
	float: none;
	flex-basis: calc(100% - 50px);
	max-width: calc(100% - 50px);
	padding: 0 0 0 p.$product_wrap_inner_elements_horizontal_padding_mobile;
}

.product-type-bundle {
	.small_width.bundle_form div.bundled_product_summary:not(.thumbnail_hidden) .details,
	.small_width.composite_form .bundle_form div.bundled_product_summary:not(.thumbnail_hidden) .details {
		padding: 0 0 0 p.$product_wrap_inner_elements_horizontal_padding_mobile;
	}
}

.woocommerce .bundle_form div.product.bundled_product_summary {
	margin-bottom: 0;
	padding: p.$product_wrap_inner_elements_horizontal_padding_mobile 0;
}

.bundled_product_title_inner {
	margin-bottom: 0;

	.item_title {
		letter-spacing: 0;
		@include tool.font-size(16);
	}
}

.bundled_product_excerpt.product_excerpt {
	p {
		margin-bottom: 0;
		@include tool.font-size(14);
	}
}

.woocommerce.single-product {
	.bundled_product .bundled_product_title.product_title {
		margin-bottom: 0;
		line-height: 1;
	}
}

.bundled_product_summary.product .bundled_product_title {
	.bundled_product_title_link {
		margin-left: 0;
	}

	.bundled_product_permalink {
		padding: 10px 20px 10px 10px;

		&:before {
			@include tool.font-size(10);
		}
	}
}

.woocommerce.single-product .product .bundled_product_title.product_title,
.qala-quickview .product .bundled_product_title.product_title {
	padding: 0;
	@include tool.font-size(24);
}

.bundled_product.bundled_product_summary .cart .variations td.label {
	padding-top: 0;
}

.woocommerce.single-product .product.product-type-bundle .price {
	margin-bottom: 0;
}

.bundle-product-content--price-subtitle {
	color: $bundle-product-content__price-subtitle__color;
	display: block;
	margin-bottom: p.$product_wrap_inner_elements_horizontal_padding_mobile;
	font-weight: 300;
	padding: 0 25px;
	@include tool.font-size(14);

	@media (min-width: g.$breakpoint-m) {
		padding: 0;
	}
}

.bundle-product-content--products-title {
	display: block;
	width: 100%;
	border-bottom: 1px solid c.$black;
	color: c.$black;
	font-weight: 500;
	padding: 3px 25px;
	@include tool.font-size(16);

	@media (min-width: g.$breakpoint-m) {
		padding: 3px 0;
	}
}

.bundled_product .attribute_options {
	.variable-item.color-variable-item,
	.variable-item.color-variable-item:hover {
		border: 0;
		box-shadow: unset;
	}

	.variable-item-span {
		padding: 0 5px;
		letter-spacing: 0;
		font-weight: 400;
	}
}

.bundle_error {
	padding-left: unset;

	.woocommerce-info {
		background: unset;
		padding-left: 65px;
		padding-right: unset;
		display: grid;
		grid-template-columns: auto 1fr;
		gap: 10px;
		align-items: center;

		&:before {
			display: flex;
			content: "!";
			width: 30px;
			height: 30px;
			border: 1px solid c.$alert;
			align-items: center;
			justify-content: center;
			color: c.$alert;
			border-radius: 50%;
			position: relative;
			top: unset;
			left: unset;
			line-height: 1;
		}

		.msg {
			padding: unset;
		}

		.msg li {
			color: c.$alert;
			@include tool.font-size(14);
		}
	}
}

.summary-inner-wrapper .bundle_sells_title {
	margin-top: p.$product_wrap_inner_elements_horizontal_padding_mobile;
	padding-top: 0;

	> h3 {
		border-bottom: 1px solid c.$black;
		margin: 0;
		padding-bottom: p.$product_wrap_inner_elements_horizontal_padding_mobile;
		font-weight: 600;
		@include tool.font-size(14);
	}
}

.bundled_product_summary .bundled_product_optional_checkbox {
	@include tool.font-size(14);

	.price,
	.price > * {
		color: c.$base-900;
		@include tool.font-size(14);
	}

	.price ins {
		margin-right: 10px;
	}

	span {
		font-weight: 400;
	}
}
