@use "Settings/grid"as g;
@use "Settings/colors"as c;

@use "Tools"as tool;

@use "Elements/headings" as *;

.text-and-image {
	@include tool.standard-vertical-padding;

	.image-right {
		flex-direction: row-reverse;
	}

	.text-and-image-grid {

		@media (min-width: g.$breakpoint-m) {
			justify-content: space-between;
		}
	}

	.image-column {
		padding-bottom: g.$gutters * .5;

		@media (min-width: g.$breakpoint-m) {
			padding-bottom: 0;
		}
	}

	.text-column {
		display: flex;
		flex-direction: column;
		align-items: center;
		flex-basis: 100%;
		max-width: 100%;

		@media (min-width: g.$breakpoint-m) {
			flex-basis: 40%;
			max-width: 40%;
			align-items: flex-start;
		}

		@media (max-width: g.$breakpoint-l) {
			flex-basis: 48%;
			max-width: 48%;
			padding-right: 4rem;
			margin-top: 2rem;
		}

		@media (max-width: g.$breakpoint-m) {
			flex-basis: 100%;
			max-width: 100%;
			padding-right: 25px;
		}

		.module-heading,
		p {
			text-align: center;
			width: 100%;

			@media (min-width: g.$breakpoint-m) {
				text-align: left;
			}
		}

		.module-heading {
			@extend h3;

			margin: 0;
		}

		p {
			margin: .5em 0 .9em;

			@media (min-width: g.$breakpoint-m) {
				margin-bottom: 1.4em;
			}
		}

		.full-width-button-container {
			width: 100%;
		}
	}

	.icons-list {
		margin: 1rem 0;
		width: 100%;

		&__item {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: tool.calculate-rem(14);
		}

		&__item-icon-wrap {
			flex-basis: 36px;
			height: 36px;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		&__item-icon-wrap svg,
		&__item-icon-wrap img {
			max-width: 22px;
		}

		&__item-content-wrap {
			flex-basis: calc(100% - 36px);
			padding-left: tool.calculate-rem(12);
		}

		&__item-subtitle,
		&__item-title {
			display: block;
			@include tool.font-size(14);
		}

		&__item-title {
			font-weight: 600;
		}

		&__item-subtitle {
			font-weight: 300;
			line-height: 1.2;
		}
	}
}

.text-and-image.light {
	background-color: c.$light-scheme__background-color;

	.text-column {
		.module-heading,
		p {
			color: c.$light-scheme__text-color;
		}
	}

	.icons-list__item-icon-wrap {
		background-color: c.$dark-scheme__background-color;
	}

	.icons-list__item-icon-wrap svg,
	.icons-list__item-icon-wrap path {
		fill: c.$light-scheme__background-color;
	}

	.cta-primary {
		@include tool.btn-type(2);
	}

	.cta-secondary {
		@include tool.btn-type(6);
	}
}

.text-and-image.dark {
	background-color: c.$dark-scheme__background-color;

	.text-column {
		.module-heading,
		p {
			color: c.$dark-scheme__text-color;
		}
	}

	.icons-list__item-subtitle,
	.icons-list__item-title {
		color: c.$dark-scheme__text-color;
	}

	.icons-list__item-icon-wrap {
		background-color: c.$light-scheme__background-color;
	}

	.icons-list__item-icon-wrap svg,
	.icons-list__item-icon-wrap path {
		fill: c.$dark-scheme__background-color;
	}

	.cta-primary {
		@include tool.btn;
	}

	.cta-secondary {
		@include tool.btn-type(5);
	}
}

.text-and-image.background-image {
	position: relative;

	.text-and-image__background-image {
		img {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.container {
		position: relative;
		z-index: 1;
	}

	.text-column {
		margin: -38px 48px 0;
		padding: 3em g.$gutters;

		@media (min-width: g.$breakpoint-m) {
			margin: 0;
			padding: 3em 2em 4.5em;
		}

		&.dark {
			background-color: c.$dark-scheme__background-color;
		}

		&.light {
			background-color: c.$light-scheme__background-color;
		}
	}
}
