@use "Settings/colors"as c;

$rating-star_fill--empty: c.$base-300 !default;
$rating-star_fill--filled: c.$primary !default;

.qala-star-rating {
	position: relative;
	overflow: hidden;
	line-height: 1px;
	margin: 0 0 .3rem;
	width: 110px;

	svg {
		width: 18px;
		padding-left: 3px;
		padding-right: 3px;
	}

	.rating-empty {
		svg path {
			fill: $rating-star_fill--empty;
		}
	}

	.rating-filled {
		position: absolute;
		top: 0;
		left: 0;
		overflow: hidden;

		svg path {
			fill: $rating-star_fill--filled;
		}
	}
}

/* stylelint-disable selector-max-id */
.woocommerce-Reviews #comments {
	h2:after {
		content: none;
	}
}

.woocommerce #reviews #comments ol.commentlist li .comment-text p.meta {
	text-transform: none;
}

/* stylelint-enable selector-max-id */
