@use "Settings/grid" as g;
@use "Settings/colors" as c;

@use "Components/pages/woocommerce/product" as p;
@use "Elements/links" as l;

.product-images-slider-container {
	display: flex;
	height: auto;

	/* stylelint-disable declaration-no-important */
	.site-content & [class*="navigation"] {
		overflow: initial !important;
		margin-bottom: 0;
	}
}

/**
 * 1. Avoid cumulative layout shift SwiperJS' spaceBetween: 15 doesn't match with
 * margin-right: 15px, so used 13px instead. (probably because of border: 2px)
 *
 */

.product-images-slider-thumbnails {
	// hide totally until a certain breakpoint...
	display: none;

	@media (min-width: g.$breakpoint-xl) {
		display: flex;
		width: 97px;
		min-width: 97px;
		flex-basis: 97px;
		margin-right: 13px; /* [1] */

		.product-images-slider-item-thumb {
			display: flex;
			align-items: center;
			justify-content: center;

			img {
				cursor: pointer;
				border: 2px solid transparent;
				transition: border .2s ease-in;
				margin-bottom: 15px;
			}

			&--selected img {
				border: 2px solid c.$primary-500;
			}

			&:nth-of-type(6) img {
				margin-bottom: 0; /* [1] */
			}

			&.video-slide img {
				height: 97px;
				object-fit: cover;
			}
		}
	}
}

.product-image-slider {
	display: flex;
	overflow: hidden;
	width: 100%;
	border-bottom: 2px solid transparent;

	@media (min-width: g.$breakpoint-xl) {
		position: relative;
	}

	.product-image-slider-item img {
		width: 100%;
	}

	.video-slide iframe {
		width: 100%;
		height: 100%;
	}

	&:has(.video-slide.swiper-slide-active) .swiper-pagination {
		pointer-events: none;
	}
}

.product-image-slider-wrapper {
	width: 100%; /* [1] */
}

/**
 * Hide all but the first product slider images until swiper initialises.
 * same for all but first 6 thumbnails (6 per slide)
 * this allows sliderHeight: auto to work without a huge page layout shift.
 *
 */

.product-image-slider-item:not(:first-of-type) {
	display: none;
}

.product-images-slider-item-thumb:nth-child(n+7) {
	display: none;
}

/**
 * 2. Make last product thumbnail bottom border visible and avoid cumulate layout shift
 * once slider has loaded with autoHeight: true
 *
 */

.swiper-initialized {
	.product-images-slider-item-thumb,
	.product-image-slider-item {
		display: block;
	}

	.product-image-slider {
		border-bottom: 2px solid transparent; /* [2] */
	}
}

.product-image-caption {
	margin-top: 20px;
}
