@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;

@use "Tools" as tool;

@use "Components/navigation/header" as h;

$color: t.$color__heading !default;
$background-color: c.$white !default;
$header_font: t.$font__content !default;
$price_font: t.$font__content !default;
$price_color: c.$primary !default;
$caption_font: t.$font__heading !default;
$qala-minicart-header-background-color: c.$base-100 !default;
$qala-minicart-close-btn-color: c.$primary !default;
$qala-minicart-close-btn-svg-fill-color: c.$primary !default;
$qala-minicart-footer-background-color: c.$base-100 !default;
$qala-minicart-footer-color: c.$black !default;
$qala-minicart-bestsellers-btn-border-color: c.$black !default;
$qala-minicart-bestsllers-btn-background-color: c.$white !default;
$qala-minicart-bestsllers-btn-color: c.$black !default;
$qala-minicart-bestsellers-btn-border-color-hover: c.$primary-600 !default;
$qala-minicart-bestsllers-btn-background-color-hover: c.$primary-600 !default;
$qala-minicart-bestsllers-btn-color-hover: c.$white !default;
$qala-minicart-checkout-btn-background-color: c.$primary !default;
$qala-minicart-checkout-btn-color: c.$white !default;
$qala-minicart-checkout-btn-background-color-hover: c.$primary-400 !default;
$qala-minicart-checkout-btn-color-hover: c.$white !default;
$qala-minicart-usp-color: c.$base-700 !default;

.site-header-cart {

	@media (min-width: g.$breakpoint-l) {
		margin-right: 0;
		position: relative;
	}

	&__button-container {
		position: relative;
		display: flex;

		svg {
			pointer-events: none;
		}

		.count {
			top: calc(50% - 6px);
			transform: translateY(-50%);
			right: -11px;
		}
	}
}

.woocommerce.widget_shopping_cart {
	display: none;
	opacity: 0;
	position: absolute;
	top: h.$height; // this will be overwritten by JS -> components/mini-cart.js
	right: 20px;
	padding: tool.calculate-rem(17);
	width: calc(100vw - 40px); // TODO: make this dynamic according to window width and mini-cart position (JS)
	max-width: 425px;
	background-color: $background-color;
	transition: opacity .1s ease-in;

	@media (min-width: g.$breakpoint-l) {
		padding: tool.calculate-rem(30);
		right: -20px;

		.woocommerce-buttons {
			flex-flow: row nowrap;

			a {
				flex-grow: 1;
			}

			a:not(.checkout) {
				margin-top: 0;
				order: 0;
			}
		}

		&:after {
			right: 20px;
		}
	}

	.mini-cart-enter & {
		display: block;
	}

	.mini-cart-enter-active & {
		opacity: 1;
	}

	.widget_shopping_cart_content {
		position: relative;
		z-index: 10;
	}

	.woocommerce-buttons {
		margin: 0;
		display: flex;
		flex-direction: column;

		a {
			// todo: @extend .btn;
			margin: 0;
			text-align: center;
			letter-spacing: t.$letter-spacing--large;
			line-height: 1.5;
		}

		a:not(.checkout) {
			// todo: @extend .btn-type-4;
			order: 10;
			margin-top: tool.calculate-rem(10);
		}
	}

	.woocommerce-mini-cart-item.mini_cart_item {
		padding: 0;
		display: flex;
		flex-flow: row nowrap;

		.remove_from_cart_button {
			display: none;
		}
	}

	// Triangle
	&:after {
		content: "";
		pointer-events: none;
		position: absolute;
		width: 20px;
		height: 20px;
		top: -6px;
		right: 46px;
		background-color: $background-color;
		transform: rotate(-45deg);
		z-index: 1;
	}
}

// Mini cart figure
.ac-figure {
	margin: 0;
}

.woocommerce ul.cart_list li {
	.ac-figure img {
		width: tool.calculate-rem(87);
		height: auto;
		margin: 0;
	}
}

.woocommerce-mini-cart {

	@media (min-width: g.$breakpoint-l) {
		.woocommerce-mini-cart-item + .woocommerce-mini-cart-item {
			margin-top: tool.calculate-rem(28);
		}
	}

	.woocommerce-mini-cart-item + .woocommerce-mini-cart-item {
		margin-top: tool.calculate-rem(17);
	}
}

// Mini cart caption
.ac-caption {
	display: flex;
	flex-grow: 1;
	flex-direction: column;
	padding-left: tool.calculate-rem(20);
	font-family: $caption_font;

	@media (min-width: g.$breakpoint-l) {
		padding-left: tool.calculate-rem(30);

		.price {
			@include tool.font-size(15);
		}

		.quantity {
			@include tool.font-size(13);
		}
	}

	.quantity {
		font-weight: 400;
		@include tool.font-size(11);
	}

	.price {
		display: flex;
		flex-flow: row nowrap;
		font-family: $caption_font;
		font-weight: 700;
		letter-spacing: t.$letter-spacing;
		color: $price_color;
		@include tool.font-size(13);

		ins {
			text-decoration: none;
		}

		del {
			order: 2;
			color: $color;
			font-weight: 400;
			padding-left: tool.calculate-rem(5);
		}
	}
}

.ac-product-title {
	color: $color;
	font-weight: 500;
	font-family: $header_font;
	@include tool.font-size(13);

	@media (min-width: g.$breakpoint-l) {
		@include tool.font-size(15);
	}
}

// Variations
.woocommerce ul.cart_list li dl {
	padding: 0;
	border: 0;

	@media (min-width: g.$breakpoint-l) {
		dt,
		dd,
		p {
			letter-spacing: t.$letter-spacing;
			@include tool.font-size(13);
		}
	}

	dt,
	dd,
	p {
		font-weight: 400;
		letter-spacing: t.$letter-spacing;
		@include tool.font-size(11);
	}

	dt,
	dd {
		margin: 0;
		padding: 0;
	}

	dt {
		margin-right: tool.calculate-rem(4);
	}

	dd p:last-child {
		margin: 0;
	}
}

// Totals
.woocommerce.widget_shopping_cart .total {
	border: 0;
	padding: tool.calculate-rem(23) 0;
	margin: 0;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	font-family: $color;

	@media (min-width: g.$breakpoint-l) {
		padding: tool.calculate-rem(45) 0 tool.calculate-rem(30);

		strong {
			@include tool.font-size(13);
		}

		.amount {
			letter-spacing: t.$letter-spacing--large;
			@include tool.font-size(24);
		}
	}

	strong {
		text-transform: uppercase;
		letter-spacing: t.$letter-spacing--large;
		display: flex;
		flex-direction: column;
		@include tool.font-size(11);

		span {
			font-weight: 400;
			letter-spacing: t.$letter-spacing;
			text-transform: none;
		}
	}

	.amount {
		font-weight: 700;
		letter-spacing: t.$letter-spacing;
		@include tool.font-size(18);
	}
}

.qala-minicart,
.woocommerce .qala-minicart {
	.qala-minicart-header {
		background: $qala-minicart-header-background-color;
	}

	.qala-minicart__close-btn {
		color: $qala-minicart-close-btn-color;

		svg path {
			fill: $qala-minicart-close-btn-svg-fill-color;
		}
	}

	.qala-minicart__footer {
		background: $qala-minicart-footer-background-color;
		color: $qala-minicart-footer-color;
	}

	.qala-minicart__checkout-button {
		background: $qala-minicart-checkout-btn-background-color;
		color: $qala-minicart-checkout-btn-color;

		&:hover,
		&:focus {
			background-color: $qala-minicart-checkout-btn-background-color-hover;
			color: $qala-minicart-checkout-btn-color-hover;
		}
	}

	.qala-minicart-bestsellers-button,
	.cart-empty__link {
		border-color: $qala-minicart-bestsellers-btn-border-color;
		background-color: $qala-minicart-bestsllers-btn-background-color;
		color: $qala-minicart-bestsllers-btn-color;
		transition: all .3s ease-in-out;

		&:hover,
		&:focus {
			border-color: $qala-minicart-bestsellers-btn-border-color-hover;
			background-color: $qala-minicart-bestsllers-btn-background-color-hover;
			color: $qala-minicart-bestsllers-btn-color-hover;
		}
	}

	.qala-minicart__usp-wrapper .usp {
		padding-left: unset;
		padding-right: 10px;
		margin-right: 10px;
		border-right: 1px solid;
		color: $qala-minicart-usp-color;

		p {
			color: $qala-minicart-usp-color;
		}

		svg path {
			stroke: $qala-minicart-usp-color;
		}

		&:last-child {
			padding: unset;
			margin: unset;
			border: unset;
		}
	}

	.qala-minicart-remove {
		font-size: 12px;
	}
}
