@use "Settings/colors"as c;
@use "Settings/typography" as t;

@use "Tools"as tool;

@use "Components/pages/woocommerce/product"as p;

$border-color: c.$base-100 !default;
$border-color--current: c.$base-500 !default;

.woo-variation-items-wrapper {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;

	.color-variable-item {
		margin-right: 10px;
		padding: 5px;
		border-radius: 100%;
		border: solid 2px c.$white;

		.variable-item-span {
			display: block;
			width: 20px;
			height: 20px;
			border-radius: 20px;
		}

		&.selected {
			border-color: $border-color--current;
		}
	}

	.button-variable-item {
		margin-right: 12px;
		border: solid 2px $border-color;

		.variable-item-span {
			display: block;
			width: auto;
			height: 48px;
			line-height: 48px;
			text-align: center;
		}

		&.selected {
			border-color: $border-color--current;
		}
	}

	.variable-item-span {
		display: block;
		font-family: p.$summary_font-family;
		font-weight: 700;
		font-style: normal;
		line-height: 1.46;
		letter-spacing: t.$letter-spacing--large;
		text-align: center;
		@include tool.font-size(13);
	}
}
