@use "Settings/colors"as c;
@use "Settings/grid" as g;
@use "Settings/typography" as t;

@use "Tools" as tool;

@use "Elements/headings" as h;

$color: c.$white !default;
$color-hover: c.$white !default;
$background-color: c.$primary !default;
$background-color-hover: c.$primary-600 !default;
$gap: .4rem !default;
$margin: 0 0 .75rem 0 !default;
$margin--desktop: 0 0 .5rem 0 !default;
$padding: 10px 20px !default;
$border-radius: 0 !default;
$font-size: 12 !default;
$font-size--desktop: 14 !default;
$font-weight: 400 !default;
$font-style: normal !default;
$letter-spacing: t.$letter-spacing !default;
$line-height: 1.1 !default;
$line-height--desktop: 1.1 !default;
$text-transform: uppercase !default;

.primary-cat-label__wrapper {
	width: 100%;
	margin: $margin;
	margin-left: 25px;
	margin-right: 25px;

	@media (min-width: g.$breakpoint-m) {
		margin: $margin--desktop;
		margin-left: 0;
		margin-right: 0;
	}
}

.primary-cat-label {
	display: inline-block;
	color: $color;
	background-color: $background-color;
	padding: $padding;
	border-radius: $border-radius;
	font-weight: $font-weight;
	letter-spacing: $letter-spacing;
	line-height: $line-height;
	text-transform: $text-transform;
	font-style: $font-style;
	transition: ease .2s all;

	@include tool.font-size($font-size);

	@media (min-width: g.$breakpoint-s) {
		line-height: $line-height--desktop;

		@include tool.font-size($font-size--desktop);
	}

	&:hover,
	&:focus {
		color: $color-hover;
		background-color: $background-color-hover;
	}
}
