@use "Settings/grid" as g;
@use "Settings/colors" as c;
@use "Settings/typography" as t;

@use "Tools" as tool;

@use "Components/navigation/footer" as f;

.site-footer-simple {
	.aclp-toggle {
		line-height: 1.64;
		letter-spacing: t.$letter-spacing;
		@include tool.font-size(14);
	}
}

.footer-editor-wrapper {
	p {
		margin-top: 0;
	}
}

.simple-footer-wrapper {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	padding-bottom: 0;
}

.footer-menu-section {
	flex-grow: 1;
	flex-basis: 50%;
	width: 50%;
	padding-left: 10px;
	padding-right: 10px;
	margin-bottom: 35px;
	display: flex;

	@media (min-width: g.$breakpoint-m) {
		flex-basis: 0;
		padding-left: 0;
		padding-right: 0;
		max-width: none;
		margin-bottom: 0;
	}

	&.has-social-section {
		flex-basis: 100%;
		width: auto;
		flex-wrap: wrap;

		@media (min-width: g.$breakpoint-m) {
			flex-basis: 0;
		}

		.footer-menu-section-wrapper {
			flex-basis: 50%;
			width: 50%;

			@media (min-width: g.$breakpoint-m) {
				flex-basis: 100%;
				width: 100%;
			}
		}
	}
}

.simple-footer-menu {
	list-style: none;
	margin: 0;
	padding: 0;

	> li > a {
		line-height: 2.36;
		letter-spacing: t.$letter-spacing;
		color: f.$color;
		@include tool.font-size(14);

		&:hover,
		&:focus {
			color: f.$link_color--hover;
		}
	}
}

.site-footer .simple-footer-menu-social {
	margin-top: 10px;

	.footer-heading {
		margin-bottom: 5px;
	}

	.social-media-buttons {
		justify-content: flex-start;
	}
}

.simple-footer-content .footer-heading {
	text-transform: uppercase;
	font-weight: 700;
	font-style: normal;
	line-height: 1.67;
	letter-spacing: t.$letter-spacing--large;
	color: f.$color;
	margin-bottom: 0;
	@include tool.font-size(12);
}

.footer-content {
	.col-m-4 {
		&:first-child {

			@media (max-width: g.$breakpoint-l) {
				max-width: 100%;
				flex-basis: 100%;
			}
		}

		&:nth-child(2),
		&:nth-child(3) {

			@media (max-width: g.$breakpoint-l) {
				max-width: 50%;
				flex-basis: 50%;
			}

			@media (max-width: g.$breakpoint-m) {
				max-width: 100%;
				flex-basis: 100%;
			}
		}
	}
}

.footer-cta {
	padding-left: 10px;
	padding-right: 10px;

	@media (min-width: g.$breakpoint-m) {
		padding-left: 0;
		padding-right: 0;
	}

	.footer-heading {
		margin-bottom: 10px;
	}

	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
	}

	li {
		padding-bottom: 20px;
		flex-basis: 50%;
		width: 50%;

		@media (min-width: g.$breakpoint-m) {
			width: 100%;
			flex-basis: 100%;
		}

		&:nth-child(odd) {
			padding-right: 12px;

			@media (min-width: g.$breakpoint-m) {
				padding-right: 0;
			}
		}

		&:nth-child(even) {
			padding-left: 12px;

			@media (min-width: g.$breakpoint-m) {
				padding-left: 0;
			}
		}
	}
}
