@use "Settings/grid" as g;
@use "Settings/colors" as c;

@use "Tools" as tool;

@use "Elements/body";

@use "Components/headings";

.wc-block-grid {
	/* We have to wrap selectors in this media query to override woocommerce rules */
	// !important to avoid use of has-2-columns (+ 3, 4, 5 to) has-6-columns CSS rules
	// always show 2 columns at max-width breakpoint-m, unless 1 is chosen

	/* stylelint-disable declaration-no-important */
	@media (max-width: g.$breakpoint-m - 1px) {
		.wc-block-grid__products {
			display: flex !important;
			margin: 0 -1.5rem 1rem;
			list-style: none;
			padding: 0;
		}

		.wc-block-grid__product {
			flex: 1 0 50% !important;
			max-width: 50% !important;
		}

		&.has-1-columns {
			.wc-block-grid__product {
				flex: 1 0 100% !important;
				max-width: 100% !important;
			}
		}

		.wc-block-grid__product:nth-child(2n) {
			padding-left: 0 !important;
		}

		.wc-block-grid__product:nth-child(odd) {
			padding-right: 0 !important;
		}
	}

	/* stylelint-enable */

	@media (min-width: g.$breakpoint-m) {
		.wc-block-grid__products {
			margin-right: 0;
			margin-left: 0;
		}
	}

	.wc-block-grid__products {
		list-style: none;
		margin: 0 tool.calculate-rem(-8) tool.calculate-rem(16);
		padding: 0;
		display: flex;
		flex-wrap: wrap;
		background-clip: padding-box;
	}

	& .wc-block-grid__product-link:hover .wc-block-grid__product-title {
		color: c.$black;
	}

	.wc-block-grid__product-title {
		@extend p;

		margin-bottom: .5rem;
		text-align: left;
	}

	.wc-block-grid__product-price {
		@extend .section-heading;

		text-align: left;
	}
}
