@use "Settings/grid"as g;
@use "Settings/colors"as c;
@use "Settings/typography"as t;
@use "Settings/buttons"as b;

@use "Tools"as tool;

@use "Components/headings" as *;
@use "Elements/links" as l;

// ====================
// ====== HEADER ======
// ====================
$header-wrapper_background-color: c.$primary-variant--botticelli !default;
$header-wrapper_align-items: center !default;
$header-wrapper__heading_color: c.$primary-variant--chambray !default;
$header-wrapper__heading_margin-top: 0 !default;
$header-wrapper__heading_padding: 39px 15px 43px !default;

// Show-more button
$show-more-button_display: block !default;
$show-more-button_width: 100% !default;
$show-more-button_margin: 0 auto tool.calculate-rem(55) !default;
$show-more-button_border: 2px solid c.$primary !default;
$show-more-button_padding: 1.4em 2em !default;

// ====================
// ====== TAGS ========
// ====================
$tag-wrapper_margin-right: #{ (g.$gutters-smallscreen-horizontal * .5) * -1} !default;
$tag-wrapper_margin-top: 0 !default;
$tag-wrapper_margin-top--desktop: 35px !default;
$tag-wrapper__tag-button_margin-right: 7px !default;
$tag-wrapper__tag-button_border-radius: 20px !default;
$tag-wrapper__tag-button_padding: .7em 1.2em !default;
$tag-wrapper__tag-button_font-size: 13 !default;

// ====================
// ====== GRID ========
// ====================
$grid_padding-top: 15px !default;
$grid_column-gap: 16px !default;
$grid_padding-top--desktop: 20px !default;
$grid_column-gap--desktop: 35px !default;
$grid_column-count: 1 !default;
$grid_column-count--medium: 2 !default;
$grid_column-count--large: 3 !default;

// =========================
// ====== GRID ITEM ========
// =========================
$grid__grid-item_margin: 0 0 1.5em !default;
$grid__grid-item_background-color: c.$white !default;
$grid__grid-item_box-shadow: 0 2px 14px 0 rgb(0 0 0 / 12%) !default;

// Image
$grid__grid-item__image-container_padding: 0 !default;
$grid__grid-item__image-container_padding--desktop: 0 !default;
$grid__grid-item__image-container_width--desktop: 100% !default;
$grid__grid-item__image-container_max-width--desktop: 100% !default;
$grid__grid-item__image-container_height--desktop: auto !default;
$grid__grid-item__image-container__img_object-fit: cover !default;
$grid__grid-item__image-container__img_width: 100px !default;
$grid__grid-item__image-container__img_height: 100px !default;
$grid__grid-item__image-container__img_max-width: 100px !default;
$grid__grid-item__image-container__img_width--desktop: 100% !default;
$grid__grid-item__image-container__img_height--desktop: auto !default;
$grid__grid-item__image-container__img_max-width--desktop: 100% !default;

// Content
$grid__grid-item__content_padding: 0 25px 0 0 !default;
$grid__grid-item__content_padding--desktop: 24px !default;
$grid__grid-item__content__meta_margin-top: 0 !default;
$grid__grid-item__content__meta_margin-top--desktop: 11px !default;
$grid__grid-item__content__meta_margin-bottom: 0 !default;
$grid__grid-item__content__meta_margin-bottom--desktop: 0 !default;
$grid__grid-item__content__meta_color: c.$base-500 !default;
$grid__grid-item__content__heading_margin-top: 0 !default;
$grid__grid-item__content__heading_margin-top--desktop: 5px !default;
$grid__grid-item__content__heading_margin-bottom: 1rem !default;
$grid__grid-item__content__heading_color: c.$base-900 !default;
$grid__grid-item__content__heading_color--hover: l.$color--hover !default;
$grid__grid-item__content__excerpt_margin-top: 0 !default;
$grid__grid-item__content__excerpt_margin-top--desktop: 5px !default;
$grid__grid-item__content__excerpt_margin-bottom: 1rem !default;
$grid__grid-item__content__excerpt_color: c.$black !default;
$grid__grid-item__content__category-link_font-size: 14 !default;
$grid__grid-item__content__category-link_padding: 12px !default;
$grid__grid-item__content__category-link_color: l.$color !default;
$grid__grid-item__content__category-link_color--hover: l.$color--hover !default;
$grid__grid-item__content__category-link_text-transform: none !default;

/* The Masonry Container */
.archive-header-wrapper {
	background-color: $header-wrapper_background-color;
	display: flex;
	align-items: $header-wrapper_align-items;

	.archive-header {
		color: $header-wrapper__heading_color;
		margin-top: $header-wrapper__heading_margin-top;
		padding: $header-wrapper__heading_padding;
		font-size: 2.2rem;
		letter-spacing: 0;
		font-weight: 500;
		@include tool.standard-horizontal-padding;
	}
}

.archive-wrapper {
	.btn.show-more-articles {
		display: $show-more-button_display;
		width: $show-more-button_width;
		text-align: center;
		cursor: pointer;
		margin: $show-more-button_margin;
		border: $show-more-button_border;
		padding: $show-more-button_padding;
	}
}

.archive-tag-wrapper {
	list-style: none;
	overflow-x: auto;
	white-space: nowrap;
	margin-right: $tag-wrapper_margin-right;
	margin-top: calc($tag-wrapper_margin-top--desktop - b.$outlin-size);
	margin-left: -#{b.$outlin-size};
	padding: b.$outlin-size;

	.category-active .tag-button,
	.category-active .archive-tag-button {
		background-color: c.$primary;
		color: c.$white;

		&:visited {
			color: c.$white;
		}
	}

	li {
		display: inline-block;
		margin-right: $tag-wrapper__tag-button_margin-right;
	}

	// todo: This should be an extension of a generic pill component. Also see _single-page.scss for related issue
	.archive-tag-button {
		border-radius: $tag-wrapper__tag-button_border-radius;
		padding: $tag-wrapper__tag-button_padding;
		@include tool.font-size($tag-wrapper__tag-button_font-size);
	}
}

.archive-masonry {
	padding-top: $grid_padding-top;
	column-gap: $grid_column-gap;
	column-count: $grid_column-count;

	@media (min-width: g.$breakpoint-m) {
		padding-top: $grid_padding-top--desktop;
		column-gap: $grid_column-gap--desktop;
		column-count: $grid_column-count--medium;
	}

	@media (min-width: g.$breakpoint-l) {
		column-count: $grid_column-count--large;
	}
}

/* The Masonry Brick */
.archive-item {
	margin: $grid__grid-item_margin;
	background-color: $grid__grid-item_background-color;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;

	@media (min-width: g.$breakpoint-m) {
		box-shadow: $grid__grid-item_box-shadow;
		width: 100%;
		display: inline-block;
	}

	.image-container {
		padding: $grid__grid-item__image-container_padding;
		display: block;

		@media (min-width: g.$breakpoint-m) {
			padding: $grid__grid-item__image-container_padding--desktop;
			width: $grid__grid-item__image-container_width--desktop;
			max-width: $grid__grid-item__image-container_max-width--desktop;
			height: $grid__grid-item__image-container_height--desktop;
		}

		img {
			object-fit: $grid__grid-item__image-container__img_object-fit;
			width: $grid__grid-item__image-container__img_width;
			height: $grid__grid-item__image-container__img_height;
			max-width: $grid__grid-item__image-container__img_max-width;

			@media (min-width: g.$breakpoint-m) {
				width: $grid__grid-item__image-container__img_width--desktop;
				height: $grid__grid-item__image-container__img_height--desktop;
				max-width: $grid__grid-item__image-container__img_max-width--desktop;
			}
		}
	}

	.meta {
		margin-top: $grid__grid-item__content__meta_margin-top;
		margin-bottom: $grid__grid-item__content__meta_margin-bottom;
		color: $grid__grid-item__content__meta_color;
		font-weight: 500;
		text-transform: none;

		@media (min-width: g.$breakpoint-m) {
			margin-top: $grid__grid-item__content__meta_margin-top--desktop;
			margin-bottom: $grid__grid-item__content__meta_margin-bottom--desktop;
		}
	}

	.archive-item-heading {
		@extend .subheading;

		margin-top: $grid__grid-item__content__heading_margin-top;
		margin-bottom: $grid__grid-item__content__heading_margin-bottom;
		font-style: inherit;
		color: $grid__grid-item__content__heading_color;
		transition: l.$transition_time l.$transition_timing-function color;

		@media (min-width: g.$breakpoint-m) {
			margin-top: $grid__grid-item__content__heading_margin-top--desktop;
		}
	}

	.archive-item-excerpt {
		color: $grid__grid-item__content__excerpt_color;
		margin-top: $grid__grid-item__content__excerpt_margin-top;
		margin-bottom: $grid__grid-item__content__excerpt_margin-bottom;

		@media (min-width: g.$breakpoint-m) {
			margin-top: $grid__grid-item__content__excerpt_margin-top--desktop;
		}
	}

	.btn {
		display: none;

		@media (min-width: g.$breakpoint-m) {
			display: block;
		}
	}

	.archive-item-excerpt-mobile {
		display: block;

		@media (min-width: g.$breakpoint-m) {
			display: none;
		}
	}

	.archive-item-excerpt-desktop {
		display: none;

		@media (min-width: g.$breakpoint-m) {
			display: block;
		}
	}

	.archive-item-content {
		padding: $grid__grid-item__content_padding;

		@media (min-width: g.$breakpoint-m) {
			padding: $grid__grid-item__content_padding--desktop;
		}

		> a[rel="tag"] { // category/term link
			color: $grid__grid-item__content__category-link_color;
			text-transform: $grid__grid-item__content__category-link_text-transform;
			display: inline-block;
			padding: $grid__grid-item__content__category-link_padding;
			margin-left: -$grid__grid-item__content__category-link_padding;
			margin-top: -$grid__grid-item__content__category-link_padding;
			@include tool.font-size($grid__grid-item__content__category-link_font-size);

			&:hover,
			&:focus {
				color: $grid__grid-item__content__category-link_color--hover;
			}
		}
	}

	.archive-item-link {
		display: block;
		text-decoration: none;

		&:hover,
		&:focus {
			text-decoration: none;

			.archive-item-heading {
				color: $grid__grid-item__content__heading_color--hover;
			}
		}
	}
}
